import logger from '@sourcegraph/amp-core/src/common/logger'
import { type Printer } from '@sourcegraph/amp-core/src/util/color'
import { fromEvent, map, merge, Observable, Subject } from '@sourcegraph/observable'
import runes from 'runes'
import stringWidth from 'string-width'
import type { ReadStream, WriteStream } from 'tty'
import { isDeepStrictEqual } from 'util'
import { bufferToHexKey, isBufferPrefix } from './buffer'
import {
	ALT_B,
	ALT_BACKSPACE,
	ALT_D,
	ALT_F,
	ARROW_DOWN,
	ARROW_LEFT,
	ARROW_RIGHT,
	ARROW_UP,
	BACKSPACE,
	CMD_ENTER,
	CR,
	CTRL_A,
	CTRL_B,
	CTRL_C,
	CTRL_D,
	CTRL_E,
	CTRL_ENTER,
	CTRL_F,
	CTRL_H,
	CTRL_K,
	CTRL_L,
	CTRL_N,
	CTRL_P,
	CTRL_U,
	CTRL_W,
	CTRL_Y,
	CTRL_G,
	CTRL_O,
	CTRL_Q,
	CTRL_R,
	CTRL_S,
	CTRL_T,
	CTRL_V,
	CTRL_X,
	CTRL_Z,
	DEL,
	END,
	END_ALT,
	ESC,
	HOME,
	HOME_ALT,
	kitty,
	NL,
	PAGE_DOWN,
	PAGE_UP,
	SHIFT_ENTER,
	SHIFT_TAB,
	TAB,
} from './keys'
import { tick } from '@sourcegraph/amp-core/src/misc/sleep'

export type LineEditorEvent =
	| { type: 'line-editor-state'; state: State['state'] }
	| { type: 'submit'; message: string }
	| { type: 'exit' }

export interface HistoryProvider {
	add(message: string): void
	previous(): string | null
	next(): string | null
	reset(): void
}

export interface LineEditor {
	content: string
	lines: string[]
	cursorLine: number
	cursorColumn: number
	acceptMessage(): void
	setRightStatus(text: string): void
	observable: Observable<LineEditorEvent>
}

type State = {
	lines: string[]
	cursorLine: number
	cursorColumn: number
	deletedText: string[]
	state: 'off' | 'message' | 'submitting' | 'closed'
	screenColumns: number
	screenRows: number
	lastChar: string | null
	isProcessingPaste: boolean
	status: { left: string; right: string }
	completions: string[]
	selectedCompletionIndex: number
	isNavigatingHistory: boolean
	currentHistoryContent: string | null
	lastCompletionQuery: string | null
}

type Completer = (text: string) => Promise<string[]>

interface LineEditorOptions {
	stdin?: ReadStream
	stdout?: WriteStream
	stderr?: WriteStream
	prompt?: string
	printer: Printer
	complete: Completer
	history?: HistoryProvider
}

type StateMachineEvent =
	| { type: 'accept-message' }
	| { type: 'input'; data: Buffer }
	| { type: 'resize' }
	| { type: 'submit'; message: string }
	| { type: 'complete'; candidates: string[] }
	| { type: 'cycle-completion'; direction: 'forward' | 'backward' }

export function createLineEditor(options: LineEditorOptions): LineEditor {
	const stdin: ReadStream = options.stdin || process.stdin
	const stdout: WriteStream = options.stdout || process.stdout
	const stderr: WriteStream = options.stderr || process.stderr
	const prompt: string = options.prompt || ''
	const history: HistoryProvider | undefined = options.history

	// Get initial terminal dimensions
	const initialColumns = stdout.columns || 80
	const initialRows = stdout.rows || 24

	const state: State = {
		lines: [''],
		cursorLine: 0,
		cursorColumn: 0,
		deletedText: [],
		state: 'off',
		screenColumns: initialColumns,
		screenRows: initialRows,
		lastChar: null,
		isProcessingPaste: false,
		status: { left: '', right: '' },
		completions: [],
		selectedCompletionIndex: 0,
		isNavigatingHistory: false,
		currentHistoryContent: null,
		lastCompletionQuery: null,
	}

	const inputs: Observable<StateMachineEvent> = fromEvent(stdin, 'data').pipe(
		map((data: Buffer) => ({
			type: 'input',
			data,
		})),
	)
	const resizes: Observable<StateMachineEvent> = fromEvent(stdout, 'resize').pipe(
		map(() => ({ type: 'resize' })),
	)

	const triggeredEvents = new Subject<StateMachineEvent>()

	const handlers = initializeHandlers({
		stdout,
		stderr,
		state,
		prompt,
		printer: options.printer,
		complete: (pattern) =>
			options.complete(pattern).then((candidates) => {
				triggeredEvents.next({ type: 'complete', candidates })
			}),
		onSubmit: (message) => {
			// Add to history when submitting if history provider exists
			if (history && message.trim()) {
				history.add(message)
				history.reset() // Reset navigation state
			}
			triggeredEvents.next({ type: 'submit', message })
		},
		// Pass the triggerEvent function to allow handlers to trigger events
		triggerEvent: (event) => triggeredEvents.next(event),
		history,
	})

	const outputEvents = new Subject<LineEditorEvent>()

	function setState(newState: State['state']) {
		state.state = newState
		outputEvents.next({ type: 'line-editor-state', state: newState })
	}

	const stateMachine = merge(inputs, resizes, triggeredEvents).pipe(
		map((event) => {
			if (event.type === 'accept-message') {
				if (state.state !== 'off') {
					throw new Error(`can't accept message when in ${state.state} state`)
				}
				setState('message')
				if (prompt) {
					stderr.write('\r')
					stderr.write(prompt)
					// Add the status line on a new line
					stderr.write('\n\r')
					updateStatusLine(stderr, state)
					// Move cursor back up to prompt line
					stderr.write('\x1b[1F')
					stderr.write(`\x1b[${prompt.length + 1}G`)
				}
			} else if (event.type === 'resize') {
				// Handle terminal resize events
				state.screenColumns = stdout.columns || initialColumns
				state.screenRows = stdout.rows || initialRows
				logger.debug('Terminal resized', {
					columns: state.screenColumns,
					rows: state.screenRows,
				})
			} else if (event.type === 'submit') {
				outputEvents.next(event)
				setState('off')
			} else if (event.type === 'complete') {
				// only reset selected completion if list changed
				if (!isDeepStrictEqual(event.candidates, state.completions)) {
					state.selectedCompletionIndex = 0
				}
				// Store the candidates for later use and reset the selection index
				state.completions = event.candidates
				state.status.left = renderCompletions(
					stderr,
					options.printer,
					event.candidates,
					state.selectedCompletionIndex,
				)
				atStatusLine(stderr, state, prompt, () => {
					updateStatusLine(stderr, state)
				})
			} else if (event.type === 'cycle-completion') {
				// Only process if we have completions to cycle through
				if (state.completions.length > 0) {
					// Update the index based on direction
					if (event.direction === 'forward') {
						state.selectedCompletionIndex =
							(state.selectedCompletionIndex + 1) % state.completions.length
					} else {
						state.selectedCompletionIndex =
							(state.selectedCompletionIndex - 1 + state.completions.length) %
							state.completions.length
					}

					// Update the status line with the new selection
					state.status.left = renderCompletions(
						stderr,
						options.printer,
						state.completions,
						state.selectedCompletionIndex,
					)
					atStatusLine(stderr, state, prompt, () => {
						updateStatusLine(stderr, state)
					})
				}
			} else if (event.type === 'input') {
				let remaining: Buffer = event.data

				if (
					state.state === 'off' ||
					state.state === 'submitting' ||
					remaining.length === 0
				) {
					return
				}

				// Detect paste operation (multiple characters at once)
				// Only consider it a paste if it contains more than one character and isn't a control sequence
				const isPaste =
					event.data.length > 1 &&
					!event.data.equals(CTRL_ENTER) &&
					!event.data.equals(kitty.CTRL_ENTER) &&
					!event.data.equals(CMD_ENTER)
				state.isProcessingPaste = isPaste

				while (remaining.length > 0) {
					if (
						(remaining.equals(CTRL_D) || remaining.equals(kitty.CTRL_D)) &&
						state.lines.length === 1 &&
						state.lines[0] === ''
					) {
						stderr.write(`\r`)
						stderr.write(`\x1b[K`) // clear

						state.lines = ['']
						state.cursorLine = 0
						state.cursorColumn = 0
						state.status.left = ''
						state.lastCompletionQuery = null
						setState('closed')
						outputEvents.next({ type: 'exit' })
						return
					}

					if (
						remaining.equals(ESC) ||
						remaining.equals(kitty.ESC) ||
						remaining.equals(CTRL_C) ||
						remaining.equals(kitty.CTRL_C)
					) {
						// Check if the prompt is empty
						const isEmpty = state.lines.length === 1 && state.lines[0] === ''

						if (isEmpty) {
							// Show message about how to exit with Ctrl+D
							state.status.left = 'Press Ctrl+D to exit'
							redrawLinesFrom(stderr, state, prompt, 0)
						} else if (
							state.isNavigatingHistory &&
							state.currentHistoryContent !== null
						) {
							// When in history navigation mode, restore the original draft
							replaceContent(stderr, state, prompt, state.currentHistoryContent)

							// Reset history navigation state
							state.isNavigatingHistory = false
							state.currentHistoryContent = null
							if (history) {
								history.reset()
							}
						} else {
							stderr.write(`\r`)
							// go down to last line + status line
							for (let i = 0; i < state.lines.length - state.cursorLine; i++) {
								stderr.write(`\x1b[1B`)
							}
							// go back up, clearing each line
							for (let i = 0; i < state.lines.length; i++) {
								stderr.write(`\x1b[K`) // clear
								stderr.write(`\x1b[1F`) // up
							}
							stderr.write(`\x1b[K`) // clear
							stderr.write(prompt)

							state.lines = ['']
							state.cursorLine = 0
							state.cursorColumn = 0
							state.status.left = ''
							state.lastCompletionQuery = null
						}
						break
					}

					remaining = handlers.process(remaining)

					// Reset paste state after processing the buffer
				}
				state.isProcessingPaste = false
			}
		}),
	)

	// Return the LineEditor interface
	return {
		get content() {
			return state.lines.join('\n')
		},
		get lines() {
			return [...state.lines]
		},
		get cursorLine() {
			return state.cursorLine
		},
		get cursorColumn() {
			return state.cursorColumn
		},
		acceptMessage() {
			if (state.state !== 'off' && state.state !== 'submitting') {
				throw new Error(`can't accept message when in ${state.state} state`)
			}
			// we need a tick here so that the line editor ignores any pending input
			// that came in while it was 'off', before changing state and accepting
			// its intended input
			tick().then(() => {
				triggeredEvents.next({ type: 'accept-message' })
			})
		},
		setRightStatus(text: string) {
			if (state.status.right === text) {
				return
			}
			state.status.right = text
			if (state.state === 'message') {
				atStatusLine(stderr, state, prompt, () => {
					updateStatusLine(stderr, state)
				})
			}
		},
		observable: new Observable<LineEditorEvent>((subscriber) => {
			const eventsSubscription = outputEvents.subscribe((event) => {
				subscriber.next(event)
			})
			const stateSubscription = stateMachine.subscribe({})

			try {
				stdin.setRawMode(true)
			} catch (e) {
				logger.error('Error setting raw mode:', e)
			}
			triggeredEvents.next({ type: 'resize' })
			outputEvents.next({ type: 'line-editor-state', state: 'off' })

			return () => {
				eventsSubscription.unsubscribe()
				stateSubscription.unsubscribe()

				try {
					stdin.setRawMode(false)
				} catch (e) {
					logger.error('Error resetting raw mode:', e)
				}

				setState('closed')
				stdin.unref()
			}
		}),
	} as LineEditor
}

type KeyHandler = (
	stdout: WriteStream,
	stderr: WriteStream,
	state: State,
	prompt: string,
) => void | { consumed: number }

interface Handler {
	process: (data: Buffer) => Buffer
}

function initializeHandlers({
	stdout,
	stderr,
	state,
	prompt,
	printer,
	onSubmit,
	complete,
	triggerEvent,
	history,
}: {
	stdout: WriteStream
	stderr: WriteStream
	state: State
	prompt: string
	printer: Printer
	onSubmit: (message: string) => void
	complete: (pattern: string) => void
	triggerEvent: (event: StateMachineEvent) => void
	history?: HistoryProvider
}): Handler {
	const keyHandlers: Record<string, KeyHandler> = {}

	// No-op handler for explicitly ignored keys
	const noOpHandler: KeyHandler = () => {
		// Do nothing - explicitly ignore these control characters
	}

	const submitHandler = createSubmitHandler(onSubmit)

	keyHandlers[bufferToHexKey(CR)] = submitHandler
	keyHandlers[bufferToHexKey(CTRL_ENTER)] = submitHandler
	keyHandlers[bufferToHexKey(kitty.CTRL_ENTER)] = submitHandler
	keyHandlers[bufferToHexKey(CMD_ENTER)] = submitHandler

	// Navigation - Go to start of current line (Ctrl+A)
	keyHandlers[bufferToHexKey(CTRL_A)] = gotoStartHandler
	keyHandlers[bufferToHexKey(kitty.CTRL_A)] = gotoStartHandler
	// Navigation - Go to end of current line (Ctrl+E)
	keyHandlers[bufferToHexKey(CTRL_E)] = gotoEndHandler
	keyHandlers[bufferToHexKey(kitty.CTRL_E)] = gotoEndHandler
	// Create arrow key handler functions with history
	const moveUpWithHistory = (
		stdout: WriteStream,
		stderr: WriteStream,
		state: State,
		prompt: string,
	) => {
		moveUpHandler(stdout, stderr, state, prompt, history)
	}
	const moveDownWithHistory = (
		stdout: WriteStream,
		stderr: WriteStream,
		state: State,
		prompt: string,
	) => {
		moveDownHandler(stdout, stderr, state, prompt, history)
	}

	// Navigation - Up arrow (ESC [ A) and Ctrl+P
	keyHandlers[bufferToHexKey(ARROW_UP)] = moveUpWithHistory
	keyHandlers[bufferToHexKey(CTRL_P)] = moveUpWithHistory
	keyHandlers[bufferToHexKey(kitty.CTRL_P)] = moveUpWithHistory
	// Navigation - Down arrow (ESC [ B) and Ctrl+N
	keyHandlers[bufferToHexKey(ARROW_DOWN)] = moveDownWithHistory
	keyHandlers[bufferToHexKey(CTRL_N)] = moveDownWithHistory
	keyHandlers[bufferToHexKey(kitty.CTRL_N)] = moveDownWithHistory
	// Navigation - Right arrow (ESC [ C) and Ctrl+F
	keyHandlers[bufferToHexKey(ARROW_RIGHT)] = moveRightHandler
	keyHandlers[bufferToHexKey(CTRL_F)] = moveRightHandler
	keyHandlers[bufferToHexKey(kitty.CTRL_F)] = moveRightHandler
	// Navigation - Left arrow (ESC [ D) and Ctrl+B
	keyHandlers[bufferToHexKey(ARROW_LEFT)] = moveLeftHandler
	keyHandlers[bufferToHexKey(CTRL_B)] = moveLeftHandler
	keyHandlers[bufferToHexKey(kitty.CTRL_B)] = moveLeftHandler
	// Delete left (Backspace or Ctrl+H)
	keyHandlers[bufferToHexKey(BACKSPACE)] = backspaceHandler
	keyHandlers[bufferToHexKey(CTRL_H)] = backspaceHandler
	keyHandlers[bufferToHexKey(kitty.CTRL_H)] = backspaceHandler
	keyHandlers[bufferToHexKey(kitty.SHIFT_BACKSPACE)] = backspaceHandler
	// Delete to beginning of current line (Ctrl+U)
	keyHandlers[bufferToHexKey(CTRL_U)] = deleteToStartHandler
	keyHandlers[bufferToHexKey(kitty.CTRL_U)] = deleteToStartHandler
	// Delete to end of current line (Ctrl+K)
	keyHandlers[bufferToHexKey(CTRL_K)] = deleteToEndHandler
	keyHandlers[bufferToHexKey(kitty.CTRL_K)] = deleteToEndHandler
	// Delete backward to a word boundary (Ctrl+W)
	keyHandlers[bufferToHexKey(CTRL_W)] = deleteWordHandler
	keyHandlers[bufferToHexKey(kitty.CTRL_W)] = deleteWordHandler
	keyHandlers[bufferToHexKey(ALT_BACKSPACE)] = deleteWordHandler
	keyHandlers[bufferToHexKey(kitty.ALT_BACKSPACE)] = deleteWordHandler
	keyHandlers[bufferToHexKey(CTRL_Y)] = yankHandler
	keyHandlers[bufferToHexKey(kitty.CTRL_Y)] = yankHandler
	keyHandlers[bufferToHexKey(CTRL_L)] = clearScreenHandler
	keyHandlers[bufferToHexKey(kitty.CTRL_L)] = clearScreenHandler
	// For newlines, use Shift+Enter
	// This must take precedence over the message submission handlers
	keyHandlers[bufferToHexKey(SHIFT_ENTER)] = newlineHandler
	keyHandlers[bufferToHexKey(kitty.SHIFT_ENTER)] = newlineHandler
	keyHandlers[bufferToHexKey(NL)] = newlineHandler

	// History navigation with Page Up/Down
	if (history) {
		keyHandlers[bufferToHexKey(PAGE_UP)] = createPageUpHandler(history, state)
		keyHandlers[bufferToHexKey(PAGE_DOWN)] = createPageDownHandler(history, state)
		keyHandlers[bufferToHexKey(kitty.PAGE_UP)] = createPageUpHandler(history, state)
		keyHandlers[bufferToHexKey(kitty.PAGE_DOWN)] = createPageDownHandler(history, state)
	}

	// Home key - jump to first line
	keyHandlers[bufferToHexKey(HOME)] = jumpToFirstLineHandler
	keyHandlers[bufferToHexKey(HOME_ALT)] = jumpToFirstLineHandler
	keyHandlers[bufferToHexKey(kitty.HOME)] = jumpToFirstLineHandler

	// End key - jump to last line
	keyHandlers[bufferToHexKey(END)] = jumpToLastLineHandler
	keyHandlers[bufferToHexKey(END_ALT)] = jumpToLastLineHandler
	keyHandlers[bufferToHexKey(kitty.END)] = jumpToLastLineHandler
	// Regular Enter, Ctrl+Enter, and Cmd+Enter are for message submission - they will be set within the observable constructor
	// Word navigation - Alt+F (forward one word) and Alt+B (backward one word)
	keyHandlers[bufferToHexKey(ALT_F)] = wordForwardHandler
	keyHandlers[bufferToHexKey(kitty.ALT_F)] = wordForwardHandler
	keyHandlers[bufferToHexKey(ALT_B)] = wordBackwardHandler
	keyHandlers[bufferToHexKey(kitty.ALT_B)] = wordBackwardHandler
	// Word deletion - Alt+D (delete word forward) and Alt+Backspace (delete word backward)
	keyHandlers[bufferToHexKey(CTRL_D)] = deleteForwardCharHandler
	keyHandlers[bufferToHexKey(kitty.CTRL_D)] = deleteForwardCharHandler
	keyHandlers[bufferToHexKey(ALT_D)] = deleteWordForwardHandler
	keyHandlers[bufferToHexKey(kitty.ALT_D)] = deleteWordForwardHandler
	// Delete key (forward delete)
	keyHandlers[bufferToHexKey(DEL)] = deleteForwardCharHandler
	keyHandlers[bufferToHexKey(kitty.DEL)] = deleteForwardCharHandler

	// Tab (cycle completion forward) and Shift+Tab (cycle completion backward)
	keyHandlers[bufferToHexKey(TAB)] = createCycleCompletionForwardHandler(triggerEvent)
	keyHandlers[bufferToHexKey(SHIFT_TAB)] = createCycleCompletionBackwardHandler(triggerEvent)
	keyHandlers[bufferToHexKey(kitty.SHIFT_TAB)] =
		createCycleCompletionBackwardHandler(triggerEvent)

	// Explicitly ignore unsupported control characters
	keyHandlers[bufferToHexKey(CTRL_G)] = noOpHandler
	keyHandlers[bufferToHexKey(kitty.CTRL_G)] = noOpHandler
	keyHandlers[bufferToHexKey(CTRL_O)] = noOpHandler
	keyHandlers[bufferToHexKey(kitty.CTRL_O)] = noOpHandler
	keyHandlers[bufferToHexKey(CTRL_Q)] = noOpHandler
	keyHandlers[bufferToHexKey(kitty.CTRL_Q)] = noOpHandler
	keyHandlers[bufferToHexKey(CTRL_R)] = noOpHandler
	keyHandlers[bufferToHexKey(kitty.CTRL_R)] = noOpHandler
	keyHandlers[bufferToHexKey(CTRL_S)] = noOpHandler
	keyHandlers[bufferToHexKey(kitty.CTRL_S)] = noOpHandler
	keyHandlers[bufferToHexKey(CTRL_T)] = noOpHandler
	keyHandlers[bufferToHexKey(kitty.CTRL_T)] = noOpHandler
	keyHandlers[bufferToHexKey(CTRL_V)] = noOpHandler
	keyHandlers[bufferToHexKey(kitty.CTRL_V)] = noOpHandler
	keyHandlers[bufferToHexKey(CTRL_X)] = noOpHandler
	keyHandlers[bufferToHexKey(kitty.CTRL_X)] = noOpHandler
	keyHandlers[bufferToHexKey(CTRL_Z)] = noOpHandler
	keyHandlers[bufferToHexKey(kitty.CTRL_Z)] = noOpHandler

	function process(data: Buffer): Buffer {
		if (data.length === 0) return data

		// Check for handlers that match the input
		for (const [keyStr, handler] of Object.entries(keyHandlers)) {
			const keyBuffer = Buffer.from(keyStr, 'hex')

			// If this key is a prefix of the input data
			if (isBufferPrefix(keyBuffer, data)) {
				handler(stdout, stderr, state, prompt)
				if (state.state === 'message') {
					updateMentions(stderr, state, complete, prompt)
				}
				state.lastChar = null
				return Buffer.from(Uint8Array.prototype.slice.call(data, keyBuffer.length))
			}
		}

		// Detect Kitty specific key sequences to prevent unhandled kitty codes from being treated as regular characters
		// Check if data starts with the CSI prefix (ESC [) which is common for all kitty sequences
		if (data.length >= 3 && data[0] === 0x1b && data[1] === 0x5b) {
			// Check for CSI u format (ends with 'u' which is 0x75)
			for (let i = 2; i < data.length; i++) {
				if (data[i] === 0x75) {
					// 'u' character - this is likely a kitty key code
					// Skip this key code entirely
					state.lastChar = null
					return Buffer.from(Uint8Array.prototype.slice.call(data, i + 1))
				}
			}
		}

		// If no handler matched, process as a complete Unicode character
		// Determine length of the complete UTF-8 character sequence
		let charLength = 1
		const firstByte = data[0]!
		// UTF-8 encoding rules to determine multi-byte sequence length
		if ((firstByte & 0xe0) === 0xc0)
			charLength = 2 // 2-byte sequence
		else if ((firstByte & 0xf0) === 0xe0)
			charLength = 3 // 3-byte sequence
		else if ((firstByte & 0xf8) === 0xf0) charLength = 4 // 4-byte sequence

		// Extract the complete Unicode character as a buffer and convert to string
		const char = Buffer.from(Uint8Array.prototype.slice.call(data, 0, charLength))
		const currentLine = state.lines[state.cursorLine]!
		const afterCursor = runes.substr(currentLine, state.cursorColumn)
		const charStr = char.toString('utf8')

		// Check if adding this character would exceed line width
		// Account for prompt length when calculating available width
		const availableWidth = state.screenColumns - prompt.length - 1
		const beforeCursor = runes.substr(currentLine, 0, state.cursorColumn)
		const newLineLength = beforeCursor.length + charStr.length + afterCursor.length

		// Handle special case for space that exactly wraps
		if (afterCursor.length === 0 && beforeCursor.length === availableWidth && charStr === ' ') {
			state.lines.splice(state.cursorLine + 1, 0, '')

			state.cursorLine++
			state.lines[state.cursorLine] = ''

			stderr.write('\n\r')
			stderr.write('\x1b[K') // Clear to end of line
			stderr.write(prompt)

			state.cursorColumn = 0

			redrawLinesFrom(stderr, state, prompt, state.cursorLine)
		}
		// If we're at the limit but the cursor is not at the end and the last char
		// on the line is a space, remove that and add the new char
		else if (
			currentLine.length == availableWidth &&
			currentLine[currentLine.length - 1] === ' ' &&
			state.cursorColumn < currentLine.length
		) {
			const withoutSpace = runes.substr(currentLine, 0, runes(currentLine).length - 1)
			// Remove the last space
			state.lines[state.cursorLine] =
				runes.substr(withoutSpace, 0, state.cursorColumn) +
				charStr +
				runes.substr(withoutSpace, state.cursorColumn)

			stderr.write('\r')
			stderr.write('\x1b[K') // Clear to end of line
			stderr.write(prompt + state.lines[state.cursorLine])
			state.cursorColumn++

			// Move cursor back to correct position
			stderr.write(`\x1b[${state.cursorColumn + 1}G`)
		}
		// Check if adding this character would make the line too long and we need word wrapping
		else if (newLineLength > availableWidth) {
			// Find the last space in the current line to determine word boundary
			const lastSpaceIndex = currentLine.lastIndexOf(' ')
			const originalCursorColumn = state.cursorColumn

			if (lastSpaceIndex !== -1) {
				const textBeforeSpace = runes.substr(currentLine, 0, lastSpaceIndex + 1) // Include the space

				if (lastSpaceIndex >= originalCursorColumn) {
					// Wrapped word is after cursor position

					const newCurrentLine =
						runes.substr(textBeforeSpace, 0, originalCursorColumn) +
						charStr +
						runes.substr(textBeforeSpace, originalCursorColumn)
					const newNextLine = runes.substr(currentLine, lastSpaceIndex + 1)

					state.cursorColumn = originalCursorColumn + charStr.length
					state.lines[state.cursorLine] = newCurrentLine
					state.lines.splice(state.cursorLine + 1, 0, newNextLine)

					redrawLinesFrom(stderr, state, prompt, state.cursorLine)
				} else {
					// Wrapped word is before or at cursor position
					const newCurrentLine = textBeforeSpace

					stderr.write(`\x1b[${prompt.length + textBeforeSpace.length + 1}G`)
					stderr.write('\x1b[K') // Clear to end of line

					const wordUpToCursor = runes.substr(
						currentLine,
						lastSpaceIndex + 1,
						originalCursorColumn - (lastSpaceIndex + 1),
					)
					const newNextLine = wordUpToCursor + charStr + afterCursor

					state.lines[state.cursorLine] = newCurrentLine
					state.lines.splice(state.cursorLine + 1, 0, newNextLine)

					state.cursorColumn = wordUpToCursor.length + charStr.length
					state.cursorLine++

					stderr.write('\n\r')
					stderr.write('\x1b[K') // Clear to end of line
					stderr.write(prompt)
					stderr.write(newNextLine)
					const textBeforeCursor = runes.substr(newNextLine, 0, state.cursorColumn)
					const visualPos = stringWidth(textBeforeCursor)
					stderr.write(`\x1b[${prompt.length + visualPos + 1}G`)

					redrawLinesFrom(stderr, state, prompt, state.cursorLine)
				}
			} else {
				// No spaces in the line, block the character from being added
				stderr.write('\x07') // Bell
			}
		} else {
			// Normal character insertion without wrapping
			state.lines[state.cursorLine] = beforeCursor + charStr + afterCursor
			stderr.write(`\x1b[${prompt.length + 1}G`)
			stderr.write('\x1b[K') // Clear from cursor to end of line
			stderr.write(beforeCursor + charStr + afterCursor)
			state.cursorColumn += 1 // Always increment by 1 logical position
			const visualPosition = stringWidth(beforeCursor + charStr)
			stderr.write(`\x1b[${visualPosition + prompt.length + 1}G`)
		}

		updateMentions(stderr, state, complete, prompt)

		state.lastChar = charStr
		return Buffer.from(Uint8Array.prototype.slice.call(data, charLength))
	}

	return { process }
}

// Handler functions
function gotoStartHandler(
	_stdout: WriteStream,
	stderr: WriteStream,
	state: State,
	prompt: string,
): void {
	state.cursorColumn = 0
	stderr.write(`\x1b[${prompt.length}G`)
}

// Helper to position cursor at specific line and column
function positionCursor(
	targetLine: number,
	targetColumn: number,
	stderr: WriteStream,
	state: State,
	prompt: string,
): void {
	// First go to beginning of current line
	stderr.write('\r')

	// Move up/down to target line
	if (targetLine < state.cursorLine) {
		// Move up if target line is above current line
		stderr.write(`\x1b[${state.cursorLine - targetLine}F`)
	} else if (targetLine > state.cursorLine) {
		// Move down if target line is below current line
		stderr.write(`\x1b[${targetLine - state.cursorLine}B`)
	}

	// Write prompt if exists
	if (prompt) {
		stderr.write(prompt)
	}

	// Move to correct column (1-based in terminal, accounting for prompt)
	const promptLength = prompt ? prompt.length : 0
	const lineText = state.lines[targetLine]!
	const textBeforeCursor = runes.substr(lineText, 0, targetColumn)
	const visualPos = stringWidth(textBeforeCursor)
	const effectiveColumn = visualPos + promptLength + 1

	if (effectiveColumn > 1 + promptLength) {
		stderr.write(`\x1b[${effectiveColumn}G`)
	}
}

function findWordBoundaryBackward(text: string, fromPos: number): number {
	const str = runes.substr(text, 0, fromPos)
	const match = str.match(/\S+\s*$/)
	if (!match) return 0

	const matchedRunes = runes(match[0]).length
	return fromPos - matchedRunes
}

function findWordBoundaryForward(text: string, fromPos: number): number {
	const str = runes.substr(text, fromPos)
	const match = str.match(/^\s*\S+/)
	if (!match) return runes(text).length

	const matchedRunes = runes(match[0]).length
	return fromPos + matchedRunes
}

// Handler for deleting a word forward
function deleteWordForwardHandler(
	_stdout: WriteStream,
	stderr: WriteStream,
	state: State,
	prompt: string,
): void {
	const currentLine = state.lines[state.cursorLine]!
	if (state.cursorColumn < currentLine.length) {
		const endPos = findWordBoundaryForward(currentLine, state.cursorColumn)
		const deletedPart = runes.substr(
			currentLine,
			state.cursorColumn,
			endPos - state.cursorColumn,
		)

		// Store for yank
		state.deletedText.unshift(deletedPart)
		if (state.deletedText.length > 10) state.deletedText.pop()

		// Delete the word
		state.lines[state.cursorLine] =
			runes.substr(currentLine, 0, state.cursorColumn) + runes.substr(currentLine, endPos)

		stderr.write('\x1b[K') // Clear from cursor to end of line
		stderr.write(runes.substr(currentLine, endPos)) // Write remaining text
		const textBeforeCursor = runes.substr(currentLine, 0, state.cursorColumn)
		const visualPos = stringWidth(textBeforeCursor)
		stderr.write(`\x1b[${prompt.length + visualPos + 1}G`) // move back cursor
	}
}

function createSubmitHandler(onSubmit: (message: string) => void): KeyHandler {
	return (stdout: WriteStream, stderr: WriteStream, state: State, prompt: string) => {
		// Don't submit if we're in the middle of processing pasted text
		if (state.isProcessingPaste) {
			// Call the newline handler instead of submitting
			newlineHandler(stdout, stderr, state, prompt)
			return
		}

		// Check for file mention mode
		const mention = getCurrentFileMention(state)
		if (mention !== null) {
			const currentLine = state.lines[state.cursorLine]!

			// Get the currently selected completion if available
			let replacement = mention.replacement
			if (state.completions.length > 0) {
				// Use the selected completion index
				const index = state.selectedCompletionIndex % state.completions.length
				replacement = state.completions[index]!
			}

			// Replace in state
			state.lines[state.cursorLine] =
				runes.substr(currentLine, 0, mention.wordStartPos) +
				replacement +
				runes.substr(currentLine, mention.wordEndPos)

			// Update cursor position to be after the inserted text
			state.cursorColumn = mention.wordStartPos + replacement.length

			// Clear any status line and completions
			state.status.left = ''
			state.completions = []
			state.lastCompletionQuery = null
			state.state = 'message'

			// Redraw the line
			stderr.write('\r')
			stderr.write('\x1b[K') // Clear line
			stderr.write(prompt + state.lines[state.cursorLine])
			const line = state.lines[state.cursorLine]!
			const beforeCursor = runes.substr(line, 0, state.cursorColumn)
			const visualPos = stringWidth(beforeCursor)
			stderr.write(`\x1b[${prompt.length + visualPos + 1}G`)

			atStatusLine(stderr, state, prompt, () => {
				updateStatusLine(stderr, state)
			})

			return
		}

		// Check if the last character was a backslash
		if (state.lastChar === '\\') {
			// Get the current line and remove the backslash
			const currentLine = state.lines[state.cursorLine]!
			state.lines[state.cursorLine] = runes.substr(currentLine, 0, state.cursorColumn - 1)

			// Move cursor back by one to remove the backslash
			stderr.write('\x1b[1D') // Move cursor left
			stderr.write('\x1b[K') // Clear from cursor to end of line
			state.cursorColumn--

			// Call the newline handler instead of submitting
			newlineHandler(stdout, stderr, state, prompt)

			return
		}

		const message = state.lines.join('\n')

		if (message.trim() === '') {
			return
		}

		// Overwrite any stderr-printed editor output with the final
		// message, now on stdout

		// Move up to the first line
		if (state.cursorLine > 0) {
			stderr.write(`\x1b[${state.cursorLine}F`)
		}
		// Clear rest of screen
		stderr.write('\r')
		stderr.write('\x1b[0J')

		// Rewrite all lines to stdout
		for (const line of state.lines) {
			stderr.write('\r')
			stdout.write(`${prompt}${line}\n`)
		}
		stderr.write('\r')
		stdout.write('\n')

		state.state = 'submitting'
		state.lines = ['']
		state.cursorLine = 0
		state.cursorColumn = 0
		state.status.left = ''
		state.lastCompletionQuery = null

		onSubmit(message)
	}
}

// Handler for go to end of line
function gotoEndHandler(
	_stdout: WriteStream,
	stderr: WriteStream,
	state: State,
	prompt: string,
): void {
	const currentLine = state.lines[state.cursorLine]!
	const runeCount = runes(currentLine).length
	state.cursorColumn = runeCount

	// Move to end of line
	stderr.write('\r')
	// Write prompt if exists
	if (prompt) {
		stderr.write(prompt)
	}
	// Account for prompt length in cursor positioning using visual width
	const promptLength = prompt ? prompt.length : 0
	const visualPos = stringWidth(currentLine)
	stderr.write(`\x1b[${visualPos + promptLength + 1}G`)
}

// Handler for move up
function moveUpHandler(
	_stdout: WriteStream,
	stderr: WriteStream,
	state: State,
	prompt: string,
	history?: HistoryProvider,
): void {
	// If at first line and we have a history provider, navigate to previous history item
	if (state.cursorLine === 0 && history) {
		// If not already navigating history, store current content
		if (!state.isNavigatingHistory) {
			const currentContent = state.lines.join('\n')
			state.currentHistoryContent = currentContent
			state.isNavigatingHistory = true
		}

		// Get previous item from history
		const previousItem = history.previous()
		if (previousItem !== null) {
			// Replace current content with history item
			replaceContent(stderr, state, prompt, previousItem)
		} else {
			// Play terminal bell if already at the beginning of history
			stderr.write('\x07') // Terminal bell
		}
	} else if (state.cursorLine > 0) {
		const targetLine = state.cursorLine - 1
		const targetCol = Math.min(state.cursorColumn, state.lines[targetLine]!.length)

		// Position cursor using the consistent helper function
		positionCursor(targetLine, targetCol, stderr, state, prompt)

		// Update cursor state after positioning
		state.cursorLine = targetLine
		state.cursorColumn = targetCol
	}
}

// Handler for move down
function moveDownHandler(
	_stdout: WriteStream,
	stderr: WriteStream,
	state: State,
	prompt: string,
	history?: HistoryProvider,
): void {
	// If at last line and we have a history provider, navigate to next history item
	const isLastLine = state.cursorLine === state.lines.length - 1

	if (isLastLine && history && state.isNavigatingHistory) {
		// Get next item from history
		const nextItem = history.next()

		// If we have a next item, use it
		if (nextItem !== null) {
			replaceContent(stderr, state, prompt, nextItem)
		} else {
			// If we've reached the end of history, restore the original content
			if (state.currentHistoryContent !== null) {
				replaceContent(stderr, state, prompt, state.currentHistoryContent)

				// Reset history navigation state
				state.isNavigatingHistory = false
				state.currentHistoryContent = null
				history.reset()
			} else {
				// Play terminal bell if trying to navigate beyond history with no saved content
				stderr.write('\x07') // Terminal bell
			}
		}
	} else if (state.cursorLine < state.lines.length - 1) {
		const targetLine = state.cursorLine + 1
		const targetCol = Math.min(state.cursorColumn, state.lines[targetLine]!.length)

		// Position cursor using the consistent helper function
		positionCursor(targetLine, targetCol, stderr, state, prompt)

		// Update cursor state after positioning
		state.cursorLine = targetLine
		state.cursorColumn = targetCol
	}
}

// Handler for move right
function moveRightHandler(
	_stdout: WriteStream,
	stderr: WriteStream,
	state: State,
	prompt: string,
): void {
	if (state.cursorColumn < state.lines[state.cursorLine]!.length) {
		const currentLine = state.lines[state.cursorLine]!
		const beforeCursor = runes.substr(currentLine, 0, state.cursorColumn)
		const afterMovingCursor = runes.substr(currentLine, 0, state.cursorColumn + 1)

		const visualDiff = stringWidth(afterMovingCursor) - stringWidth(beforeCursor)

		state.cursorColumn++
		stderr.write(`\x1b[${visualDiff}C`)
	} else if (state.cursorLine < state.lines.length - 1) {
		state.cursorLine++
		state.cursorColumn = 0
		stderr.write('\x1b[1B\r')
		// Write prompt at the beginning of the next line
		if (prompt) {
			stderr.write(prompt)
		}
	}
}

// Handler for move left
function moveLeftHandler(
	_stdout: WriteStream,
	stderr: WriteStream,
	state: State,
	prompt: string,
): void {
	if (state.cursorColumn > 0) {
		const currentLine = state.lines[state.cursorLine]!
		const beforeCursor = runes.substr(currentLine, 0, state.cursorColumn)
		const afterMovingCursor = runes.substr(currentLine, 0, state.cursorColumn - 1)

		const visualDiff = stringWidth(beforeCursor) - stringWidth(afterMovingCursor)

		state.cursorColumn--
		stderr.write(`\x1b[${visualDiff}D`)
	} else if (state.cursorLine > 0) {
		state.cursorLine--
		state.cursorColumn = state.lines[state.cursorLine]!.length
		stderr.write('\x1b[1F\r')
		// Write prompt if exists
		if (prompt) {
			stderr.write(prompt)
		}
		// Account for prompt length in cursor positioning
		const promptLength = prompt ? prompt.length : 0
		const currentLine = state.lines[state.cursorLine]!
		const beforeCursor = runes.substr(currentLine, 0, state.cursorColumn)
		const visualPos = stringWidth(beforeCursor)
		stderr.write(`\x1b[${visualPos + promptLength + 1}G`)
	}
}

// Handler for backspace
function backspaceHandler(
	_stdout: WriteStream,
	stderr: WriteStream,
	state: State,
	prompt: string,
): void {
	const availableWidth = state.screenColumns - prompt.length - 1

	if (state.cursorColumn > 0) {
		// Delete character within the current line
		const currentLine = state.lines[state.cursorLine]!

		const beforeCursor = runes.substr(currentLine, 0, state.cursorColumn)
		const runeToDelete = runes(beforeCursor).pop()!
		const charWidth = stringWidth(runeToDelete) || 1

		// Store for yank
		state.deletedText.unshift(runeToDelete)
		if (state.deletedText.length > 10) state.deletedText.pop()

		const afterCursor = runes.substr(currentLine, state.cursorColumn)

		const newBeforeCursor = runes.substr(beforeCursor, 0, runes(beforeCursor).length - 1)
		state.lines[state.cursorLine] = newBeforeCursor + afterCursor
		state.cursorColumn--

		// Update display: move cursor left, delete character, redraw remaining text
		stderr.write(`\x1b[${charWidth}D`) // Move left by character width
		stderr.write('\x1b[K') // Clear from cursor to end of line
		stderr.write(afterCursor) // Write remaining text

		// Move cursor back to correct position
		if (afterCursor.length > 0) {
			stderr.write(`\x1b[${stringWidth(afterCursor)}D`)
		}
	} else if (state.cursorLine > 0) {
		if (
			state.lines[state.cursorLine - 1]!.length + state.lines[state.cursorLine]!.length >
			availableWidth
		) {
			// If the line is too long, don't allow backspace
			stderr.write('\x07') // Bell
			return
		}
		// We're at the beginning of a line, join with previous line
		const previousLine = state.lines[state.cursorLine - 1]!
		const currentLine = state.lines[state.cursorLine]

		// Store newline for yank
		state.deletedText.unshift('\n')
		if (state.deletedText.length > 10) state.deletedText.pop()

		// Join the lines - set cursor at position of the join
		state.cursorColumn = previousLine.length
		state.lines[state.cursorLine - 1] = previousLine + currentLine
		state.lines.splice(state.cursorLine, 1)
		state.cursorLine--

		stderr.write(`\x1b[1F`) // Move up to the joined line
		stderr.write(`\x1b[${prompt ? prompt.length + 1 : 1}G`)

		redrawLinesFrom(stderr, state, prompt, state.cursorLine)
	}
}

// Handler for delete to beginning of line
function deleteToStartHandler(
	_stdout: WriteStream,
	stderr: WriteStream,
	state: State,
	prompt: string,
): void {
	if (state.cursorColumn > 0) {
		const currentLine = state.lines[state.cursorLine]!
		const deletedPart = runes.substr(currentLine, 0, state.cursorColumn)
		const afterCursor = runes.substr(currentLine, state.cursorColumn)

		// Store for yank
		state.deletedText.unshift(deletedPart)
		if (state.deletedText.length > 10) state.deletedText.pop()

		// Delete to beginning of line
		state.lines[state.cursorLine] = afterCursor

		// Move cursor to beginning of line
		stderr.write('\r')

		// Write prompt if exists
		if (prompt) {
			stderr.write(prompt)
		}

		// Clear the line and redraw
		stderr.write('\x1b[K')
		stderr.write(afterCursor)

		state.cursorColumn = 0
	}
}

// Handler for delete to end of line
function deleteToEndHandler(
	_stdout: WriteStream,
	stderr: WriteStream,
	state: State,
	_prompt: string,
): void {
	const currentLine = state.lines[state.cursorLine]!
	if (state.cursorColumn < currentLine.length) {
		const deletedPart = runes.substr(currentLine, state.cursorColumn)

		// Store for yank
		state.deletedText.unshift(deletedPart)
		if (state.deletedText.length > 10) state.deletedText.pop()

		// Delete to end of line
		state.lines[state.cursorLine] = runes.substr(currentLine, 0, state.cursorColumn)

		// Clear from cursor to end of line
		stderr.write('\x1b[K')
	}
}

// Handler for delete word backward
function deleteWordHandler(
	_stdout: WriteStream,
	stderr: WriteStream,
	state: State,
	prompt: string,
): void {
	if (state.cursorColumn > 0) {
		const currentLine = state.lines[state.cursorLine]!
		const textBeforeCursor = runes.substr(currentLine, 0, state.cursorColumn)
		const newPos = findWordBoundaryBackward(textBeforeCursor, runes(textBeforeCursor).length)

		// Store deleted text
		const deletedWord = runes.substr(textBeforeCursor, newPos)
		state.deletedText.unshift(deletedWord)
		if (state.deletedText.length > 10) state.deletedText.pop()

		// Get text after cursor
		const afterCursor = runes.substr(currentLine, state.cursorColumn)

		// Delete the word
		state.lines[state.cursorLine] = runes.substr(textBeforeCursor, 0, newPos) + afterCursor

		// Move cursor back to word boundary and redraw rest
		const visualPos = stringWidth(runes.substr(textBeforeCursor, 0, newPos))
		stderr.write(`\x1b[${prompt.length + visualPos + 1}G`)
		stderr.write('\x1b[K')
		stderr.write(afterCursor)
		stderr.write(`\x1b[${prompt.length + visualPos + 1}G`)

		// Update cursor position
		state.cursorColumn = newPos
	}
}

// Function to create a handler for Page Up (navigate to previous history item)
function createPageUpHandler(history: HistoryProvider, state: State): KeyHandler {
	return (
		_stdout: WriteStream,
		stderr: WriteStream,
		editorState: State,
		prompt: string,
	): void => {
		// If not already navigating history, store current content
		if (!editorState.isNavigatingHistory) {
			const currentContent = editorState.lines.join('\n')
			editorState.currentHistoryContent = currentContent
			editorState.isNavigatingHistory = true
		}

		// Get previous item from history
		const previousItem = history.previous()
		if (previousItem !== null) {
			// Replace current content with history item
			replaceContent(stderr, editorState, prompt, previousItem)
		} else {
			// Play terminal bell if already at the beginning of history
			stderr.write('\x07') // Terminal bell
		}
	}
}

// Function to create a handler for Page Down (navigate to next history item)
function createPageDownHandler(history: HistoryProvider, state: State): KeyHandler {
	return (
		_stdout: WriteStream,
		stderr: WriteStream,
		editorState: State,
		prompt: string,
	): void => {
		// Only process if we're already in history navigation mode
		if (!editorState.isNavigatingHistory) {
			// Play terminal bell if trying to navigate down when not in history mode
			stderr.write('\x07') // Terminal bell
			return
		}

		// Get next item from history
		const nextItem = history.next()

		// If we have a next item, use it
		if (nextItem !== null) {
			replaceContent(stderr, editorState, prompt, nextItem)
		} else {
			// If we've reached the end of history, restore the original content
			if (editorState.currentHistoryContent !== null) {
				replaceContent(stderr, editorState, prompt, editorState.currentHistoryContent)

				// Reset history navigation state
				editorState.isNavigatingHistory = false
				editorState.currentHistoryContent = null
				history.reset()
			} else {
				// Play terminal bell if trying to navigate beyond history with no saved content
				stderr.write('\x07') // Terminal bell
			}
		}
	}
}

function jumpToFirstLineHandler(
	_stdout: WriteStream,
	stderr: WriteStream,
	state: State,
	prompt: string,
): void {
	// If already on first line, just go to start of line
	if (state.cursorLine === 0) {
		state.cursorColumn = 0
		// Move to beginning of first line
		stderr.write('\r')
		// Write prompt if exists
		if (prompt) {
			stderr.write(prompt)
		}
		return
	}

	// Jump to first line at position 0
	positionCursor(0, 0, stderr, state, prompt)
	state.cursorLine = 0
	state.cursorColumn = 0
}

// Handler for End key - jump to last line
function jumpToLastLineHandler(
	_stdout: WriteStream,
	stderr: WriteStream,
	state: State,
	prompt: string,
): void {
	const lastLineIndex = state.lines.length - 1

	// If already on last line, just go to end of line
	if (state.cursorLine === lastLineIndex) {
		state.cursorColumn = state.lines[lastLineIndex]!.length
		// Move to end of line
		stderr.write('\r')
		// Write prompt if exists
		if (prompt) {
			stderr.write(prompt)
		}
		// Account for prompt length in cursor positioning
		const promptLength = prompt ? prompt.length : 0
		const lastLine = state.lines[lastLineIndex]!
		const visualPos = stringWidth(lastLine)
		stderr.write(`\x1b[${visualPos + promptLength + 1}G`)
		return
	}

	// Jump to last line at end position
	const lastLineLength = state.lines[lastLineIndex]!.length
	positionCursor(lastLineIndex, lastLineLength, stderr, state, prompt)
	state.cursorLine = lastLineIndex
	state.cursorColumn = lastLineLength
}

// Helper function to replace the editor content
function replaceContent(stderr: WriteStream, state: State, prompt: string, content: string): void {
	// Clear the current content
	if (state.cursorLine > 0) {
		stderr.write(`\x1b[${state.cursorLine}F`)
	}
	stderr.write('\r')
	for (let i = 0; i < state.lines.length; i++) {
		stderr.write('\x1b[K') // Clear line

		// Move down to the next line (except for the last line)
		if (i < state.lines.length - 1) {
			stderr.write('\n\r')
		}
	}

	// Go back to the first line
	if (state.lines.length > 1) {
		stderr.write(`\x1b[${state.lines.length - 1}F`)
	}

	// Split content into lines and update state
	const newLines = content.split('\n')
	state.lines = newLines
	state.cursorLine = 0
	state.cursorColumn = newLines.length > 0 ? newLines[0]?.trimEnd().length || 0 : 0

	// Redraw the content
	redrawLinesFrom(stderr, state, prompt, 0)
}

function yankHandler(
	_stdout: WriteStream,
	stderr: WriteStream,
	state: State,
	prompt: string,
): void {
	if (state.deletedText.length > 0) {
		const textToInsert = state.deletedText[0]!

		if (textToInsert === '\n') {
			// Split the current line
			const currentLine = state.lines[state.cursorLine]!
			const beforeCursor = runes.substr(currentLine, 0, state.cursorColumn)
			const afterCursor = runes.substr(currentLine, state.cursorColumn)

			state.lines[state.cursorLine] = beforeCursor
			state.lines.splice(state.cursorLine + 1, 0, afterCursor)

			// Clear to end of line
			stderr.write('\x1b[K')

			// Add a new line and write the after-cursor content
			stderr.write('\n\r')
			stderr.write(afterCursor)

			// Move cursor to beginning of new line
			stderr.write('\r')

			// Write prompt if exists
			if (prompt) {
				stderr.write(prompt)
			}

			state.cursorLine++
			state.cursorColumn = 0
		} else {
			// Insert at cursor position
			const currentLine = state.lines[state.cursorLine]!
			const afterCursor = runes.substr(currentLine, state.cursorColumn)

			// Update line in data structure
			state.lines[state.cursorLine] =
				runes.substr(currentLine, 0, state.cursorColumn) + textToInsert + afterCursor

			// Display the inserted text and text after cursor
			stderr.write(textToInsert)
			stderr.write(afterCursor)

			// Move cursor back to after the inserted text
			if (afterCursor.length > 0) {
				stderr.write(`\x1b[${afterCursor.length}D`)
			}

			state.cursorColumn += textToInsert.length
		}
	}
}

// Handler for clear screen
function clearScreenHandler(
	_stdout: WriteStream,
	stderr: WriteStream,
	state: State,
	prompt: string,
): void {
	// Clear the screen and reset position
	stderr.write('\x1b[2J') // Clear entire screen
	stderr.write('\x1b[H') // Move cursor to home position

	redrawLinesFrom(stderr, state, prompt, 0)
}

// Handler for forward word navigation
function wordForwardHandler(
	_stdout: WriteStream,
	stderr: WriteStream,
	state: State,
	prompt: string,
): void {
	const currentLine = state.lines[state.cursorLine]!
	if (state.cursorColumn < currentLine.length) {
		const newPos = findWordBoundaryForward(currentLine, state.cursorColumn)

		const beforeOld = runes.substr(currentLine, 0, state.cursorColumn)
		const beforeNew = runes.substr(currentLine, 0, newPos)
		const visualDiff = stringWidth(beforeNew) - stringWidth(beforeOld)

		if (visualDiff > 0) {
			stderr.write(`\x1b[${visualDiff}C`) // Move right by visual width
			state.cursorColumn = newPos
		}
	} else if (state.cursorLine < state.lines.length - 1) {
		// At end of line, move to next line
		stderr.write('\n\r')
		if (prompt) {
			stderr.write(prompt)
		}
		state.cursorLine++
		state.cursorColumn = 0
	}
}

// Handler for backward word navigation
function wordBackwardHandler(
	_stdout: WriteStream,
	stderr: WriteStream,
	state: State,
	prompt: string,
): void {
	const currentLine = state.lines[state.cursorLine]!
	if (state.cursorColumn > 0) {
		const newPos = findWordBoundaryBackward(currentLine, state.cursorColumn)

		const beforeOld = runes.substr(currentLine, 0, state.cursorColumn)
		const beforeNew = runes.substr(currentLine, 0, newPos)
		const visualDiff = stringWidth(beforeOld) - stringWidth(beforeNew)

		if (visualDiff > 0) {
			stderr.write(`\x1b[${visualDiff}D`) // Move left by visual width
			state.cursorColumn = newPos
		}
	} else if (state.cursorLine > 0) {
		// At beginning of line, move to previous line
		stderr.write('\x1b[1F\r') // Move up and to start of line
		if (prompt) {
			stderr.write(prompt)
		}
		state.cursorLine--
		state.cursorColumn = state.lines[state.cursorLine]!.length

		// Move cursor to end of previous line
		const promptLength = prompt ? prompt.length : 0
		stderr.write(`\x1b[${state.cursorColumn + promptLength + 1}G`)
	}
}

function newlineHandler(
	_stdout: WriteStream,
	stderr: WriteStream,
	state: State,
	prompt: string,
): void {
	// Split the current line at cursor position
	const currentLine = state.lines[state.cursorLine]!
	const beforeCursor = runes.substr(currentLine, 0, state.cursorColumn)
	const afterCursor = runes.substr(currentLine, state.cursorColumn)

	// Update data structure
	state.lines[state.cursorLine] = beforeCursor
	state.lines.splice(state.cursorLine + 1, 0, afterCursor)

	// First clear from cursor to end of line (delete the after-cursor text from view)
	stderr.write('\x1b[0K')
	stderr.write('\n') // Move to next line

	// If we're at end of file (just adding a new line at the end), simple case
	if (state.cursorLine >= state.lines.length - 2) {
		stderr.write('\x1b[2K' + '\r')
		stderr.write(prompt + afterCursor)

		// Update cursor position
		state.cursorLine++
		state.cursorColumn = 0

		// Make sure there's a status line and then go back up
		stderr.write('\r\n')
		updateStatusLine(stderr, state)
		stderr.write('\x1b[1F') // Move up one line
		stderr.write(`\x1b[${prompt.length + 1}G`) // Move to start of line

		return
	}

	// Update cursor state
	state.cursorLine++
	state.cursorColumn = 0

	redrawLinesFrom(stderr, state, prompt, state.cursorLine)
}

// Function to create the Tab handler (cycle forward)
function createCycleCompletionForwardHandler(
	triggerEvent: (event: StateMachineEvent) => void,
): KeyHandler {
	return (_stdout: WriteStream, stderr: WriteStream, state: State, _prompt: string): void => {
		// Only process in file mention mode with available completions
		const mention = getCurrentFileMention(state)
		if (mention && state.completions.length > 0) {
			// Trigger cycle event
			triggerEvent({ type: 'cycle-completion', direction: 'forward' })
		}
	}
}

// Function to create the Shift+Tab handler (cycle backward)
function createCycleCompletionBackwardHandler(
	triggerEvent: (event: StateMachineEvent) => void,
): KeyHandler {
	return (_stdout: WriteStream, stderr: WriteStream, state: State, _prompt: string): void => {
		// Only process in file mention mode with available completions
		const mention = getCurrentFileMention(state)
		if (mention && state.completions.length > 0) {
			// Trigger cycle event
			triggerEvent({ type: 'cycle-completion', direction: 'backward' })
		}
	}
}

// Add a handler for Ctrl+D to delete forward character
function deleteForwardCharHandler(
	_stdout: WriteStream,
	stderr: WriteStream,
	state: State,
	prompt: string,
): void {
	const currentLine = state.lines[state.cursorLine]!
	if (state.cursorColumn < currentLine.length) {
		// Delete the character at the cursor position
		const allRunes = runes(currentLine)
		const deletedRune = allRunes[state.cursorColumn]!

		// Store for yank
		state.deletedText.unshift(deletedRune)
		if (state.deletedText.length > 10) state.deletedText.pop()

		const beforeCursor = runes.substr(currentLine, 0, state.cursorColumn)
		const afterCursor = runes.substr(currentLine, state.cursorColumn + 1)
		state.lines[state.cursorLine] = beforeCursor + afterCursor

		// Clear from cursor to end of line and redraw the remaining text
		stderr.write('\x1b[K') // Clear from cursor to end of line
		stderr.write(afterCursor) // Write remaining text

		// Move cursor back to the original position
		if (afterCursor.length > 0) {
			stderr.write(`\x1b[${stringWidth(afterCursor)}D`)
		}
	} else if (state.cursorLine < state.lines.length - 1) {
		// At end of line, join with next line
		const nextLine = state.lines[state.cursorLine + 1]!

		// Store newline for yank
		state.deletedText.unshift('\n')
		if (state.deletedText.length > 10) state.deletedText.pop()

		// Join the lines
		state.lines[state.cursorLine] = state.lines[state.cursorLine]! + nextLine
		state.lines.splice(state.cursorLine + 1, 1)

		// Redraw from current line
		redrawLinesFrom(stderr, state, prompt, state.cursorLine)
	}
}

function atStatusLine(stderr: WriteStream, state: State, prompt: string, inner: () => void) {
	// Move down to the status line
	for (let i = 0; i < state.lines.length - state.cursorLine; i++) {
		stderr.write('\n\r')
	}
	stderr.write('\x1b[K') // Clear line

	// Call the inner function
	inner()

	// Move back up to the original cursor position
	stderr.write(`\x1b[${state.lines.length - state.cursorLine}F`)
	const currentLine = state.lines[state.cursorLine]!
	const textBeforeCursor = runes.substr(currentLine, 0, state.cursorColumn)
	const visualPos = stringWidth(textBeforeCursor)
	stderr.write(`\x1b[${visualPos + prompt.length + 1}G`)
}

function updateStatusLine(stderr: WriteStream, state: State): void {
	// Calculate space for left and right status elements
	const availableWidth = state.screenColumns
	let leftStatus = state.status.left
	let rightStatus = state.status.right

	// If we have right status and there's room for both
	if (rightStatus && rightStatus.length > 0) {
		const combinedLength = stringWidth(leftStatus) + stringWidth(rightStatus) + 2 // +2 for spacing

		// If not enough room for both, prioritize right status
		if (combinedLength > availableWidth) {
			// If right status is too large by itself, truncate it
			if (stringWidth(rightStatus) >= availableWidth) {
				rightStatus = rightStatus.slice(0, availableWidth - 3) + '...'
				leftStatus = ''
			} else {
				// Truncate left status to make room for right
				const leftAvailable = availableWidth - stringWidth(rightStatus) - 2
				if (leftAvailable > 3) {
					leftStatus = leftStatus.slice(0, leftAvailable - 3) + '...'
				} else {
					leftStatus = ''
				}
			}
		}

		stderr.write(leftStatus)

		const leftWidth = stringWidth(leftStatus)
		const rightWidth = stringWidth(rightStatus)

		const padding = availableWidth - leftWidth - rightWidth
		if (padding > 0) {
			stderr.write(' '.repeat(padding))
		}

		// Reset colors before writing right status
		stderr.write('\x1b[0m')
		stderr.write(rightStatus)
	} else {
		// Just write the left status
		stderr.write(leftStatus)
	}
}

interface FileMention {
	index: number
	fullWord: string
	replacement: string
	wordStartPos: number
	wordEndPos: number
}

function getCurrentFileMention(state: State): FileMention | null {
	const currentLine = state.lines[state.cursorLine]!

	// Get text before and after cursor
	const beforeCursor = runes.substr(currentLine, 0, state.cursorColumn)
	const afterCursor = runes.substr(currentLine, state.cursorColumn)

	// Find word boundaries around cursor position
	// Match non-space before cursor and non-space after cursor as a single word
	const beforeMatch = beforeCursor.match(/\S*$/)
	const afterMatch = afterCursor.match(/^\S*/)

	// Combine the parts to get the full word at cursor
	const beforePart = beforeMatch ? beforeMatch[0] : ''
	const afterPart = afterMatch ? afterMatch[0] : ''
	const fullWord = beforePart + afterPart

	// Check if it's empty or just whitespace
	if (!fullWord.trim()) {
		return null
	}

	// Only consider it a mention if the word starts with @
	if (!fullWord.startsWith('@')) {
		return null
	}

	// Calculate the absolute position of @ in the line
	const wordStartPos = state.cursorColumn - beforePart.length
	const wordEndPos = wordStartPos + fullWord.length
	const replacement = runes.substr(fullWord, 1) // Remove the @

	return {
		index: wordStartPos,
		fullWord,
		replacement,
		wordStartPos,
		wordEndPos,
	}
}

export function redrawLinesFrom(
	stderr: WriteStream,
	state: State,
	prompt: string,
	startLine: number,
): void {
	const lineCountBeforeCursor = state.cursorLine - startLine
	// Move back up first if required
	if (lineCountBeforeCursor > 0) {
		stderr.write(`\x1b[${lineCountBeforeCursor}F`)
	} else if (lineCountBeforeCursor < 0) {
		throw new Error('Invalid redraw start position')
	}
	// Get lines that need to be redrawn
	const linesToRedraw = state.lines.slice(startLine)!

	stderr.write('\r')
	stderr.write(`\x1b[0J`)

	// Clear and redraw all following lines
	for (let i = 0; i < linesToRedraw.length; i++) {
		stderr.write(prompt + linesToRedraw[i]! + '\n\r')
	}

	updateStatusLine(stderr, state)

	// Position cursor - taking into account the added status line
	const linesToMoveUp = linesToRedraw.length - lineCountBeforeCursor
	if (linesToMoveUp > 0) {
		stderr.write(`\x1b[${linesToMoveUp}F`)
	}
	const currentLine = state.lines[state.cursorLine]!
	const textBeforeCursor = runes.substr(currentLine, 0, state.cursorColumn)
	const visualPos = stringWidth(textBeforeCursor)
	stderr.write(`\x1b[${prompt.length + visualPos + 1}G`)
}

export function renderCompletions(
	stderr: WriteStream,
	printer: Printer,
	candidates: string[],
	selectedIndex: number = 0,
): string {
	const rotatedCandidates = [
		...candidates.slice(selectedIndex),
		...candidates.slice(0, selectedIndex),
	]
	const spacer = '  '
	let width = 0
	const fitted = []
	for (const candidate of rotatedCandidates) {
		width += spacer.length + stringWidth(candidate)
		if (width <= stderr.columns) {
			fitted.push(candidate)
		}
	}
	const [first, ...rest] = fitted
	if (!first) {
		return ''
	}
	return [
		printer.print(first, { foreground: 'blue' }),

		...rest.map((candidate) => printer.print(candidate, { foreground: 'gray' })),
	].join(spacer)
}

function updateMentions(
	stderr: WriteStream,
	state: State,
	complete: (text: string) => void,
	prompt: string,
) {
	const mention = getCurrentFileMention(state)
	if (mention !== null) {
		// Only call complete if the query pattern has changed
		if (state.lastCompletionQuery !== mention.replacement) {
			state.lastCompletionQuery = mention.replacement
			complete(mention.replacement) // Send the complete word after @ for completion
			logger.debug('Entered file mention mode', { query: mention.replacement })
		}
	} else {
		state.state = 'message'
		state.status.left = ''
		state.lastCompletionQuery = null
	}
	atStatusLine(stderr, state, prompt, () => {
		updateStatusLine(stderr, state)
	})
}
