import { getClientApplication } from '../misc/known-clients'

export type OS = 'darwin' | 'windows' | 'linux'

export interface PlatformInfo {
	webBrowser: boolean
	os: OS
	client: string

	ctrlCmdKeyShort: '⌘' | '⌃'
	ctrlCmdKey: '⌘' | 'Ctrl'
}

let _platform: PlatformInfo

export function platform(): PlatformInfo {
	if (!_platform) {
		const isNode = typeof process !== 'undefined' && process.versions && process.versions.node
		const webBrowser = !isNode

		let os: OS = 'linux'
		if (typeof navigator !== 'undefined') {
			const platform = navigator.platform.toLowerCase()
			if (platform.includes('mac') || platform.includes('ios')) os = 'darwin'
			else if (platform.includes('win')) os = 'windows'
		} else if (typeof process !== 'undefined') {
			if (process.platform === 'darwin') os = 'darwin'
			else if (process.platform === 'win32') os = 'windows'
		}

		_platform = {
			webBrowser,
			os,
			client: getClientApplication().name,

			ctrlCmdKeyShort: os === 'darwin' ? '⌘' : '⌃',
			ctrlCmdKey: os === 'darwin' ? '⌘' : 'Ctrl',
		}
	}

	return _platform
}
