import type { ReadStream, WriteStream } from 'tty'
import { isBufferPrefix } from './line-editor/buffer'
import { kitty } from './line-editor/keys'

export type Stdio = {
	stdin: ReadStream
	stdout: WriteStream
}

export default async function question(
	text: string,
	options: string[],
	{ stdin, stdout }: Stdio = { stdin: process.stdin, stdout: process.stdout },
): Promise<string | null> {
	return new Promise<string | null>((resolve) => {
		// Format the options, making the first one uppercase to indicate it's default
		const formattedOptions = options
			.map((opt, i) => (i === 0 ? opt.toUpperCase() : opt.toLowerCase()))
			.join('/')
		stdout.write(`${text} (${formattedOptions}) `)

		function finalize(result: string | null) {
			stdin.removeListener('data', onData)
			stdin.removeListener('error', onError)
			stdout.write('\n')
			resolve(result)
		}

		function onData(data: Buffer) {
			for (let i = 0; i < data.length; i++) {
				// CTRL+C
				if (data[i] === 3) {
					finalize(null)
					return
				}

				// Check for Kitty keyboard protocol CTRL+C
				if (isBufferPrefix(kitty.CTRL_C, data.subarray(i))) {
					finalize(null)
					return
				}

				// ESC key
				if (data[i] === 27) {
					// If this is the beginning of a longer escape sequence, we'll wait
					// to see if more bytes come in. If not, treat as ESC.
					if (data.length === 1) {
						finalize(null)
						return
					}
				}

				// Check for Kitty keyboard protocol ESC
				if (isBufferPrefix(kitty.ESC, data.subarray(i))) {
					finalize(null)
					return
				}

				const char = String.fromCharCode(data[i]!).toLowerCase()

				// Check if the character is in our options list (case insensitive)
				for (const opt of options) {
					if (opt.toLowerCase() === char) {
						// Print the character that was entered
						stdout.write(opt)
						// Finalize with the selected option
						finalize(opt)
						return
					}
				}

				// Enter/Return key selects the default option (first one)
				if (data[i] === 13 || data[i] === 10) {
					// CR or NL
					stdout.write(options[0] || '')
					finalize(options[0] || '')
					return
				}
			}
		}

		function onError(err: Error) {
			finalize(null)
		}

		stdin.on('data', onData)
		stdin.once('error', onError)
	})
}
