// Traditional control codes and escape sequences
export const CTRL_A = Buffer.from([0x01]) // Go to start of line
export const CTRL_B = Buffer.from([0x02]) // Back one character
export const CTRL_C = Buffer.from([0x03]) // Interrupt
export const CTRL_D = Buffer.from([0x04]) // End of transmission
export const CTRL_E = Buffer.from([0x05]) // Go to end of line
export const CTRL_F = Buffer.from([0x06]) // Forward one character
export const CTRL_H = Buffer.from([0x08]) // Delete left (same as backspace)
export const CTRL_K = Buffer.from([0x0b]) // Delete from current position to end of line
export const CTRL_L = Buffer.from([0x0c]) // Clear screen
export const CTRL_N = Buffer.from([0x0e]) // Next line
export const CTRL_P = Buffer.from([0x10]) // Previous line
export const CTRL_U = Buffer.from([0x15]) // Delete from current position to line start
export const CTRL_W = Buffer.from([0x17]) // Delete backward to a word boundary
export const CTRL_Y = Buffer.from([0x19]) // Yank previously deleted text

// Unsupported control characters (explicitly ignored)
export const CTRL_G = Buffer.from([0x07]) // Bell/Cancel (ignored)
export const CTRL_O = Buffer.from([0x0f]) // Shift out (ignored)
export const CTRL_Q = Buffer.from([0x11]) // XON flow control (ignored)
export const CTRL_R = Buffer.from([0x12]) // Reverse search (ignored for now)
export const CTRL_S = Buffer.from([0x13]) // XOFF flow control (ignored)
export const CTRL_T = Buffer.from([0x14]) // Transpose characters (ignored for now)
export const CTRL_V = Buffer.from([0x16]) // Literal next/paste (ignored for now)
export const CTRL_X = Buffer.from([0x18]) // Cut (ignored for now)
export const CTRL_Z = Buffer.from([0x1a]) // Suspend/undo (ignored for now)

// Basic control codes
export const ESC = Buffer.from([0x1b])
export const CR = Buffer.from([0x0d])
export const NL = Buffer.from([0x0a])
export const BACKSPACE = Buffer.from([0x7f])
export const TAB = Buffer.from([0x09])
export const SHIFT_TAB = Buffer.from([0x1b, 0x5b, 0x5a]) // CSI Z

// Arrow keys
export const ARROW_UP = Buffer.from([0x1b, 0x5b, 0x41]) // Up arrow
export const ARROW_DOWN = Buffer.from([0x1b, 0x5b, 0x42]) // Down arrow
export const ARROW_RIGHT = Buffer.from([0x1b, 0x5b, 0x43]) // Right arrow
export const ARROW_LEFT = Buffer.from([0x1b, 0x5b, 0x44]) // Left arrow
export const PAGE_UP = Buffer.from([0x1b, 0x5b, 0x35, 0x7e]) // PgUp
export const PAGE_DOWN = Buffer.from([0x1b, 0x5b, 0x36, 0x7e]) // PgDn
export const HOME = Buffer.from([0x1b, 0x5b, 0x48]) // Home
export const END = Buffer.from([0x1b, 0x5b, 0x46]) // End
export const HOME_ALT = Buffer.from([0x1b, 0x5b, 0x31, 0x7e]) // Home alternative
export const END_ALT = Buffer.from([0x1b, 0x5b, 0x34, 0x7e]) // End alternative
export const DEL = Buffer.from([0x1b, 0x5b, 0x33, 0x7e]) // Delete key

// Meta keys
export const ALT_F = Buffer.from([0x1b, 0x66]) // Forward one word (Meta+F on macOS)
export const ALT_B = Buffer.from([0x1b, 0x62]) // Back one word (Meta+B on macOS)
export const ALT_D = Buffer.from([0x1b, 0x64]) // Delete word forward (Meta+D on macOS)
export const ALT_BACKSPACE = Buffer.from([0x1b, 0x7f]) // Delete word backward (Meta+Backspace on macOS)

// Multi-key combinations
export const SHIFT_ENTER = Buffer.from([0x1b, 0x5b, 0x32, 0x37, 0x3b, 0x32, 0x3b, 0x31, 0x33, 0x7e])
export const CTRL_ENTER = Buffer.from([0x1b, 0x5b, 0x32, 0x37, 0x3b, 0x35, 0x3b, 0x31, 0x33, 0x7e])
export const CMD_ENTER = Buffer.from([0x1b, 0x5b, 0x32, 0x37, 0x3b, 0x38, 0x3b, 0x31, 0x33, 0x7e]) // For Mac users

// Kitty keyboard protocol key codes (https://sw.kovidgoyal.net/kitty/keyboard-protocol/)
export const kitty = {
	ESC: Buffer.from([0x1b, 0x5b, 0x32, 0x37, 0x75]), // CSI 27 u (Escape key)
	// Control keys
	CTRL_A: Buffer.from([0x1b, 0x5b, 0x39, 0x37, 0x3b, 0x35, 0x75]), // CSI 97;5 u ('a')
	CTRL_B: Buffer.from([0x1b, 0x5b, 0x39, 0x38, 0x3b, 0x35, 0x75]), // CSI 98;5 u ('b')
	CTRL_C: Buffer.from([0x1b, 0x5b, 0x39, 0x39, 0x3b, 0x35, 0x75]), // CSI 99;5 u ('c')
	CTRL_D: Buffer.from([0x1b, 0x5b, 0x31, 0x30, 0x30, 0x3b, 0x35, 0x75]), // CSI 100;5 u ('d')
	CTRL_E: Buffer.from([0x1b, 0x5b, 0x31, 0x30, 0x31, 0x3b, 0x35, 0x75]), // CSI 101;5 u ('e')
	CTRL_F: Buffer.from([0x1b, 0x5b, 0x31, 0x30, 0x32, 0x3b, 0x35, 0x75]), // CSI 102;5 u ('f')
	CTRL_H: Buffer.from([0x1b, 0x5b, 0x31, 0x30, 0x34, 0x3b, 0x35, 0x75]), // CSI 104;5 u ('h')
	CTRL_K: Buffer.from([0x1b, 0x5b, 0x31, 0x30, 0x37, 0x3b, 0x35, 0x75]), // CSI 107;5 u ('k')
	CTRL_L: Buffer.from([0x1b, 0x5b, 0x31, 0x30, 0x38, 0x3b, 0x35, 0x75]), // CSI 108;5 u ('l')
	CTRL_N: Buffer.from([0x1b, 0x5b, 0x31, 0x31, 0x30, 0x3b, 0x35, 0x75]), // CSI 110;5 u ('n')
	CTRL_P: Buffer.from([0x1b, 0x5b, 0x31, 0x31, 0x32, 0x3b, 0x35, 0x75]), // CSI 112;5 u ('p')
	CTRL_U: Buffer.from([0x1b, 0x5b, 0x31, 0x31, 0x37, 0x3b, 0x35, 0x75]), // CSI 117;5 u ('u')
	CTRL_W: Buffer.from([0x1b, 0x5b, 0x31, 0x31, 0x39, 0x3b, 0x35, 0x75]), // CSI 119;5 u ('w')
	CTRL_Y: Buffer.from([0x1b, 0x5b, 0x31, 0x32, 0x31, 0x3b, 0x35, 0x75]), // CSI 121;5 u ('y')

	// Unsupported control characters (explicitly ignored)
	CTRL_G: Buffer.from([0x1b, 0x5b, 0x31, 0x30, 0x33, 0x3b, 0x35, 0x75]), // CSI 103;5 u ('g')
	CTRL_O: Buffer.from([0x1b, 0x5b, 0x31, 0x31, 0x31, 0x3b, 0x35, 0x75]), // CSI 111;5 u ('o')
	CTRL_Q: Buffer.from([0x1b, 0x5b, 0x31, 0x31, 0x33, 0x3b, 0x35, 0x75]), // CSI 113;5 u ('q')
	CTRL_R: Buffer.from([0x1b, 0x5b, 0x31, 0x31, 0x34, 0x3b, 0x35, 0x75]), // CSI 114;5 u ('r')
	CTRL_S: Buffer.from([0x1b, 0x5b, 0x31, 0x31, 0x35, 0x3b, 0x35, 0x75]), // CSI 115;5 u ('s')
	CTRL_T: Buffer.from([0x1b, 0x5b, 0x31, 0x31, 0x36, 0x3b, 0x35, 0x75]), // CSI 116;5 u ('t')
	CTRL_V: Buffer.from([0x1b, 0x5b, 0x31, 0x31, 0x38, 0x3b, 0x35, 0x75]), // CSI 118;5 u ('v')
	CTRL_X: Buffer.from([0x1b, 0x5b, 0x31, 0x32, 0x30, 0x3b, 0x35, 0x75]), // CSI 120;5 u ('x')
	CTRL_Z: Buffer.from([0x1b, 0x5b, 0x31, 0x32, 0x32, 0x3b, 0x35, 0x75]), // CSI 122;5 u ('z')

	// Alt/Meta combinations (primarily for macOS)
	ALT_B: Buffer.from([0x1b, 0x5b, 0x39, 0x38, 0x3b, 0x33, 0x75]), // CSI 98;3 u ('b' with Alt)
	ALT_F: Buffer.from([0x1b, 0x5b, 0x31, 0x30, 0x32, 0x3b, 0x33, 0x75]), // CSI 102;3 u ('f' with Alt)
	ALT_D: Buffer.from([0x1b, 0x5b, 0x31, 0x30, 0x30, 0x3b, 0x33, 0x75]), // CSI 100;3 u ('d' with Alt)
	ALT_BACKSPACE: Buffer.from([0x1b, 0x5b, 0x31, 0x32, 0x37, 0x3b, 0x33, 0x75]), // CSI 127;3 u (Backspace with Alt)
	SHIFT_BACKSPACE: Buffer.from([0x1b, 0x5b, 0x31, 0x32, 0x37, 0x3b, 0x32, 0x75]), // CSI 127;2 u (Backspace with Shift)
	SHIFT_TAB: Buffer.from([0x1b, 0x5b, 0x39, 0x3b, 0x32, 0x75]), // CSI 9;2 u (Tab with Shift)

	// Special keys
	SHIFT_ENTER: Buffer.from([0x1b, 0x5b, 0x31, 0x33, 0x3b, 0x32, 0x75]), // CSI 13;2 u
	CTRL_ENTER: Buffer.from([0x1b, 0x5b, 0x31, 0x33, 0x3b, 0x35, 0x75]), // CSI 13;5 u
	PAGE_UP: Buffer.from([0x1b, 0x5b, 0x35, 0x75]), // CSI 5 u
	PAGE_DOWN: Buffer.from([0x1b, 0x5b, 0x36, 0x75]), // CSI 6 u
	HOME: Buffer.from([0x1b, 0x5b, 0x48, 0x75]), // CSI H u
	END: Buffer.from([0x1b, 0x5b, 0x46, 0x75]), // CSI F u
	DEL: Buffer.from([0x1b, 0x5b, 0x51, 0x75]), // CSI Q u (Delete key)

	enable() {
		// Enable Kitty keyboard protocol progressive enhancement for all terminals
		// CSI = 1 u - Enable disambiguate escape codes flag for Kitty keyboard protocol
		process.stderr.write('\x1b[=1u')
	},

	disable() {
		// Disable Kitty keyboard protocol enhancements

		// CSI = 0 u - Disable disambiguate escape codes flag for Kitty keyboard protocol
		// See: https://github.com/dankamongmen/notcurses/issues/2811
		process.stderr.write('\x1b[=0u')
		// CSI < u - Restore previous keyboard mode
		process.stderr.write('\x1b[<u')
	},
}
