import type {
	ThreadMeta,
	ThreadServerInfo,
	ThreadSyncRequest,
	ThreadSyncResponse,
} from '@sourcegraph/amp-api-types'
import {
	abortableOperation,
	BehaviorSubject,
	createSetSubject,
	debounceTime,
	filter,
	firstValueFrom,
	map,
	merge,
	subscriptionDisposable,
	switchMap,
	tap,
	type Observable,
} from '@sourcegraph/observable'
import logger from '../common/logger'
import { resolvedConfig } from '../configuration/resolver'
import { DEFAULT_AMP_URL_STRING, isDefaultAmpURL } from '../constants'
import { fetchFromAmpServer, isAmpServerConfigured } from '../server-api/client'
import type { Thread } from './thread'
import { type ThreadID } from './thread'
import { type ThreadHistoryService } from './thread-history-service'
import { type ThreadService } from './thread-service'

export type ThreadSyncInfo =
	| {
			status: 'unknown'
	  }
	| {
			status: 'synced'
			info: ThreadServerInfo | null
	  }

/**
 * Service that handles syncing threads between the client and the Amp server.
 */
export interface ThreadSyncService {
	/**
	 * Synchronize all threads with the server.
	 */
	sync(): Promise<void>

	/**
	 * Whether the initial sync is complete.
	 */
	initialSyncComplete: Promise<void>

	/**
	 * Update the sharing status of a thread
	 */
	updateThreadMeta(threadID: ThreadID, meta: ThreadMeta): Promise<void>

	/**
	 * Get the current sync info of one or more threads.
	 */
	threadSyncInfo(threadIDs: ThreadID[]): Observable<Record<ThreadID, ThreadSyncInfo>>

	/**
	 * Dispose the service and clean up resources.
	 */
	dispose(): void
}

/**
 * - The server is the source of truth for sharing status (shared, public, or private).
 */
export function createThreadSyncService({
	threadService,
	threadHistoryService,
	isExtensionDevelopment = false,
}: {
	threadService: ThreadService
	threadHistoryService: ThreadHistoryService
	isExtensionDevelopment?: boolean
}): ThreadSyncService {
	let isDisposed = false
	const disposables: { dispose(): void }[] = []

	/** Threads that need syncing. */
	const pendingSync = createSetSubject<ThreadID>()

	/** Map of thread ID to sync info. */
	const syncInfos = new BehaviorSubject<Map<ThreadID, ThreadSyncInfo>>(new Map())

	/** Whether we should sync threads. */
	const shouldSync = async (): Promise<boolean> => {
		// Don't sync if the service is disposed
		if (isDisposed) {
			return false
		}

		// Don't sync if the server isn't configured.
		if (!(await firstValueFrom(isAmpServerConfigured))) {
			return false
		}

		// Don't sync to default Amp URL in development mode to avoid polluting production.
		const config = await firstValueFrom(resolvedConfig)
		if (isExtensionDevelopment && isDefaultAmpURL(config.settings.url)) {
			logger.debug(
				`Skipping sync to ${DEFAULT_AMP_URL_STRING} in extension development mode to avoid polluting the production server`,
				{ name: 'ThreadSyncService' },
			)
			return false
		}

		return true
	}

	/** Upload a thread to the server. */
	const uploadThread = async (threadID: ThreadID): Promise<void> => {
		// Check for backoff first
		if (!shouldProcessThread(threadID)) {
			return
		}

		const thread = await firstValueFrom(threadService.observe(threadID))

		// Don't sync empty threads
		if (thread && !thread.messages.length) {
			return
		}

		if (!(await shouldSync())) {
			return
		}

		const abortController = new AbortController()
		disposables.push({
			dispose: () => abortController.abort(),
		})

		const resp = await fetchFromAmpServer(`/api/threads/${threadID}`, {
			method: 'POST',
			body: JSON.stringify(thread),
			signal: abortController.signal,
		})
		if (!resp.ok) {
			const responseText = await resp.text().catch(() => 'Unable to read response body')
			throw new Error(
				`Thread sync API (for thread ${threadID}) responded with status: ${resp.status}${responseText ? `: ${responseText}` : ''}`,
			)
		}
	}

	/** Map of thread IDs to their last failed sync time. */
	const lastFailedSync = new Map<ThreadID, number>()

	/** Backoff time in milliseconds before retrying a failed thread sync */
	const SYNC_RETRY_BACKOFF_MS = 60000 // 1 minute

	/** Check if a thread should be processed based on backoff */
	const shouldProcessThread = (threadID: ThreadID): boolean => {
		const lastFailed = lastFailedSync.get(threadID)
		if (!lastFailed) {
			return true
		}

		const elapsed = Date.now() - lastFailed
		const shouldProcess = elapsed >= SYNC_RETRY_BACKOFF_MS
		if (!shouldProcess) {
			logger.debug(
				`Skipping thread ${threadID} sync due to backoff (${Math.round(elapsed / 1000)}s elapsed of ${SYNC_RETRY_BACKOFF_MS / 1000}s backoff)`,
				{ name: 'ThreadSyncService' },
			)
		}

		return shouldProcess
	}

	/** Sync threads that have been queued for syncing. */
	const processSyncQueue = async (): Promise<void> => {
		for (const threadID of pendingSync.set) {
			// Skip threads that have recently failed (respect backoff)
			if (!shouldProcessThread(threadID)) {
				continue
			}

			try {
				await uploadThread(threadID)
				pendingSync.delete(threadID)
				lastFailedSync.delete(threadID)
			} catch (error) {
				lastFailedSync.set(threadID, Date.now())

				// Keep thread in pending sync queue to retry later with backoff.
				// Using 'as any' to bypass string literal constraint
				logger.error(`Error syncing thread ${threadID} (will retry after backoff)`, error, {
					name: 'ThreadSyncService',
				})
			}
		}
	}

	/** Initialize sync process by checking all threads against server. */
	const sync = async (specificThreadIDs?: ThreadID[], metaOnly = false): Promise<void> => {
		if (!(await shouldSync())) {
			return
		}

		type ThreadIDVersion = Pick<Thread, 'id' | 'v'>
		let threadsToSync: ThreadIDVersion[]
		if (specificThreadIDs) {
			threadsToSync = (
				await Promise.all(
					specificThreadIDs.map((id) =>
						firstValueFrom(
							threadService
								.observe(id)
								.pipe(map((t) => (t ? { id: t.id, v: t.v } : null))),
						),
					),
				)
			).filter((v) => v !== null)
		} else {
			// Update sync status for all threads in history *and* all currently open threads (which
			// might include empty threads that aren't yet stored in history).
			const threadHistory = await firstValueFrom(
				threadHistoryService
					.observeHistory()
					.pipe(
						map((threads) =>
							threads.map((t) => ({ id: t.id, v: t.v }) satisfies ThreadIDVersion),
						),
					),
			)
			const activeThreads = await firstValueFrom(
				threadService.exclusiveThreadsChanges.pipe(
					map((threads) =>
						Object.values(threads).map(
							(t) => ({ id: t.id, v: t.v }) satisfies ThreadIDVersion,
						),
					),
				),
			)
			threadsToSync = threadHistory.concat(activeThreads)
		}

		const threadSyncRequest: Required<ThreadSyncRequest> = {
			threadVersions: [],
			threadMetas: [],
		}
		const seen = new Set<ThreadID>()
		for (const thread of threadsToSync) {
			if (seen.has(thread.id)) {
				continue
			}
			seen.add(thread.id)

			if (!metaOnly) {
				threadSyncRequest.threadVersions.push({ id: thread.id, v: thread.v })
			}

			// Include our view of meta information if we have it.
			const syncInfo = syncInfos.getValue().get(thread.id)
			threadSyncRequest.threadMetas.push({
				id: thread.id,
				meta: syncInfo && syncInfo.status === 'synced' ? syncInfo.info?.meta : undefined,
			})
		}

		if (
			threadSyncRequest.threadVersions.length === 0 &&
			threadSyncRequest.threadMetas.length === 0
		) {
			return
		}

		// Tell server which threads we have and at what versions.
		if (!(await shouldSync())) {
			return
		}

		const abortController = new AbortController()
		disposables.push({
			dispose: () => abortController.abort(),
		})

		logger.debug('Sending threads sync request', { name: 'ThreadSyncService' })
		const resp = await fetchFromAmpServer('/api/threads/sync', {
			method: 'POST',
			body: JSON.stringify(threadSyncRequest),
			signal: abortController.signal,
		})
		if (!resp.ok) {
			const responseText = await resp.text().catch(() => 'Unable to read response body')
			logger.warn('Threads sync request failed', {
				name: 'ThreadSyncService',
				status: resp.status,
				text: responseText,
			})
			throw new Error(
				`Threads sync API responded with status: ${resp.status}${responseText ? `: ${responseText}` : ''}`,
			)
		}
		logger.debug('Got response for threads sync request', {
			name: 'ThreadSyncService',
			status: resp.status,
		})

		const syncResponse = (await resp.json()) as ThreadSyncResponse

		// Process sync actions from server.
		let threadsNeedingSync = 0
		for (const a of syncResponse.threadActions) {
			if (!('action' in a && typeof a.action === 'string')) {
				continue
			}
			switch (a.action) {
				case 'upload': {
					// Server needs an updated copy of this thread.
					pendingSync.add(a.id)
					threadsNeedingSync++
					break
				}
				case 'meta': {
					// Update our local meta information.
					syncInfos.getValue().set(a.id, { status: 'synced', info: { meta: a.meta } })
					break
				}
				default:
					continue
			}
		}

		// If any of the specificThreadIDs we queried are still 'unknown', we know we can mark them
		// as 'null' because otherwise the server would have returned info.
		if (specificThreadIDs) {
			for (const threadID of specificThreadIDs) {
				if (syncInfos.getValue().get(threadID)?.status === 'unknown') {
					syncInfos.getValue().set(threadID, { status: 'synced', info: null })
				}
			}
		}

		syncInfos.next(syncInfos.getValue())

		// Process the sync queue.
		await processSyncQueue()

		logger.debug(
			`Sync completed (${specificThreadIDs ? specificThreadIDs.length : 'all'} threads checked, ${threadsNeedingSync} threads needed sync)`,
			{ name: 'ThreadSyncService' },
		)
	}

	// TODO(sqs): We will need to monitor thread deletions as well and sync those. When we implement
	// better thread deletion syncing, we'll need to track deleted thread IDs and sync those in a
	// future implementation.

	// Run initial sync.
	const initialSyncComplete = sync().catch((error) =>
		logger.error(`Failed to perform initial sync:`, error, { name: 'ThreadSyncService' }),
	)

	// Periodically process the queue.
	disposables.push(
		subscriptionDisposable(
			pendingSync.observable
				.pipe(
					filter((threads) => threads.size > 0),
					debounceTime(3000), // Debounce to batch closely-timed changes
					abortableOperation(() =>
						processSyncQueue().catch((error) =>
							logger.error(`Error processing sync queue:`, error, {
								name: 'ThreadSyncService',
							}),
						),
					),
				)
				.subscribe({}),
		),
	)

	// Monitor for threads that change and enqueue them for syncing.
	disposables.push(
		subscriptionDisposable(
			threadService.exclusiveThreadsChanges
				.pipe(
					switchMap((threads) =>
						merge(
							...Array.from(threads.entries()).map(([, threadChanges]) =>
								threadChanges.pipe(tap((thread) => pendingSync.add(thread.id))),
							),
						),
					),
				)
				.subscribe({}),
		),
	)

	return {
		async sync(): Promise<void> {
			return sync()
		},

		initialSyncComplete,

		async updateThreadMeta(
			threadID: ThreadID,
			meta: Pick<ThreadMeta, 'private' | 'public'>,
		): Promise<void> {
			const initialValue = syncInfos.getValue().get(threadID)
			try {
				syncInfos.getValue().set(threadID, { status: 'synced', info: { meta } })
				syncInfos.next(syncInfos.getValue())

				const abortController = new AbortController()
				disposables.push({
					dispose: () => abortController.abort(),
				})

				const resp = await fetchFromAmpServer(`/api/threads/${threadID}/meta`, {
					method: 'PUT',
					body: JSON.stringify({ private: meta.private, public: meta.public }),
					signal: abortController.signal,
				})
				if (!resp.ok) {
					throw new Error(`Failed to update sharing status: ${resp.status}`)
				}
			} catch (error) {
				// Revert on error to avoid displaying the wrong value.
				if (initialValue) {
					syncInfos.getValue().set(threadID, initialValue)
				} else {
					syncInfos.getValue().delete(threadID)
				}
				throw error
			}
		},

		threadSyncInfo(threadIDs: ThreadID[]): Observable<Record<ThreadID, ThreadSyncInfo>> {
			for (const id of threadIDs) {
				if (!syncInfos.getValue().has(id)) {
					syncInfos.getValue().set(id, { status: 'unknown' })
				}
			}

			// Trigger a sync with the server for these specific threads to get their meta information
			sync(threadIDs, true).catch((error) =>
				logger.error(`Error syncing thread metadata:`, error, {
					name: 'ThreadSyncService',
				}),
			)

			return syncInfos.pipe(
				map((sharingMap) => {
					const result: Record<ThreadID, ThreadSyncInfo> = {}
					for (const threadID of threadIDs) {
						result[threadID] = sharingMap.get(threadID) ?? {
							status: 'synced',
							info: null,
						}
					}
					return result
				}),
			)
		},

		dispose(): void {
			if (isDisposed) {
				return
			}
			isDisposed = true
			for (const disposable of disposables) {
				disposable.dispose()
			}
		},
	}
}
