import type { MCPServerSpec } from '../mcp/service'
import type { Hook } from '../threads/hooks/schema'
import type { SettingsTarget } from './storage'

export interface Settings {
	url?: string
	'anthropic.thinking.enabled'?: boolean

	'notifications.enabled'?: boolean
	'todos.enabled'?: boolean
	mcpServers?: { [name: string]: WithTarget<MCPServerSpec> }
	'tools.disable'?: string[]
	'connections.disable'?: string[]
	'mcp.disable'?: string[]
	'commands.allowlist'?: string[]
	submitOnEnter?: boolean

	'terminal.commands.environment'?: 'vscode-terminal' | 'node-spawn'
	'terminal.commands.hide'?: boolean
	'terminal.commands.node-spawn.load-profile'?: 'always' | 'daily' | 'never'

	'tab.enabled'?: boolean
	'tab.verboseLogs'?: boolean
	debugLogs?: boolean

	hooks?: Hook[]

	mode?: never // TODO(sqs): remove after 2025-05-15
}

export type WithTarget<T> = T & { _target?: SettingsTarget | 'default' }

const MERGED_ARRAY_KEYS = ['commands.allowlist'] as const

export function isMergedArrayKey(key: string): key is (typeof MERGED_ARRAY_KEYS)[number] {
	return MERGED_ARRAY_KEYS.includes(key as (typeof MERGED_ARRAY_KEYS)[number])
}

export const SECRET_NAMES = ['apiKey'] as const
export type SecretName = (typeof SECRET_NAMES)[number]
