import { getGitRepositoryInfo } from '@sourcegraph/amp-core/src/threads/git-utils'
import {
	getThreadEnvPlatform,
	type ThreadEnvironment,
} from '@sourcegraph/amp-core/src/threads/thread-env'
import { URI } from 'vscode-uri'

/**
 * Generate environment information for threads executed in the CLI. Captures repository information
 * and environment details.
 */
export async function getThreadEnvironment(): Promise<ThreadEnvironment> {
	return {
		trees: await Promise.all([process.cwd()].map((dir) => getGitRepositoryInfo(URI.file(dir)))),
		platform: getThreadEnvPlatform(),
		interactive: true,
	}
}
