import { exec } from 'node:child_process'
import logger from '../common/logger'

export type NotificationSoundType = 'idle' | 'requires-user-input'

export function playNotificationSound(type: NotificationSoundType = 'idle') {
	try {
		if (type === 'idle') {
			// Try to play simple system beep if available
			if (process.platform === 'darwin') {
				// Use macOS system sound - Submarine is short and distinctive
				exec('afplay /System/Library/Sounds/Submarine.aiff')
			} else if (process.platform === 'win32') {
				// Use Windows console beep (through PowerShell)
				exec('powershell [console]::beep(800,200)')
			} else if (process.platform === 'linux') {
				// Try pulseaudio first, then fallback to beep command
				exec('paplay /usr/share/sounds/freedesktop/stereo/message.oga || beep')
			}
		} else if (type === 'requires-user-input') {
			// Play a sound for terminal command permission requests
			if (process.platform === 'darwin') {
				// Use macOS system sound - Ping is distinct from Submarine
				exec('afplay /System/Library/Sounds/Ping.aiff')
			} else if (process.platform === 'win32') {
				// Use Windows console beep with different pitch/duration
				exec('powershell [console]::beep(1000,300)')
			} else if (process.platform === 'linux') {
				// Try pulseaudio first with dialog-information sound, then fallback to beep command
				exec(
					'paplay /usr/share/sounds/freedesktop/stereo/dialog-information.oga || beep -f 1000 -l 100',
				)
			}
		}
	} catch (error) {
		logger.error(`Failed to play notification sound (${type}):`, error)
	}
}
