import { type Disposable } from 'vscode'

import { codebaseSearchAgentToolReg } from './builtin/codebase_search_agent/common'
import { getDiagnosticsToolReg } from './builtin/diagnostics/common'
import { createFileToolReg } from './builtin/filesystem/create_file.common'
import { editFileToolReg } from './builtin/filesystem/edit_file.common'
import { globToolReg } from './builtin/filesystem/glob.common'
import { listDirectoryToolReg } from './builtin/filesystem/list_directory.common'
import { readFileToolReg } from './builtin/filesystem/read_file.common'
import { undoEditToolReg } from './builtin/filesystem/undo_edit.common'
import { formatFileToolReg } from './builtin/format_file/common'
import { grepToolReg } from './builtin/grep/common'
import { mermaidToolReg } from './builtin/mermaid/common'
import { readWebPageToolReg } from './builtin/read_web_page/common'
import { taskToolReg } from './builtin/task/common'
import { todoReadToolReg, todoWriteToolReg } from './builtin/todo/common'
import { webSearchToolReg } from './builtin/web_search/common'

import { bashToolReg } from './builtin/bash/common'
import { runRoutineToolReg } from './builtin/run_routine/common'
import { thinkToolReg } from './builtin/think/common'
import type { ToolService } from './tool-service'

let registered = false

export async function registerBuiltinTools(
	toolService: ToolService,
	includeRoutines = false,
): Promise<Disposable> {
	if (registered) {
		return { dispose() {} }
	}
	registered = true
	return await registerBuiltinToolsToToolService(toolService, includeRoutines)
}

export async function registerBuiltinToolsToToolService(
	toolService: ToolService,
	includeRoutines = false,
): Promise<Disposable> {
	const disposables: Disposable[] = [
		toolService.registerTool(readWebPageToolReg),
		toolService.registerTool(codebaseSearchAgentToolReg),
		toolService.registerTool(taskToolReg),

		toolService.registerTool(listDirectoryToolReg),
		toolService.registerTool(grepToolReg),
		toolService.registerTool(bashToolReg),
		toolService.registerTool(getDiagnosticsToolReg),

		toolService.registerTool(globToolReg),
		toolService.registerTool(mermaidToolReg),
		toolService.registerTool(readFileToolReg),
		toolService.registerTool(createFileToolReg),
		toolService.registerTool(editFileToolReg),
		toolService.registerTool(undoEditToolReg),
		toolService.registerTool(thinkToolReg),
		toolService.registerTool(formatFileToolReg),
		toolService.registerTool(todoWriteToolReg),
		toolService.registerTool(todoReadToolReg),
		toolService.registerTool(webSearchToolReg),
	]
	if (includeRoutines) {
		disposables.push(toolService.registerTool(runRoutineToolReg))
	}

	return {
		dispose() {
			for (const d of disposables) {
				d.dispose()
			}
		},
	}
}
