import logger from '@sourcegraph/amp-core/src/common/logger'
import {
	AGENT_FILE_NAME,
	AGENT_FILE_GENERATE_PROMPT,
} from '@sourcegraph/amp-core/src/threads/system-prompt'
import type { ThreadWorker } from '@sourcegraph/amp-core/src/threads/thread-worker'
import { mkdir, readFile, writeFile, unlink } from 'node:fs/promises'
import path from 'node:path'

export async function checkAgentFileStatus(): Promise<{
	shouldGenerate: boolean
	migrated: boolean
}> {
	const agentFilePath = path.join(process.cwd(), AGENT_FILE_NAME)
	const oldMemoryFilePath = path.join(process.cwd(), '.sourcegraph', 'memory.md')

	let shouldGenerateNewAgentFile = true
	let migrated = false

	try {
		// Check if old memory file exists
		await readFile(oldMemoryFilePath, 'utf-8')
		// Old memory file exists. Now check if AGENT.md also exists.
		try {
			await readFile(agentFilePath, 'utf-8')
			// Both old and new files exist. AGENT.md already exists, so no generation needed.
			shouldGenerateNewAgentFile = false
		} catch (newPathError) {
			// AGENT.md doesn't exist, but old memory file does. Migrate it.
			try {
				await mkdir(path.dirname(agentFilePath), { recursive: true })
				const content = await readFile(oldMemoryFilePath, 'utf-8')
				await writeFile(agentFilePath, content, 'utf-8')
				// Remove the old file after successful migration
				await unlink(oldMemoryFilePath)
				// After migration, we skip generating a new file via prompt.
				shouldGenerateNewAgentFile = false
				migrated = true
			} catch (migrationError) {
				logger.error('Failed to migrate memory.md to AGENT.md', migrationError)
				// Continue with normal generation if migration fails
				shouldGenerateNewAgentFile = true
			}
		}
	} catch (oldPathError) {
		// Old memory file doesn't exist. Check if AGENT.md exists.
		try {
			await readFile(agentFilePath, 'utf-8')
			// AGENT.md exists, nothing to do
			shouldGenerateNewAgentFile = false
		} catch (error) {
			// Neither file exists, proceed to generate new AGENT.md
			shouldGenerateNewAgentFile = true
		}
	}

	return {
		shouldGenerate: shouldGenerateNewAgentFile,
		migrated,
	}
}

export async function generateAgentFile(worker: ThreadWorker): Promise<void> {
	await worker.handle({
		type: 'user:message',
		message: {
			content: [{ type: 'text', text: AGENT_FILE_GENERATE_PROMPT }],
		},
	})
}
