import type { StoredLastValue } from '@sourcegraph/observable'
import { map, shareReplay, storeLastValue, type Observable } from '@sourcegraph/observable'
import { observeAll, type Storage } from '../storage/common'
import type { ThreadEntry } from './thread'
import { type Thread, type ThreadID } from './thread'

/**
 * Service for managing thread history and deletion.
 */
export class ThreadHistoryService {
	constructor(
		private storage: Pick<
			Storage<Record<string, Thread>>,
			'delete' | 'get' | 'changes' | 'keys'
		>,
	) {
		this._observeHistory = storeLastValue(
			observeAll(this.storage).pipe(
				map((threads) => {
					const validThreads = Object.values(threads)
						// NOTE(sqs): remove old threads as of data model change 2025-02-27
						.filter((thread) => thread && 'messages' in thread)
						.toSorted((a, b) => b.created - a.created)

					const keepThreads: Thread[] = []
					for (const thread of validThreads) {
						const isEmpty = thread.messages.length === 0
						if (!isEmpty) {
							keepThreads.push(thread)
						}
					}

					return keepThreads
						.map(
							(thread) =>
								({
									id: thread.id,
									v: thread.v,
									created: thread.created,
									title: thread.title ?? null,
									userLastInteractedAt: threadUserLastInteractedAt(thread),
									env: thread.env,
									originThreadID: thread.originThreadID,
								}) satisfies ThreadEntry,
						)
						.toSorted((a, b) => b.userLastInteractedAt - a.userLastInteractedAt)
				}),
				shareReplay(),
			),
		)
	}

	/**
	 * Delete a thread.
	 */
	public async delete(id: ThreadID): Promise<void> {
		await this.storage.delete(id)
	}

	/**
	 * Observe the history of all threads.
	 */
	public observeHistory(): Observable<ThreadEntry[]> {
		return this._observeHistory.observable
	}

	private _observeHistory: StoredLastValue<ThreadEntry[]>

	public dispose(): void {
		this._observeHistory.subscription.unsubscribe()
	}
}

/**
 * The timestamp of the most recent user interaction with the thread.
 */
export function threadUserLastInteractedAt(thread: Pick<Thread, 'created' | 'messages'>): number {
	return Math.max(
		thread.created,
		...thread.messages
			.map((m) => (m.role === 'user' ? m.meta?.sentAt : undefined))
			.filter((v) => v !== undefined),
	)
}
