export const AMP_API_KEY_PREFIX = 'sgamp_'

/**
 * If you change this, also change vscode/package.json's default value.
 *
 * Include the trailing slash.
 */
export const DEFAULT_AMP_URL = new URL('https://ampcode.com/')

export const DEFAULT_AMP_URL_STRING = DEFAULT_AMP_URL.toString()

/**
 * The default Amp URL in development mode.
 */
export const DEV_AMP_URL_STRING = 'https://localhost:7002/'

/**
 * If you change this, also change vscode/package.json's default value.
 *
 * Include the trailing slash.
 */
export const DEFAULT_ANTHROPIC_API_URL = new URL('https://api.anthropic.com/')

export const DEFAULT_ANTHROPIC_API_URL_STRING = DEFAULT_ANTHROPIC_API_URL.toString()

/**
 * Reports whether URL is `https://ampcode.com/` or equivalent. Note that this always checks against
 * `https://ampcode.com/` and does not check against {@link DEV_AMP_URL_STRING} in dev mode.
 */
export function isDefaultAmpURL(url: string | URL): boolean {
	const urlStr = typeof url === 'string' ? url : url.toString()
	return urlStr === DEFAULT_AMP_URL_STRING || urlStr === DEFAULT_AMP_URL_STRING.slice(0, -1) // without trailing slash
}

export function normalizeAmpURL(url: string): string {
	return isDefaultAmpURL(url) ? DEFAULT_AMP_URL_STRING : url
}

/**
 * Whether {@link url} is a recognized and trusted Amp URL. The client MUST ask the user for
 * confirmation if something (such as a URI handler) tries to set the user's Amp URL to an untrusted
 * Amp URL.
 */
export function isTrustedAmpURL(url: string | URL): boolean {
	return (
		isDefaultAmpURL(url) ||
		[
			DEV_AMP_URL_STRING,
			DEV_AMP_URL_STRING.slice(0, -1), // without trailing slash
		].includes(url.toString())
	)
}
