import { firstValueFrom } from '@sourcegraph/observable'
import type { ConfigService } from '../configuration/service'
import type { FileSystem } from '../platform/fs/fs'
import type { SerializedFileMentions } from './file-mentions'
import type { ThreadUserMessage } from './thread'
import { combinedTextContent } from './thread'

export async function parseUserMessage(
	{
		configService,
		filesystem,
	}: {
		configService: Pick<ConfigService, 'workspaceRoots'>
		filesystem: FileSystem
	},
	message: Omit<ThreadUserMessage, 'role'>,
): Promise<ThreadUserMessage> {
	const workspaceRootUri = (await firstValueFrom(configService.workspaceRoots)).at(0) ?? null
	const workspaceRoot = workspaceRootUri?.fsPath
	if (!workspaceRoot) {
		return {
			role: 'user',
			content: message.content,
			source: message.source,
			fileMentions: undefined,
			userState: undefined,
		}
	}

	const fileMentions: SerializedFileMentions | undefined =
		import.meta.env.VITE_PLATFORM === 'node' || import.meta.env.VITE_PLATFORM === 'vscode'
			? (
					await import('./files').then(({ getFileMentions }) =>
						getFileMentions(
							combinedTextContent(message.content),
							workspaceRoot,
							filesystem,
						),
					)
				).serialize()
			: undefined

	const userState =
		import.meta.env.VITE_PLATFORM === 'vscode'
			? (await import('./user-state/vscode')).getUserState()
			: undefined

	return {
		role: 'user',
		content: message.content,
		source: message.source,
		fileMentions,
		userState,
	}
}
