import { firstValueFrom } from '@sourcegraph/observable'
import { resolvedConfig } from '../configuration/resolver'
import { anthropicClient } from '../inference/backends/anthropic'
import type { ThreadUserMessage } from './thread'
import { combinedTextContent } from './thread'

/**
 * Generate a title for a thread.
 */
export async function generateThreadTitle(
	firstMessage: ThreadUserMessage,
	signal: AbortSignal,
): Promise<string | undefined> {
	const text = combinedTextContent(firstMessage.content)
	if (!text) {
		return undefined
	}

	const config = await firstValueFrom(resolvedConfig, signal)
	const anthropic = await anthropicClient(config)

	const response = await anthropic.messages.create(
		{
			model: 'claude-3-5-haiku-20241022',
			max_tokens: 60,
			temperature: 0.7,
			system: `You are an assistant that generates short, descriptive titles (maximum 7 words, "Sentence case" with the first word capitalized not "Title Case") based on user's message to an agentic coding tool. Your titles should be concise (max 7 words) and capture the essence of the query or topic. DO NOT ASSUME OR GUESS the user's intent beyond what is in their message. Omit generic words like "question", "request", etc. Be professional and precise. Use common software engineering terms and acronyms if they are helpful. Use the set_title tool to provide your answer.`,
			messages: [
				{
					role: 'user',
					content: `<message>${text}</message>`,
				},
			],
			tools: [
				{
					name: 'set_title',
					input_schema: {
						type: 'object',
						properties: {
							title: {
								type: 'string',
								description:
									'The short thread title (maximum 7 words, "Sentence case" with the first word capitalized not "Title Case") that you generated for the message',
							},
						},
						required: ['title'],
					},
				},
			],
			tool_choice: {
				type: 'tool',
				name: 'set_title',
				disable_parallel_tool_use: true,
			},
		},
		{ stream: false, signal },
	)

	const toolUse = response.content.at(0)
	if (!toolUse) {
		throw new Error('no content in generateThreadTitle response')
	}
	if (toolUse.type !== 'tool_use' || toolUse.name !== 'set_title') {
		throw new Error('missing or invalid tool_use in generateThreadTitle response')
	}
	const title =
		toolUse.input &&
		typeof toolUse.input === 'object' &&
		'title' in toolUse.input &&
		typeof toolUse.input.title === 'string'
			? toolUse.input.title
			: undefined
	return title
}
