import type Anthropic from '@anthropic-ai/sdk'
import { partialParse } from '@anthropic-ai/sdk/_vendor/partial-json-parser/parser'
import logger from '../common/logger'
import type { ToolRun } from '../tools/tool-service'
import {
	findToolUseBlock,
	lastMessageOfRole,
	toolResultBlock,
	toolUseBlock,
	type Thread,
	type ThreadAssistantMessage,
	type ThreadID,
	type ThreadToolUseID,
	type ThreadUserMessage,
	type ToolRunUserInput,
} from './thread'
import { type ThreadEnvironment } from './thread-env'
import type { DebugUsage } from './thread-service'

/**
 * An atomic update to a thread.
 */
export type ThreadDelta =
	| {
			/** Send or update a user message. */
			type: 'user:message'

			/**
			 * If set, the index of the message in the thread to update. Otherwise, append the
			 * message to the end of the thread.
			 */
			index?: number | undefined

			/** The content of the message to send or update. */
			message: Omit<ThreadUserMessage, 'role'>

			// TODO!(sqs): support file mentions, etc.
	  }
	| {
			/**
			 * Provide user input to a tool, such as accepting a `Bash` invocation.
			 */
			type: 'user:tool-input'

			/** The ID of the tool use to provide input for. */
			toolUse: ThreadToolUseID

			/** The user input. */
			value: ToolRunUserInput
	  }
	| {
			/**
			 * Receive data (in-progress partial results or a terminal status and result) from a
			 * tool.
			 */
			type: 'tool:data'

			/** The ID of the tool use to receive data for. */
			toolUse: ThreadToolUseID

			/** The data from the tool invocation. */
			data: ToolRun
	  }
	| {
			/** Receive a message from the assistant. */
			type: 'assistant:message'

			/** The message from the assistant. */
			message: ThreadAssistantMessage
	  }
	| {
			/**
			 * Receive a partial message update from the assistant.
			 */
			type: 'assistant:message-delta'

			/** The update event from the assistant. */
			event: Anthropic.Messages.MessageStreamEvent

			inferenceStart?: number
	  }
	| {
			/**
			 * Signals that an inference operation has completed, with final usage information.
			 * This is emitted after all streaming events or a complete message response.
			 */
			type: 'inference:completed'

			/** The usage information from the inference operation. */
			usage?: DebugUsage | undefined

			/** The parameters used for the inference operation. */
			params?: unknown
	  }
	| {
			/**
			 * Signals that an inference operation or tool use has been cancelled by the user.
			 */
			type: 'cancelled'
	  }
	| {
			/** Set the title for the thread. */
			type: 'title'
			value: string | undefined
	  }
	| {
			/** Set the environment for the thread. */
			type: 'environment'
			env: { initial: ThreadEnvironment }
	  }
	| {
			type: 'summary:created'
			summary:
				| { type: 'external'; summaryThreadID: ThreadID }
				| { type: 'internal'; summary: string }
	  }

/**
 * Apply a {@link ThreadDelta} to a {@link Thread} and return the updated thread.
 */
export type VersionedThreadDelta = { v: number } & ThreadDelta

/**
 * Helper function to update the thread's debug information
 */
function updateThreadDebugInfo(
	thread: Thread,
	updates: { usage?: DebugUsage | undefined; params?: unknown },
): void {
	thread['~debug'] = {
		...(thread['~debug'] ?? {}),
		...(updates.usage ? { lastInferenceUsage: updates.usage } : {}),
		...(updates.params ? { lastInferenceInput: updates.params } : {}),
	}
}

/**
 * Apply a {@link ThreadDelta} to a {@link Thread} and return the updated thread.
 */
export function applyThreadDelta(threadInput: Thread, delta: ThreadDelta): Thread {
	// Create a copy of the thread to update
	const thread = JSON.parse(JSON.stringify(threadInput)) as Thread
	thread.v++

	switch (delta.type) {
		case 'cancelled': {
			markPriorStreamingAssistantMessageAsCancelled(thread)
			const lastMessage = thread.messages.at(-1)
			if (lastMessage?.role === 'user') {
				// Without cancelling the last tool_result on this user message,
				// the extension will auto-resume the agent for this thread if
				// the user restarts the extension.
				const lastToolResult = lastMessage.content.findLast(
					(block) => block.type === 'tool_result',
				)
				if (lastToolResult) {
					lastToolResult.run.status = 'cancelled'
				}
			}
			break
		}

		case 'summary:created': {
			if (delta.summary.type === 'external') {
				if (!thread.summaryThreads) {
					thread.summaryThreads = []
				}
				thread.summaryThreads.push(delta.summary.summaryThreadID)
				thread.messages.push({
					role: 'info',
					content: [
						{
							type: 'summary',
							summary: {
								type: 'thread',
								thread: delta.summary.summaryThreadID,
							},
							usageBefore: thread['~debug']?.lastInferenceUsage,
						},
					],
				})
			} else if (delta.summary.type === 'internal') {
				const usageBefore = thread['~debug']?.lastInferenceUsage
				thread['~debug'] = undefined
				thread.messages.push({
					role: 'info',
					content: [
						{
							type: 'summary',
							summary: {
								type: 'message',
								summary: delta.summary.summary,
							},
							usageBefore,
						},
					],
				})
			}
			break
		}

		case 'user:message': {
			markPriorStreamingAssistantMessageAsCancelled(thread)

			const message: ThreadUserMessage = {
				role: 'user',
				...delta.message,
			}
			if (delta.index !== undefined) {
				// Edit an existing user message.
				if (!thread.messages[delta.index]) {
					throw new Error(`user message at index ${delta.index} not found`)
				}
				// Delete all messages after the edited message, because they are no longer valid.
				thread.messages.splice(delta.index, thread.messages.length - delta.index, message)
			} else {
				// Append a new user message.
				thread.messages.push(message)
			}
			break
		}

		case 'user:tool-input': {
			// Ensure that there are corresponding tool_use and tool_result blocks.
			const toolUseBlk = toolUseBlock(thread, delta.toolUse)
			if (!toolUseBlk) {
				logger.debug(
					`Ignoring user:tool-input delta for missing tool use ${delta.toolUse} (likely deleted due to thread edit/truncation)`,
				)
				break
			}

			const block = toolResultBlock(thread, delta.toolUse)
			if (!block) {
				logger.debug(
					`Ignoring user:tool-input delta for missing tool result block ${delta.toolUse} (likely deleted due to thread edit/truncation)`,
				)
				break
			}
			block.userInput = delta.value
			break
		}

		case 'tool:data': {
			// Ensure that there is a corresponding tool_use block.
			const toolUse = findToolUseBlock(thread, delta.toolUse)
			if (!toolUse) {
				logger.debug(
					`Ignoring tool:data delta for missing tool use ${delta.toolUse} (likely deleted due to thread edit/truncation)`,
				)
				break
			}

			addToolData(thread, delta.toolUse, delta.data)
			break
		}

		case 'assistant:message': {
			markPriorStreamingAssistantMessageAsCancelled(thread)

			thread.messages.push(delta.message)
			break
		}

		case 'assistant:message-delta': {
			const event = delta.event

			// Handle message_start separately because it's the only one where the lastMsg is a user message
			const lastMsg = thread.messages.at(-1)
			if (event.type === 'message_start') {
				// We're creating a new assistant message. It's not possible to have 2 assistant
				// messages in a row, so ensure that the last message is from the user.
				if (!lastMsg || lastMsg.role !== 'user') {
					throw new Error('(unreachable) assistant message start without user message')
				}

				markPriorStreamingAssistantMessageAsCancelled(thread)

				thread.messages = [
					...thread.messages,
					{ role: 'assistant', content: [], state: { type: 'streaming' } },
				]
				return thread
			}

			// At this point, the last message might be a user message with partial tool_results
			// because we can invoke tool_uses while they're streaming in before the entire
			// assistant message finishes.
			const asstMsg = lastMessageOfRole(thread, 'assistant')
			if (!asstMsg) {
				// If this is reached, it means the message is not an assistant message, which is a
				// bug. The inference operation should have been aborted before it could have sent
				// this ThreadUpdate.
				throw new Error('(unreachable) no last assistant message to update')
			}

			switch (event.type) {
				case 'content_block_start': {
					if (event.index !== asstMsg.content.length) {
						throw new Error('(bug) content block delta out of order')
					}

					let contentBlock: ThreadAssistantMessage['content'][number]
					switch (event.content_block.type) {
						case 'text':
							contentBlock = { type: 'text', text: '' }
							break
						case 'thinking':
							contentBlock = { type: 'thinking', thinking: '', signature: '' }
							break
						case 'redacted_thinking':
							contentBlock = {
								type: 'redacted_thinking',
								data: event.content_block.data,
							}
							break
						case 'tool_use':
							contentBlock = {
								type: 'tool_use',
								id: event.content_block.id as ThreadToolUseID,
								name: event.content_block.name,
								input: event.content_block.input as Record<string, unknown>,
								inputPartialJSON: { json: '' },
							}
							break
						default:
							throw new Error(
								`unsupported content block type: ${event.content_block.type}`,
							)
					}

					asstMsg.content.push(contentBlock)
					break
				}
				case 'content_block_delta': {
					const block = asstMsg.content[event.index]
					if (!block) {
						throw new Error('(bug) content block delta index out of bounds')
					}

					switch (event.delta.type) {
						case 'text_delta': {
							if (block.type !== 'text') {
								throw new Error(
									`(bug) content block delta type mismatch: text_delta !== ${block.type}`,
								)
							}
							block.text += event.delta.text
							break
						}
						case 'input_json_delta': {
							if (block.type !== 'tool_use') {
								throw new Error(
									`(bug) content block delta type mismatch: input_json_delta !== ${block.type}`,
								)
							}
							if (!block.inputPartialJSON) {
								throw new Error('(bug) input_json_delta without incomplete')
							}
							block.inputPartialJSON.json += event.delta.partial_json
							try {
								block.input = partialParse(block.inputPartialJSON.json) as any
							} catch {
								block.input = {}
								/* noop */
							}
							break
						}
						case 'thinking_delta': {
							if (block.type !== 'thinking') {
								throw new Error(
									`(bug) content block delta type mismatch: thinking_delta !== ${block.type}`,
								)
							}
							block.thinking += event.delta.thinking
							break
						}
						case 'signature_delta': {
							if (block.type !== 'thinking') {
								throw new Error(
									`(bug) content block delta type mismatch: signature_delta !== ${block.type}`,
								)
							}
							block.signature = event.delta.signature
							break
						}
					}
					break
				}
				case 'content_block_stop': {
					const block = asstMsg.content[event.index]
					if (!block) {
						throw new Error('(bug) content block delta index out of bounds')
					}
					if (block.type === 'tool_use') {
						if (!block.inputPartialJSON) {
							throw new Error('(bug) content block stop without incomplete')
						}
						if (block.inputPartialJSON.json === '') {
							block.input = {}
						} else {
							block.input = JSON.parse(block.inputPartialJSON.json)
						}
						// @ts-expect-error: change the type union of this ToolUseBlock
						delete block.inputPartialJSON
					}
					break
				}
				case 'message_delta': {
					// Handle edge case where message_delta arrives before all
					// content_block_stop events.
					if (event.delta.stop_reason) {
						// If we ran into `max_tokens` as the stop reason, it's
						// possible that we didn't receive a `content_block_stop`.
						// In that case, we're going to clean up the partial results
						// and return an error message to the model.
						if (event.delta.stop_reason === 'max_tokens') {
							// Check for any tool_use blocks with inputPartialJSON
							for (const block of asstMsg.content) {
								if (block.type === 'tool_use' && block.inputPartialJSON) {
									// @ts-expect-error: change the type union of this ToolUseBlock
									delete block.inputPartialJSON

									addToolData(thread, block.id, {
										status: 'error',
										error: {
											message:
												'Ran into max tokens limit of 200 tokens. Received only partial input for tool.',
										},
									})
								}
							}
						} else {
							// This is the old code where we didn't know that max_tokens was the issue.
							// We leave it in here in case there is _another_ case with malformed json.
							for (const block of asstMsg.content) {
								if (block.type === 'tool_use' && block.inputPartialJSON) {
									logger.debug(
										`Found a block with inputPartialJSON still set on message_delta`,
									)
									// Try to parse the partial JSON
									try {
										if (block.inputPartialJSON.json !== '') {
											block.input = JSON.parse(block.inputPartialJSON.json)
										} else {
											block.input = {}
										}
									} catch (e) {
										logger.error(
											`Failed to parse malformed JSON for tool_use ${block.id}`,
											e,
											{
												threadId: threadInput.id,
												blockId: block.id,
												input: block.inputPartialJSON.json,
											},
										)
										const errorMessage = `Malformed JSON: '${block.inputPartialJSON.json}'.`
										addToolData(thread, block.id, {
											status: 'error',
											error: {
												message: errorMessage, // Pass enhanced error message to tool result
											},
										})
									}
									// @ts-expect-error: change the type union of this ToolUseBlock
									delete block.inputPartialJSON
								}
							}
						}

						asstMsg.state = { type: 'complete', stopReason: event.delta.stop_reason }
					}
					break
				}
				case 'message_stop': {
					// noop
					break
				}
			}
			break
		}

		case 'inference:completed': {
			updateThreadDebugInfo(thread, { usage: delta.usage, params: delta.params })
			break
		}

		case 'title': {
			thread.title = delta.value || undefined
			break
		}

		case 'environment': {
			thread.env = delta.env
			break
		}
	}

	return thread
}

function markPriorStreamingAssistantMessageAsCancelled(
	thread: Pick<Thread, 'id' | 'messages'>,
): void {
	const lastAsstMsg = lastMessageOfRole(thread, 'assistant')
	if (!lastAsstMsg) return

	let shouldCleanup = false
	// Check if the message is still streaming
	if (lastAsstMsg.state.type === 'streaming') {
		lastAsstMsg.state = { type: 'cancelled' }
		shouldCleanup = true // Streaming message was cancelled, cleanup partial data
	}
	// Also check for inconsistent state: message is complete but has incomplete tool_use blocks
	else if (lastAsstMsg.state.type === 'complete') {
		const hasIncompleteToolUse = lastAsstMsg.content.some(
			(block) => block.type === 'tool_use' && 'inputPartialJSON' in block,
		)
		if (hasIncompleteToolUse) {
			lastAsstMsg.state = { type: 'cancelled' }
			shouldCleanup = true // Mark as cancelled and cleanup partial data
		}
	}
	// If we marked the message as cancelled, iterate and cleanup partial tool blocks
	if (shouldCleanup) {
		for (const block of lastAsstMsg.content) {
			if (block.type === 'tool_use' && block.inputPartialJSON) {
				logger.debug(`Cleaning up partial tool_use ${block.id}`, {
					name: 'markPriorStreamingAssistantMessageAsCancelled',
					threadID: thread.id,
					blockID: block.id,
				})
				try {
					// Attempt to parse the partial JSON, maybe it's usable
					if (block.inputPartialJSON.json !== '') {
						block.input = partialParse(block.inputPartialJSON.json) as any
					} else {
						block.input = {} // Default to empty object if no partial JSON
					}
				} catch (e) {
					logger.warn(
						`Failed to parse partial JSON for cancelled tool_use ${block.id}:`,
						e,
						{
							name: 'markPriorStreamingAssistantMessageAsCancelled',
							threadID: thread.id,
							blockID: block.id,
							input: block.inputPartialJSON.json,
						},
					)
					// Fallback to empty object on error
					block.input = {}
				}
				// CRITICAL: Remove the partial state indicator
				// @ts-expect-error: We are intentionally changing the block's structure
				delete block.inputPartialJSON
			}
		}
	}
}

function addToolData(thread: Pick<Thread, 'messages'>, toolUseID: ThreadToolUseID, data: ToolRun) {
	// Create or reuse existing tool_result block and user message. We need to keep
	// tool_result blocks together in the same user message for the prior assistant
	// message's tool_uses.
	const lastMsg = thread.messages.at(-1)
	let msg: ThreadUserMessage
	if (lastMsg && lastMsg.role === 'user') {
		msg = lastMsg
	} else {
		msg = { role: 'user', content: [] }
		thread.messages.push(msg)
	}

	// Use same ordering of tool_results as for their corresponding tool_uses.
	let resultBlock = toolResultBlock(thread, toolUseID)
	if (resultBlock) {
		resultBlock.run = data
	} else {
		resultBlock = {
			type: 'tool_result',
			toolUseID: toolUseID,
			run: data,
		}
		msg.content.push(resultBlock)
	}
}
