import type { Settings } from '@sourcegraph/amp-core/src/configuration/settings'
import { DEFAULT_SETTINGS_FILE } from './settings'

type DocumentedSettings = {
	[K in keyof Omit<Settings, 'mode'>]-?: {
		value: Settings[K]
		visible: boolean
		description: string
	}
}

