const sampleSettingsForDisplay = Object.fromEntries(
	Object.entries(documentedSettings)
		.filter(([, setting]) => setting.visible)
		.map(([key, setting]) => [`amp.${key}`, setting.value]),
)

export const helpContent = {
	examples: [
		{
			description: 'Start an interactive session:',
			command: 'amp',
		},
		{
			description: 'Run a command in a non-interactive session:',
			command: 'echo "commit all my unstaged changes" | amp',
		},
		{
			description:
				'Run from a prompt file in a non-interactive session and store output in a file:',
			command: 'amp < prompt.txt > output.txt',
		},
	],
	configuration: {
		description: `Amp can be configured using a JSON settings file located at ${DEFAULT_SETTINGS_FILE}. All settings use the "amp." prefix.`,
		sampleConfig: JSON.stringify(sampleSettingsForDisplay, null, 2),
		keyDescriptions: Object.entries(documentedSettings)
			.filter(([, setting]) => setting.visible)
			.map(([key, setting]) => ({
				key: `amp.${key}`,
				description: setting.description,
			})),
	},
}
