export type ThreadWorkerState = 'initial' | 'active'

export type InferenceState = 'idle' | 'running' | 'cancelled'

export interface FileChange {
	path: string
	created: boolean
	reverted?: boolean
	diffStat: {
		added: number
		removed: number
		modified: number
	}
}

export interface FileChanges {
	files: FileChange[]
}

export type ThreadWorkerStatus =
	| { state: Exclude<ThreadWorkerState, 'active'> }
	| {
			state: Extract<ThreadWorkerState, 'active'>
			inferenceState: InferenceState
			fileChanges: FileChanges
			todos?: string | undefined
			ephemeralError?:
				| { message: string; stack?: string | undefined; error?: any }
				| undefined
	  }

export interface ThreadWorkerDeps {
	configService: Pick<ConfigService, 'workspaceRoots'>
	threadService: Pick<ThreadService, 'exclusiveSyncReadWriter'>
	toolService: Pick<ToolService, 'tools' | 'invokeTool'>
	getThreadEnvironment: () => Promise<ThreadEnvironment>
	systemPromptService: SystemPromptService
	threadSummaryService: ThreadSummaryService
	createFileSystem: CreateFileSystem
}

const _latestConfig = storeLastValue(resolvedConfig)

async function latestConfig(): Promise<ResolvedConfiguration> {
	await firstValueFrom(_latestConfig.observable)
	return _latestConfig.value.last!
}

/**
 * The worker that handles all updates and agentic behavior on a thread.
 *
 * Each thread can have at most 1 worker that is active.
 */
export class ThreadWorker {
	/**
	 * Get or create the single {@link ThreadWorker} for the given thread.
	 */
	static async getOrCreateForThread(
		deps: ThreadWorkerDeps,
		threadID: ThreadID,
	): Promise<ThreadWorker> {
		let agent = this.threadWorkers.get(threadID)
		if (!agent) {
			agent = new ThreadWorker(deps, threadID)
			this.threadWorkers.set(threadID, agent)
		}
		return agent
	}
	private static threadWorkers = createMapSubject<ThreadID, ThreadWorker>()

	static get statuses(): Observable<Record<ThreadID, ThreadViewState>> {
		return this.threadWorkers.observable.pipe(
			switchMap((workers) =>
				workers.size === 0
					? Observable.of({})
					: combineLatest(
							...Array.from(workers.values()).map((worker) =>
								worker.status.pipe(
									map(
										(status) =>
											[
												worker.threadID,
												worker.threadReadWriter
													? threadViewState(worker.thread, status)
													: undefined,
											] as const,
									),
								),
							),
						).pipe(map((entries) => Object.fromEntries(entries))),
			),
		)
	}

	public static prettyPrintToolRun(
		threadID: ThreadID,
		toolUse: ThreadToolUseID,
		params: PrettyPrintToolRunParams,
	): string {
		const worker = this.threadWorkers.get(threadID)
		for (const message of worker?.thread.messages ?? []) {
			for (const content of message.content) {
				if (content.type === 'tool_result' && content.toolUseID === toolUse) {
					return prettyPrintToolRun(content.run, params)
				}
			}
		}
		throw new Error(`Tool run not found for thread ${threadID} and tool use ${toolUse}`)
	}

	static async cancelToolRun(threadID: ThreadID, toolUse: ThreadToolUseID): Promise<void> {
		const worker = this.threadWorkers.get(threadID)
		await worker?.findAndCancelToolRun(toolUse)
	}

	static async detachToolRun(threadID: ThreadID, toolUse: ThreadToolUseID): Promise<void> {
		const worker = this.threadWorkers.get(threadID)
		await worker?.detachToolRun(toolUse)
	}

	static async cancel(threadID: ThreadID): Promise<void> {
		const worker = this.threadWorkers.get(threadID)
		await worker?.cancel()
	}

	static dispose(threadID: ThreadID): void {
		const worker = this.threadWorkers.get(threadID)
		worker?.dispose()
	}

	static async retry(threadID: ThreadID): Promise<void> {
		const worker = this.threadWorkers.get(threadID)
		if (!worker) {
			throw new Error(`No active worker for thread ${threadID}`)
		}
		await worker.retry()
	}

	/**
	 * Reverts all file changes made in this thread by restoring original file content.
	 * For newly created files, they will be deleted.
	 */
	static async revertFileChanges(threadID: ThreadID, filePath?: string): Promise<void> {
		const worker = this.threadWorkers.get(threadID)
		if (!worker) {
			throw new Error(`No active worker for thread ${threadID}`)
		}
		await worker.revertFileChanges(filePath)
	}

	/**
	 * Cleans up backup files for a thread. This should be called when a thread is deleted.
	 */
	static async cleanupThreadBackups(deps: ThreadWorkerDeps, threadID: ThreadID): Promise<void> {
		const worker = this.threadWorkers.get(threadID)
		if (worker) {
			await worker.filesystem.cleanupBackupFiles()
		} else {
			const filesystem = deps.createFileSystem(threadID)
			try {
				await filesystem.cleanupBackupFiles()
				logger.debug(`Cleaned up backup files for thread ${threadID}`, { threadID })
			} catch (error) {
				logger.error('Error cleaning up thread backups', error, { threadID })
			}
		}
	}

	/**
	 * Checks for any in-progress tool results in the thread and re-invokes the tools
	 * if they don't have a corresponding operation in this.ops.tools.
	 * This happens when the ThreadWorker is disposed in the middle of an operation.
	 */
	private async resumeInProgressTools(): Promise<void> {
		for (const message of this.thread.messages) {
			if (message.role !== 'user') continue

			for (const block of message.content) {
				if (block.type !== 'tool_result') continue

				// An in-progress tool must have a matching ope
				const needsResuming =
					block.run.status === 'in-progress' && !this.ops.tools[block.toolUseID]
				if (!needsResuming) {
					continue
				}

				const toolUseBlock = this.findToolUseById(block.toolUseID)
				if (!toolUseBlock) {
					continue
				}

				const shouldCancel =
					toolUseBlock.name === BASH_TOOL_NAME || toolUseBlock.name === OLD_BASH_TOOL_NAME
				if (shouldCancel) {
					// It's outright dangerous to auto-resume a terminal
					// command. We had a bug report where Amp got VS Code into a
					// doom loop where it killed the VS Code extension host
					// while loading the last thread because it had an
					// in-progress command `pkill node`.
					this.findAndCancelToolRun(block.toolUseID)
					continue
				}

				logger.debug(`re-invoking tool ${toolUseBlock.name} with ID ${block.toolUseID}`, {
					name: 'resumeInProgressTools',
					threadID: this.threadID,
				})
				this.invokeTool(toolUseBlock, block.userInput)
			}
		}

		await this.updateFileChanges()
	}

	/**
	 * Find a specific tool use block by ID
	 */
	private findToolUseById(toolUseID: ThreadToolUseID): ToolUseBlock | undefined {
		for (const message of this.thread.messages) {
			if (message.role !== 'assistant') continue

			for (const block of message.content) {
				if (block.type === 'tool_use' && block.id === toolUseID) {
					return block
				}
			}
		}
		return undefined
	}

	/**
	 * Scan thread history and track all file mentions and tool result files
	 */
	private trackFilesFromHistory(): void {
		logger.debug('Tracking files from thread history', {
			name: 'trackFilesFromHistory',
			threadID: this.threadID,
		})
		for (const message of this.thread.messages) {
			// Track file mentions in user messages
			if (message.role === 'user' && message.fileMentions?.files) {
				for (const file of message.fileMentions.files) {
					this.trackFile(file.path)
				}
			}

			// Track files from tool results in user messages
			if (message.role === 'user') {
				for (const block of message.content) {
					if (block.type === 'tool_result' && block.run.status === 'done') {
						for (const file of block.run.files ?? []) {
							this.trackFile(file)
						}
					}
				}
			}
		}
	}

	/**
	 * Dispose the ThreadWorker for a specific thread ID.
	 */
	static disposeForThread(threadID: ThreadID): void {
		const worker = this.threadWorkers.get(threadID)
		if (worker) {
			worker.dispose()
			this.threadWorkers.delete(threadID)
		}
	}

	/**
	 * The operations that we can spawn. Any other future kind of operation we perform should have
	 * an {@link AbortController} recorded here.
	 */
	private ops: {
		tools: Record<ThreadToolUseID, AbortController>
		toolMessages: Record<ThreadToolUseID, SubscriptionObserver<ToolMessage>>
		inference: AbortController | null
	} = {
		tools: {},
		toolMessages: {},
		inference: null,
	}

	private _state = new BehaviorSubject<ThreadWorkerState>('initial')
	public readonly state = this._state.pipe(distinctUntilChanged(), shareReplay())

	/** Promise queue to serialize calls to handle() */
	private handlePromise = Promise.resolve()

	private ephemeralError = new BehaviorSubject<Error | undefined>(undefined)
	private inferenceState = new BehaviorSubject<InferenceState>('idle')
	private fileChanges = new BehaviorSubject<FileChanges>({ files: [] })

	private trackedFiles = new Set<string>()

	private fileChangeRecords = new Map<ThreadToolUseID, Map<string, FileChangeRecord>>()
	private createFileTracker: CreateToolUseFileTracker = NOOP_CREATE_TOOL_USE_CHANGE_TRACKER
	private filesystem: FileSystem

	constructor(
		private deps: ThreadWorkerDeps,
		public readonly threadID: ThreadID,
	) {
		this.filesystem = deps.createFileSystem(this.threadID)
		this.createFileTracker = (params: CreateToolUseFileTrackerParams): ToolUseFileTracker =>
			createFileChangeTracker({
				...params,
				filesystem: this.filesystem,
				threadID: this.threadID,
			})
	}

	public readonly status: Observable<ThreadWorkerStatus> = this.state.pipe(
		switchMap((state) =>
			state === 'active'
				? combineLatest(this.inferenceState, this.fileChanges, this.ephemeralError).pipe(
						map(
							([inferenceState, fileChanges, ephemeralError]) =>
								({
									state,
									inferenceState,
									fileChanges: fileChanges,
									ephemeralError: ephemeralError
										? {
												message: ephemeralError.message,
												stack:
													'stack' in ephemeralError
														? ephemeralError.stack
														: undefined,
												error:
													'error' in ephemeralError &&
													ephemeralError.error &&
													typeof ephemeralError.error === 'object' &&
													'error' in ephemeralError.error
														? ephemeralError.error.error
														: undefined,
											}
										: undefined,
								}) satisfies ThreadWorkerStatus,
						),
					)
				: Observable.of<ThreadWorkerStatus>({ state }),
		),
	)

	// The ThreadWorker takes an exclusive synchronous read-writer handle for the thread. This is
	// because it needs to be able to write synchronously to it (to avoid race conditions and the
	// need for transactional reads/writes) and because the ThreadWorker assumes it's the only
	// writer. Other code can observe the thread's updates asynchronously.
	private threadReadWriter: (ThreadExclusiveSyncReadWriter & { dispose: () => void }) | null =
		null
	private get thread(): Thread {
		if (!this.threadReadWriter) {
			throw new Error(`thread read-writer not initialized for ThreadWorker: ${this.threadID}`)
		}
		return this.threadReadWriter.read()
	}
	private set thread(updated: Thread) {
		if (!this.threadReadWriter) {
			throw new Error(`thread read-writer not initialized for ThreadWorker: ${this.threadID}`)
		}
		this.threadReadWriter.write(updated)
		if (import.meta.env.VITEST) {
			this.__testing__setThread(updated)
		}
	}
	private async acquireThread(): Promise<void> {
		if (!this.threadReadWriter) {
			this.threadReadWriter = await this.deps.threadService.exclusiveSyncReadWriter(
				this.threadID,
			)
			this._state.next('active')
		}
	}

	/** For testing only. */
	public __testing__setThread(_updated: Thread): void {}

	/**
	 * Resume the worker, figuring out what needs to be done based on the (possibly
	 * incomplete/in-progress/invalid) state of the thread.
	 */
	async resume(): Promise<void> {
		if (this.resumed) {
			return
		}
		this.resumed = true

		if (this.handleCalled) {
			throw new Error('cannot call ThreadWorker.resume after ThreadWorker.handle')
		}

		await this.acquireThread()

		await this.restoreFileChangesFromBackups()

		// Track all files from history when loading a thread
		this.trackFilesFromHistory()

		// If we the last message was cancelled or it's an info message, don't resume any tool calls or anything.
		const lastMsg = this.thread.messages.at(-1)
		if (isCancelledThreadMessage(lastMsg) || isInfoMessage(lastMsg)) {
			this.inferenceState.next('cancelled')
			return
		}

		// Check for any in-progress tool results that don't have a corresponding operation
		await this.resumeInProgressTools()

		// Replay the last complete message.
		const lastCompleteMsgIndex = this.thread.messages.findLastIndex((m) =>
			m.role === 'user'
				? isUserMessageCommitted(m)
				: m.role === 'assistant' && m.state.type === 'complete',
		)
		if (lastCompleteMsgIndex === -1) {
			if (this.thread.messages.length !== 0) {
				throw new Error(`(bug) invalid thread: ${this.threadID}`)
			}
			return // nothing to resume
		}

		const lastCompleteMsg = this.thread.messages[lastCompleteMsgIndex]!
		switch (lastCompleteMsg.role) {
			case 'user':
				this.onThreadDelta({ type: 'user:message', message: lastCompleteMsg })
				break
			case 'assistant':
				this.onThreadDelta({ type: 'assistant:message', message: lastCompleteMsg })
				break
		}
	}
	private resumed = false

	public async summarizeThread(): Promise<void> {
		await this.acquireThread()
		await this.cancel() // Ensures all ongoing operations are stopped and thread state is updated.

		const summary = await this.deps.threadSummaryService.summarizeThread(this.threadID)
		// The handle method is internally serialized, ensuring this is processed in order.
		await this.handle({
			type: 'summary:created',
			summary: { type: 'internal', summary },
		})
	}

	public async createSummaryThread(): Promise<ThreadID> {
		await this.acquireThread()
		await this.cancel() // Ensures all ongoing operations are stopped.

		const summaryThreadID = await this.deps.threadSummaryService.createSummaryThread(
			this.threadID,
		)
		await this.handle({
			type: 'summary:created',
			summary: { type: 'external', summaryThreadID },
		})
		return summaryThreadID
	}

	/**
	 * Handle a thread {@link delta}, updating the thread and reacting to it.
	 */
	async handle(delta: ThreadDelta, signal?: AbortSignal): Promise<void> {
		this.handlePromise = this.handlePromise
			.then(async () => {
				await this.innerHandle(delta, signal)
			})
			.catch((err) => {
				// Catch errors from the promise chain logic itself to prevent deadlocks
				// This might catch errors from acquireThread, resume, etc. if they happen within .then()
				logger.error('Unhandled error in handle promise queue', err, {
					threadID: this.threadID,
				})
			})

		await this.handlePromise
	}

	private async innerHandle(delta: ThreadDelta, signal?: AbortSignal): Promise<void> {
		// Check for disposal or abortion *before* starting work inside the queue
		if (this.isDisposed) {
			logger.debug(`Skipping ${delta.type} - worker disposed.`, {
				name: 'handle queue',
				threadID: this.threadID,
			})
			return
		}
		if (signal?.aborted) {
			logger.debug(`Skipping ${delta.type} - signal aborted.`, {
				name: 'handle queue',
				threadID: this.threadID,
			})
			return
		}

		await this.resume()
		this.handleCalled = true
		await this.acquireThread()

		// Special handling for the user:message event, which requires async pre-parsing.
		if (delta.type === 'user:message') {
			if (delta.index !== undefined) {
				// If the user edited a message, we need to revert all file changes that
				// were caused by it and subsequent messages. We need to do this here,
				// before we update the thread.
				await this.revertPriorFileChanges(delta.index)
			}
			delta.message = await parseUserMessage(
				{ configService: this.deps.configService, filesystem: this.filesystem },
				delta.message,
			)

			// Add any image file attachments to the message content
			if (delta.message.fileMentions?.imageBlocks?.length) {
				logger.debug(
					`Adding ${delta.message.fileMentions.imageBlocks.length} image attachments to message`,
					{
						name: 'adding image attachments',
						threadID: this.threadID,
					},
				)
				// Add the image blocks to the message content
				delta.message.content = [
					...delta.message.content,
					...delta.message.fileMentions.imageBlocks,
				]
			}

			if (!delta.message.meta) {
				delta.message.meta = {}
			}
			delta.message.meta.sentAt = Date.now()
			signal?.throwIfAborted()
		}

		try {
			if (this.ephemeralError.getValue() !== undefined) this.ephemeralError.next(undefined)
			this.updateThread(delta)
			this.onThreadDelta(delta)
		} catch (error) {
			if (!isAbortError(error)) {
				logger.error('Ephemeral error during handle processing', error, {
					name: 'ThreadWorker',
					threadID: this.threadID,
				})
				this.ephemeralError.next(error instanceof Error ? error : new Error(String(error)))
			} else {
				logger.debug(`AbortError caught during handle processing for ${delta.type}.`, {
					name: 'handle queue',
					threadID: this.threadID,
				})
			}
		}
	}
	private handleCalled = false

	/**
	 * Update the thread by applying the {@link delta}.
	 */
	private updateThread(delta: ThreadDelta): void {
		this.thread = applyThreadDelta(this.thread, delta)
	}

	/**
	 * React to a {@link ThreadDelta} change that has already been applied and written to the thread.
	 */
	private onThreadDelta(delta: ThreadDelta): void {
		// For simplicity, keep this method synchronous (no awaits). All actual asynchronous work
		// needs to be tracked with an AbortController stored in a class field so it can be aborted
		// by other operations. The AbortController must be written synchronously by the methods
		// called from here, such as `this.invokeTool`.

		switch (delta.type) {
			case 'user:message': {
				// Handle thread edits by scanning history to update the tracker
				if (delta.index !== undefined) {
					this.trackedFiles.clear()
					this.thread.messages.forEach((message) => {
						if (message.role === 'user') {
							// Track files from tool results
							for (const block of message.content) {
								if (block.type === 'tool_result' && block.run.status === 'done') {
									for (const file of block.run.files ?? []) {
										logger.info(`tracking (again) ${file}`, {
											threadID: this.threadID,
										})
										this.trackFile(file)
									}
								}
							}

							// Track file mentions
							for (const file of message.fileMentions?.files ?? []) {
								logger.debug(`file: ${file.path}`, {
									name: 'tracking file mention',
									threadID: this.threadID,
								})
								this.trackFile(file.path)
							}
						}
					})
				} else {
					// Track file mentions in new messages
					for (const file of delta.message.fileMentions?.files ?? []) {
						logger.debug(`file: ${file.path}`, {
							name: 'tracking file mention',
							threadID: this.threadID,
						})
						this.trackFile(file.path)
					}
				}
				this.runInferenceAndUpdateThread()
				break
			}

			case 'user:tool-input': {
				// Re-invoke the tool now that we have user input for it.
				const block = toolUseBlock(this.thread, delta.toolUse)
				if (block) {
					this.invokeTool(block, delta.value)
				}
				break
			}

			case 'tool:data': {
				// A tool provided updated data. Check if all tools' tool_results are complete, and
				// if so, run inference.
				const runInference = shouldRunInferenceWithToolData(this.thread, delta.toolUse)
				logger.debug(
					`updated tool_result${runInference ? ' and running inference because all tools completed' : ''}`,
					{
						name: `handleThreadDelta(${delta.type}, ${delta.toolUse}, ${delta.data.status})`,
						threadID: this.threadID,
					},
				)
				// Only run inference if the user didn't explicitly cancel it
				if (runInference && this.inferenceState.getValue() !== 'cancelled') {
					this.runInferenceAndUpdateThread()
				}
				break
			}

			case 'assistant:message': {
				// Process new assistant message to check for tools to invoke.
				const toolUses = this.findToolUsesNeedingInvocation(delta.message)
				logger.debug(
					`saw ${toolUses.length} tool uses (${toolUses.map((t) => t.name).join(', ')})`,
					{ name: `handleThreadDelta(${delta.type})`, threadID: this.threadID },
				)
				for (const block of toolUses) {
					this.invokeTool(block, undefined)
				}
				break
			}

			case 'assistant:message-delta': {
				if (delta.event.type === 'message_start') {
					// NOTE(olafurpg): feel free to remove this. I'm adding it
					// so I can get confirmation from people on how much prompt
					// caching we're actually doing in production.
					logger.debug('token usage', delta.event.message.usage)
				} else if (
					delta.event.type === 'message_delta' &&
					delta.inferenceStart &&
					delta.event.usage?.output_tokens
				) {
					const end = performance.now()
					const elapsed = end - delta.inferenceStart
					logger.debug('completed message', {
						outputTokens: delta.event.usage.output_tokens,
						tokensPerSecond: (delta.event.usage.output_tokens / elapsed) * 1000,
						inferenceDuration: humanDuration(elapsed),
					})
				}

				// Process new assistant message to check for tools to invoke.
				const asstMsg = lastMessageOfRole(this.thread, 'assistant')
				if (!asstMsg) {
					throw new Error('no assistant message found')
				}
				const toolUses = this.findToolUsesNeedingInvocation(asstMsg)
				for (const block of toolUses) {
					this.invokeTool(block, undefined)
				}

				// It's possible that all tool_uses have completed by the time we get message_stop.
				// If so, rerun inference.
				if (shouldRunInferenceWithToolData(this.thread)) {
					this.runInferenceAndUpdateThread()
				}

				break
			}
		}
	}

	private findToolUsesNeedingInvocation(message: ThreadAssistantMessage): ToolUseBlock[] {
		const toolResults = toolResultBlocks(this.thread)
		return message.content
			.filter((block): block is ToolUseBlock => block.type === 'tool_use')
			.filter((block) => !block.inputPartialJSON)
			.filter(
				(block) =>
					// Only include tools that haven't been invoked yet.
					!toolResults.has(block.id),
			)
	}

	private async invokeTool(
		toolUse: ToolUseBlock,
		userInput: ToolRunUserInput | undefined,
	): Promise<void> {
		if (this.ops.tools[toolUse.id]) {
			throw new Error(`(bug) tool invocation already in progress: ${toolUse.id}`)
		}
		if (toolUse.inputPartialJSON) {
			throw new Error(`(bug) tool use is incomplete: ${toolUse.id}`)
		}

		// Run hooks.
		const config = await firstValueFrom(resolvedConfig)
		if (config.settings?.hooks) {
			const hookResult = processToolExecutionHooks(config.settings.hooks, {
				threadID: this.threadID,
				toolUse,
			})
			const { abortOp } = await applyHookResult(this, hookResult)
			if (abortOp) {
				return
			}
		}

		logger.debug('Invoking tool', {
			name: 'invokeTool',
			threadID: this.threadID,
			toolUse,
			userInput,
		})
		const op = new AbortController()
		this.ops.tools[toolUse.id] = op

		this.updateThread({
			type: 'tool:data',
			toolUse: toolUse.id,
			data: { status: 'in-progress' },
		})

		try {
			const workspaceRoots = await firstValueFrom(
				this.deps.configService.workspaceRoots,
				op.signal,
			)

			const todos = getTodos(this.thread)
			const toolMessages = new Observable<ToolMessage>((subscriber) => {
				// This block only gets called if the tool actually subscribes
				// to `toolMessages`.
				this.ops.toolMessages[toolUse.id] = subscriber
			})
			const env: ToolRunEnvironment = {
				dir: workspaceRoots.at(0) ?? null,
				dirs: workspaceRoots,
				tool: toolUse.name,
				threadID: this.threadID,
				config: await latestConfig(),
				trackedFiles: new Set(this.trackedFiles),
				trackFileChange: this.createToolUseFileChangeTracker(toolUse.id),
				filesystem: this.filesystem,
				todos,
				systemPromptService: this.deps.systemPromptService,
				toolMessages,
				threadEnvironment: await this.deps.getThreadEnvironment(),
			}

			const sub = this.deps.toolService
				.invokeTool(toolUse.name, { args: toolUse.input, userInput }, env)
				.pipe(
					finalize(() => {
						delete this.ops.tools[toolUse.id]
						this.ops.toolMessages[toolUse.id]?.complete()
						delete this.ops.toolMessages[toolUse.id]
					}),
				)
				.subscribe({
					next: async (run: ToolRun) => {
						logger.debug(`${toolUse.id}, ${run.status}`, {
							name: 'invokeTool',
							threadID: this.threadID,
						})
						if (isToolRunTerminalState(run)) {
							delete this.ops.tools[toolUse.id]
							sub.unsubscribe()

							// Track files when tool calls are completed successfully
							if (run.status === 'done' && run.files?.length) {
								for (const file of run.files) {
									logger.debug(`file: ${file}`, {
										name: 'tracking file from tool call',
										threadID: this.threadID,
									})
									this.trackFile(file)
								}
							}
						}

						await this.updateFileChanges()

						await this.handle(
							{
								type: 'tool:data',
								toolUse: toolUse.id,
								data: run,
							},
							op.signal,
						)
					},
					error: async (error) => {
						await this.handle(
							{
								type: 'tool:data',
								toolUse: toolUse.id,
								data: {
									status: 'error',
									error: {
										message: 'message' in error ? error.message : String(error),
									},
								},
							},
							op.signal,
						)
					},
					complete: () => {},
				})
			onAbort(op.signal, () => sub.unsubscribe())
		} catch (error) {
			delete this.ops.tools[toolUse.id]
			throw error
		}
	}

	private async cancelInProgressTools(): Promise<void> {
		for (const toolUseID of Object.keys(this.ops.tools)) {
			await this.cancelTool(toolUseID as ThreadToolUseID)
		}
	}

	private cancelUnstartedTools(): void {
		const toolResults = toolResultBlocks(this.thread)

		for (const m of this.thread.messages) {
			if (m.role !== 'assistant') {
				continue
			}
			// TODO!(sqs): remove tool_use blocks that were incomplete if they are aborted? probably
			for (const block of m.content) {
				if (
					block.type === 'tool_use' &&
					!block.inputPartialJSON &&
					!toolResults.has(block.id)
				) {
					this.updateThread({
						type: 'tool:data',
						toolUse: block.id,
						data: { status: 'cancelled' },
					})
				}
			}
		}
	}

	private async runInferenceAndUpdateThread(): Promise<void> {
		// Cancel any existing inference operation.
		this.ops.inference?.abort()
		this.ops.inference = null

		this.inferenceState.next('running')
		const op = new AbortController()
		this.ops.inference = op

		// Cancel all in-progress and unstarted tools, because the user sent a
		// message before they were done, so we assume their intent is to cancel
		// them.
		this.cancelUnstartedTools()
		await this.cancelInProgressTools()

		// Also cancel all blocked-on-user tools and tools that were just streamed in but have no
		// tool_result block yet.
		for (const m of this.thread.messages) {
			if (m.role === 'user') {
				for (const block of m.content) {
					if (block.type === 'tool_result' && block.run.status === 'blocked-on-user') {
						this.updateThread({
							type: 'tool:data',
							toolUse: block.toolUseID,
							data: { status: 'cancelled' },
						})
					}
				}
			}
		}

		// If it's the first user message, (re)generate the title and set environment.
		const isFirstUserMessage = this.thread.messages.length === 1
		if (isFirstUserMessage) {
			const msg = this.thread.messages[0]!
			if (msg.role !== 'user') {
				throw new Error('(bug) first message is not a user message')
			}
			generateThreadTitle(msg, op.signal)
				.then((title) => {
					if (op.signal.aborted || this.isDisposed) return
					if (this.thread.title !== title) {
						this.updateThread({ type: 'title', value: title })
					}
				})
				.catch((error) => {
					if (!isAbortError(error)) {
						logger.error('generateThreadTitle error', error, {
							name: 'ThreadWorker',
							threadID: this.threadID,
						})
					}
				})

			if (this.deps.getThreadEnvironment) {
				this.deps
					.getThreadEnvironment()
					.then((env) => {
						// Record model.
						if (!env.tags) env.tags = []
						env.tags = [`model:${ACTIVE_MODEL}`]

						this.updateThread({
							type: 'environment',
							env: { initial: env },
						})
					})
					.catch((error) => {
						logger.error('Failed to initialize thread environment', error, {
							threadID: this.threadID,
						})
					})
			}
		}

		const opAborted = new Promise<void>((resolve) => onAbort(op.signal, resolve))

		try {
			async function generatorNextOrAbort<T1, T2>(
				g: AsyncGenerator<T1, T2>,
			): Promise<IteratorResult<T1, T2>> {
				let result: IteratorResult<T1, T2> | undefined
				await Promise.race([
					opAborted,
					g.next().then((next) => {
						result = next
					}),
				])
				op.signal.throwIfAborted()
				return result!
			}

			const useGemini = false
			if (useGemini) {
				const g = await this.runGeminiInference(this.thread, op.signal)

				const events = toThreadDeltas(g)
				while (true) {
					const event = await events.next()
					if (event.done) {
						await this.handle({
							type: 'inference:completed',
							usage: event.value['~debugUsage'],
							params: event.value['~debugParamsUsed'],
						})
						return
					}
					await this.handle(event.value, op.signal)
					op.signal?.throwIfAborted()
				}
			} else {
				const start = performance.now()
				const g = await this.runInference(this.thread, op.signal)

				let result = await generatorNextOrAbort(g)
				let streamed = false
				while (!result.done) {
					streamed = true

					await this.handle(
						{
							type: 'assistant:message-delta',
							event: result.value,
							inferenceStart: start,
						},
						op.signal,
					)

					result = await generatorNextOrAbort(g)
					op.signal?.throwIfAborted()
				}

				op.signal?.throwIfAborted()
				const returnValue = result.value

				// If we didn't stream any results, use the return value.
				if (!streamed && returnValue.message) {
					await this.handle(
						fromAnthropicResultToThreadDelta(returnValue.message),
						op.signal,
					)
				}

				await this.handle({
					type: 'inference:completed',
					usage: returnValue['~debugUsage'],
					params: returnValue['~debugParamsUsed'],
				})
			}
		} catch (error) {
			if (!(isAbortError(error) || isUserCancellationError(error))) {
				this.ephemeralError.next(error instanceof Error ? error : new Error(String(error)))
			}
		} finally {
			if (this.ops.inference === op) {
				this.ops.inference = null
				this.inferenceState.next('idle')
			}
		}
	}

	private async runInference(
		thread: Thread,
		signal: AbortSignal,
	): Promise<AsyncGenerator<Anthropic.Messages.MessageStreamEvent, AnthropicResult>> {
		const { messages, tools, systemPrompt } = await this.getInferenceParameters(thread, signal)
		const result = runAnthropic(messages, tools, systemPrompt, thread.id, signal)
		signal?.throwIfAborted()
		return result
	}

	private async getInferenceParameters(
		thread: Thread,
		signal?: AbortSignal,
	): Promise<{
		messages: Anthropic.Messages.MessageParam[]
		tools: ToolSpec[]
		systemPrompt: Anthropic.Messages.TextBlockParam[]
	}> {
		const toolResults = await firstValueFrom(this.deps.toolService.tools, signal)
		const enabledToolSpecs = toolResults
			.map(({ spec, enabled }) => (enabled ? spec : null))
			.filter((v): v is NonNullable<typeof v> => v !== null)

		signal?.throwIfAborted()

		const config = await firstValueFrom(resolvedConfig, signal)
		const todosEnabled = config?.settings?.['todos.enabled'] ?? false
		const systemPrompt = await buildSystemPrompt(
			this.deps.systemPromptService,
			await this.deps.getThreadEnvironment(),
			todosEnabled,
		)
		signal?.throwIfAborted()

		// Convert thread to Anthropic format (caching is now handled inside runAnthropic)
		const messages = AnthropicConverters.fromNativeThread(thread)

		return {
			messages,
			tools: enabledToolSpecs,
			systemPrompt,
		}
	}

	private async runGeminiInference(
		thread: Thread,
		signal: AbortSignal,
	): Promise<AsyncGenerator<GenerateContentResponse, GeminiResult>> {
		const toolResults = await firstValueFrom(this.deps.toolService.tools, signal)
		const enabledToolSpecs = toolResults
			.filter(({ enabled }) => enabled)
			.map(({ spec }) => spec)

		signal?.throwIfAborted()

		const config = await firstValueFrom(resolvedConfig, signal)
		const todosEnabled = config?.settings?.['todos.enabled'] ?? false
		const systemPrompt = await buildGeminiSystemPrompt(
			this.deps.systemPromptService,
			await this.deps.getThreadEnvironment(),
			todosEnabled,
		)
		signal?.throwIfAborted()

		// Convert thread to Gemini format
		const messages = GeminiConverters.fromNativeThread(thread)

		// Run the model
		const result = runGemini(messages, enabledToolSpecs, systemPrompt, thread.id, signal)
		signal?.throwIfAborted()
		return result
	}

	private async findAndCancelToolRun(id: ThreadToolUseID, signal?: AbortSignal): Promise<void> {
		this.cancelInference()

		const block = toolUseBlock(this.thread, id)
		if (!block) return

		await this.cancelTool(id, signal)
	}
	private async detachToolRun(id: ThreadToolUseID): Promise<void> {
		const block = toolUseBlock(this.thread, id)
		if (!block) return
		const messages = this.ops.toolMessages[id]
		if (!messages) {
			logger.warn('unable to detach tool use, missing toolMessages', { id })
			return
		}
		messages.next({ type: 'detach-terminal' })
	}

	private cancelDataForToolRun(id: ThreadToolUseID): Extract<ToolRun, { status: 'cancelled' }> {
		const toolResults = toolResultBlocks(this.thread)
		const toolRun = toolResults.get(id)?.run

		let cancelData: ToolRun = { status: 'cancelled' }
		if (toolRun?.status === 'in-progress' && toolRun.progress) {
			cancelData = {
				status: 'cancelled',
				progress: toolRun.progress,
			}
		}
		return cancelData
	}

	private async cancelTool(id: ThreadToolUseID, signal?: AbortSignal): Promise<void> {
		const cancelData = this.cancelDataForToolRun(id)
		this.abortToolOp(id)
		logger.debug(`cancelToolRun(${id})`)
		await this.handle(
			{
				type: 'tool:data',
				toolUse: id,
				data: cancelData,
			},
			signal,
		)
	}

	private async cancel(): Promise<void> {
		logger.debug('cancel: aborting inference operation and tools')

		this.cancelInference()
		this.cancelUnstartedTools()
		this.cancelInProgressTools()

		// When canceling, we leave the current todo list as is
		await this.handle({ type: 'cancelled' }, undefined)
	}

	private cancelInference(): void {
		if (this.ops.inference) {
			this.ops.inference.abort(AbortReason.USER_CANCELLED)
			this.ops.inference = null
		}
		this.inferenceState.next('cancelled')
	}

	private async retry(): Promise<void> {
		logger.debug('retry: retrying inference operation')

		if (this.ephemeralError.getValue() !== undefined) {
			this.ephemeralError.next(undefined)
		}

		if (this.ops.inference) {
			this.ops.inference.abort()
			this.ops.inference = null
		}

		this.inferenceState.next('idle')

		await this.runInferenceAndUpdateThread()
	}

	private abortToolOp(id: ThreadToolUseID): void {
		const abortController = this.ops.tools[id]
		abortController?.abort()
		delete this.ops.tools[id]
	}

	private abortAllTools(): void {
		for (const toolUseID of Object.keys(this.ops.tools)) {
			this.abortToolOp(toolUseID as ThreadToolUseID)
		}
	}

	public async trackFile(path: string): Promise<void> {
		if (this.trackedFiles.has(path)) {
			return
		}
		const workspaceRoots = await this.workspaceRoots()
		const absolutePath = path.endsWith('/')
			? await resolveArgumentDirectory(
					workspaceRoots.map((dir) => dir.fsPath),
					path,
				)
			: await resolveArgumentPath(this.filesystem, workspaceRoots, path)
		logger.debug('tracking file', { absolutePath })
		this.trackedFiles.add(absolutePath)
	}

	private async restoreFileChangesFromBackups(): Promise<void> {
		try {
			const result = await restoreFileChangesFromBackups(this.filesystem)

			for (const [toolUseID, diffs] of result.toolDiffs.entries()) {
				this.fileChangeRecords.set(toolUseID, diffs)
			}

			logger.debug(`Restored ${result.toolDiffs.size} file changes from disk`)

			await this.updateFileChanges()
		} catch (error) {
			logger.error('Error restoring file changes', error, { threadID: this.threadID })
		}
	}

	private createToolUseFileChangeTracker(
		toolUseID: ThreadToolUseID,
	): (path: string, modifyFn: (path: string) => Promise<void>) => Promise<void> {
		return this.createFileTracker({
			toolUseID,
			threadID: this.threadID,
			toolFileChanges: this.fileChangeRecords,
		})
	}

	private async workspaceRoots(): Promise<URI[]> {
		return firstValueFrom(this.deps.configService.workspaceRoots)
	}

	/**
	 * Reverts all file changes made by this thread by restoring the original content
	 * of modified files and deleting newly created files.
	 */
	public async revertFileChanges(filePath?: string): Promise<void> {
		const workspaceRoots = await this.workspaceRoots()
		const workspaceRoot = workspaceRoots.at(0)
		if (!workspaceRoot) {
			throw new Error('No workspace root found')
		}
		await revertAllFileChanges(
			this.filesystem,
			this.fileChangeRecords,
			workspaceRoot.fsPath,
			filePath,
		)
		await this.updateFileChanges()
	}

	private async revertPriorFileChanges(index: number): Promise<void> {
		const keep = new Set<ThreadToolUseID>()
		this.thread.messages.slice(0, index).forEach((message) => {
			if (message.role === 'user') {
				for (const block of message.content) {
					if (block.type === 'tool_result') {
						keep.add(block.toolUseID)
					}
				}
			} else {
				for (const block of message.content) {
					if (block.type === 'tool_use') {
						keep.add(block.id)
					}
				}
			}
		})

		const revert = new Map<ThreadToolUseID, Map<string, FileChangeRecord>>()
		for (const [toolUseID, diffs] of this.fileChangeRecords.entries()) {
			if (!keep.has(toolUseID)) {
				revert.set(toolUseID, diffs)
			}
		}
		if (revert.size === 0) return

		const workspaceRoots = await firstValueFrom(this.deps.configService.workspaceRoots)
		const workspaceRoot = workspaceRoots.at(0)
		if (!workspaceRoot) {
			throw new Error('No workspace root found')
		}

		await revertAllFileChanges(this.filesystem, revert, workspaceRoot.fsPath)

		for (const [toolUseID, _] of revert.entries()) {
			this.fileChangeRecords.delete(toolUseID)
		}
		await this.updateFileChanges()
	}

	private async updateFileChanges(): Promise<void> {
		const workspaceRoots = await firstValueFrom(this.deps.configService.workspaceRoots)
		const workspaceRoot = workspaceRoots.at(0)

		const latestChange = new Map<string, FileChangeRecord>()
		for (const [_toolUseID, diffs] of this.fileChangeRecords.entries()) {
			for (const [absPath, change] of diffs.entries()) {
				const path = workspaceRoot
					? absPath.replace(workspaceRoot.fsPath + '/', '')
					: absPath

				if (
					!latestChange.has(path) ||
					change.timestamp > latestChange.get(path)!.timestamp
				) {
					latestChange.set(path, change)
				}
			}
		}

		const fileChanges: FileChange[] = []
		for (const [path, change] of latestChange.entries()) {
			const { added, removed, modified, created, reverted } = calculateDiffStat(change)
			if (!added && !removed && !modified) continue

			fileChanges.push({
				created,
				path,
				reverted,
				diffStat: {
					added,
					removed,
					modified,
				},
			})
		}

		this.fileChanges.next({
			files: fileChanges,
		})
	}

	private isDisposed = false
	public dispose(): void {
		if (this.isDisposed) {
			return
		}

		this.isDisposed = true

		this.abortAllTools()
		if (this.ops.inference) {
			this.ops.inference.abort()
			this.ops.inference = null
		}

		this.fileChangeRecords.clear()
		this.fileChanges.next({ files: [] })

		if (this.threadReadWriter) {
			this.threadReadWriter.dispose()
			this.threadReadWriter = null
		}

		// Note: We don't clean up the backup files during worker disposal
		// Backups are persistent across sessions and are only cleaned up when
		// the thread is explicitly deleted by the user
	}
}

/**
 * Called after a `tool:data` delta to see whether inference should be run.
 */
function shouldRunInferenceWithToolData(
	thread: Thread,
	updatedToolUseID?: ThreadToolUseID,
): boolean {
	const asstMsg = lastMessageOfRole(thread, 'assistant')
	if (!asstMsg || asstMsg.state.type !== 'complete' || asstMsg.state.stopReason !== 'tool_use') {
		return false
	}

	// Get all tool_use IDs from the last assistant message.
	const toolUseIDs: ThreadToolUseID[] = asstMsg.content
		.filter((b): b is ToolUseBlock => b.type === 'tool_use')
		.map((b) => b.id)

	// Check if the updated tool is part of this message's tool uses.
	if (updatedToolUseID && !toolUseIDs.includes(updatedToolUseID)) {
		throw new Error(
			`(bug) updated tool use ID not found in assistant message: ${updatedToolUseID}`,
		)
	}

	// Check if all tools are now in terminal state.
	const toolResults = toolResultBlocks(thread)
	const allComplete = toolUseIDs.every((id) => {
		const resultBlock = toolResults.get(id)
		return resultBlock && isToolRunTerminalState(resultBlock.run)
	})
	return allComplete
}
