export const GeminiConverters: Converter<Content[]> = {
	fromNativeThread(thread: Thread): Content[] {
		const contents: Content[] = []

		for (const m of thread.messages) {
			switch (m.role) {
				case 'user': {
					const parts = userMessageToGeminiParts(m)
					if (parts.length === 0) {
						continue
					}
					contents.push({
						role: 'user',
						parts,
					})
					break
				}
				case 'assistant': {
					const content = m.content.filter(
						(
							b,
						): b is Exclude<
							(typeof m.content)[number],
							ToolUseBlockIncomplete | ServerToolUseBlock | ServerWebSearchResultBlock
						> => {
							if (b.type === 'tool_use' && b.inputPartialJSON) {
								return false
							}
							if (
								b.type === 'server_tool_use' ||
								b.type === 'web_search_tool_result'
							) {
								return false
							}
							return true
						},
					)

					// Verify all tool_use blocks have corresponding tool_result blocks
					const toolResults = toolResultBlocks(thread)
					for (const block of content) {
						if (block.type === 'tool_use') {
							const toolResult = toolResults.get(block.id)
							if (!toolResult?.run) {
								throw new Error(
									`(bug) corresponding tool_result not found for tool_use (id=${block.id}, name=${block.name})`,
								)
							}
						}
					}

					// Skip empty assistant messages
					if (content.length === 0) {
						break
					}

					const assistantParts = assistantMessageToGeminiParts(content)
					if (assistantParts.length > 0) {
						contents.push({
							role: 'model',
							parts: assistantParts,
						})
					}
					break
				}
			}
		}

		return contents
	},

	toNativeThread(_: Content[]): Pick<Thread, 'messages'> {
		// UNUSED, so unimplemented
		return { messages: [] }
	},
}

export interface GeminiResult {
	'~debugParamsUsed'?: object
	'~debugUsage': DebugUsage
}

// https://ai.google.dev/gemini-api/docs/models#gemini-2.5-pro-preview-05-06
export const GEMINI_2_5_PRO = 'gemini-2.5-pro-preview-05-06'
const GEMINI_2_5_MAX_INPUT_TOKENS = 1_048_576
const GEMINI_2_5_MAX_OUTPUT_TOKENS = 65_535

export async function* runGemini(
	contents: Content[],
	tools: ToolSpec[],
	systemPrompt: string,
	threadID: ThreadID,
	signal?: AbortSignal,
): AsyncGenerator<GenerateContentResponse, GeminiResult, void> {
	const config = await firstValueFrom(resolvedConfig, signal)
	signal?.throwIfAborted()
	const genai = await getGenAIClient(config, { threadID })
	signal?.throwIfAborted()
	// TODO: figure out how to give a signal to the chat

	const lastUserMessage = contents.pop()
	if (lastUserMessage?.role !== 'user') {
		logger.warn('expected last message to be a user message', lastUserMessage)
		throw new Error('expected last message to be a user message')
	}

	const geminiTools = toolsToGeminiFunctions(tools)

	const chatParams: CreateChatParameters = {
		model: GEMINI_2_5_PRO,
		history: contents,
		config: {
			thinkingConfig: {
				includeThoughts: true,
			},
		},
	}
	const chat = await genai.chats.create(chatParams)

	const messageParams: SendMessageParameters = {
		message: lastUserMessage.parts ?? [],
		config: {
			tools: [geminiTools],
			systemInstruction: systemPrompt,
			maxOutputTokens: GEMINI_2_5_MAX_OUTPUT_TOKENS,
			thinkingConfig: {
				includeThoughts: true,
			},
		},
	}

	const result = await chat.sendMessageStream(messageParams)

	const allChunks = []
	for await (const chunk of result) {
		allChunks.push(chunk)
		yield chunk
	}

	const lastChunk = allChunks.at(-1)
	return {
		'~debugParamsUsed': {
			model: GEMINI_2_5_PRO,
			chatParams,
			messageParams,
			streamedMessages: allChunks,
		},
		'~debugUsage': {
			maxInputTokens: GEMINI_2_5_MAX_INPUT_TOKENS,
			inputTokens: lastChunk?.usageMetadata?.promptTokenCount ?? 0,
			outputTokens:
				(lastChunk?.usageMetadata?.candidatesTokenCount ?? 0) +
				(lastChunk?.usageMetadata?.thoughtsTokenCount ?? 0),
			totalInputTokens: lastChunk?.usageMetadata?.promptTokenCount ?? 0,
			cacheCreationInputTokens: 0,
			cacheReadInputTokens: 0,
		},
	}
}

function toolsToGeminiFunctions(tools: ToolSpec[]): Tool {
	return {
		functionDeclarations: [...tools.map(toolSpecToFunctionDeclaration)],
	}
}

function toolSpecToFunctionDeclaration(tool: ToolSpec): FunctionDeclaration {
	return {
		name: tool.name,
		description: tool.description ?? '',
		parameters: jsonSchemaToSchema(tool.inputSchema as JSONSchema4),
	}
}

const typeConversion: Partial<Record<JSONSchema4TypeName, Type>> = {
	string: Type.STRING,
	number: Type.NUMBER,
	integer: Type.INTEGER,
	boolean: Type.BOOLEAN,
	object: Type.OBJECT,
	array: Type.ARRAY,
}

function jsonSchemaToSchema(jsonSchema: JSONSchema4): Schema {
	const schema: Schema = {}

	// Not all types map 1:1. For example, null or any. Just skip setting type in those cases.
	const schemaType = typeConversion[(jsonSchema.type as JSONSchema4TypeName) ?? 'any']
	if (schemaType) schema.type = schemaType

	// Add optional fields conditionally
	if (jsonSchema.description) schema.description = jsonSchema.description
	if (jsonSchema.required) schema.required = jsonSchema.required as string[]

	// NOTE: some fields cause API errors even though they are defined in the
	// types exposed by the library. That's why these are commented out here.
	// if (jsonSchema.enum) schema.enum = jsonSchema.enum
	// if (jsonSchema.format) schema.format = jsonSchema.format
	// if (jsonSchema.default !== undefined) schema.default = jsonSchema.default

	// Handle nested properties
	if (jsonSchema.properties) {
		schema.properties = Object.fromEntries(
			Object.entries(jsonSchema.properties).map(([name, schema]) => [
				name,
				jsonSchemaToSchema(schema),
			]),
		)
	}

	// Handle array items
	if (jsonSchema.items) {
		schema.items = jsonSchemaToSchema(jsonSchema.items)
	}

	return schema
}

/**
 * Returns a GenAI client configured to use the Amp server as a proxy.
 */
export async function getGenAIClient(
	config: ResolvedConfiguration,
	options?: { threadID?: string },
): Promise<GoogleGenAI> {
	const { settings, secrets } = config
	const { url } = settings
	const apiKey = await secrets.getToken('apiKey', url)
	if (!apiKey) {
		throw new Error('API key not found. You must provide an API key in settings.')
	}

	// Return a client configured to use the Amp server proxy
	return new GoogleGenAI({
		apiKey: '',
		vertexai: true,
		httpOptions: {
			baseUrl: new URL('/api/provider/google', url).toString(),
			headers: {
				Authorization: 'Bearer ' + apiKey,
				[HEADER_KEY_THREAD_ID]: options?.threadID || '',
			},
		},
	})
}
/**
 * Convert ThreadUserMessage to GenAI parts format.
 */
function userMessageToGeminiParts(message: ThreadUserMessage): Array<Part> {
	const parts: Array<Part> = []

	// Format file mentions if present
	if (message.fileMentions && message.fileMentions.files.length > 0) {
		parts.push({ text: formatFileMentionsForPrompt(message.fileMentions) })
	}

	// Format metadata if present
	if (message.userState) {
		parts.push({ text: formatUserStateForPrompt(message.userState) })
	}

	for (const block of message.content) {
		if (block.type === 'text') {
			parts.push({ text: block.text })
		} else if (block.type === 'image') {
			if (
				block.source.type === 'base64' &&
				'mediaType' in block.source &&
				'data' in block.source
			) {
				parts.push({
					inlineData: {
						mimeType: block.source.mediaType,
						data: block.source.data,
					},
				})
			} else if (block.source.type === 'url' && 'url' in block.source) {
				// Note: Gemini doesn't directly support URL images, would need to fetch and convert to base64
				// TODO: consider fetching the image content
				parts.push({ text: `[Image URL: ${block.source.url}]` })
			}
		} else if (block.type === 'tool_result') {
			// Tool results are converted to function responses
			const response: Record<string, unknown> = {}
			if (block.run.status === 'done') {
				response.output = block.run.result
			} else if (block.run.status === 'error') {
				response.error = block.run.error?.message ?? 'Error executing tool'
			} else {
				response.error = `Tool status: ${block.run.status}`
			}

			// HACK: extract the tool name from the ID because gemini requires the name
			const [, name] = block.toolUseID.split('__')
			const functionResponse: FunctionResponse = {
				name: name!,
				response,
			}
			parts.push({ functionResponse })
		}
	}

	return parts
}

/**
 * Convert assistant message content to Gemini parts.
 */
function assistantMessageToGeminiParts(
	content: (TextBlock | ToolUseBlock | ThinkingBlock | RedactedThinkingBlock)[],
): Array<Part> {
	const parts: Array<Part> = []

	for (const block of content) {
		if (block.type === 'text') {
			parts.push({ text: block.text })
		} else if (block.type === 'tool_use') {
			// Convert tool_use to functionCall
			parts.push({
				functionCall: {
					name: block.name,
					args: block.input ?? {},
				},
			})
		} else if (block.type === 'thinking') {
			parts.push({ text: block.thinking, thought: true })
		} else if (block.type === 'redacted_thinking') {
			// Skip redacted thinking blocks or include placeholder
			parts.push({ text: '[Redacted thinking]' })
		}
	}

	return parts
}

export async function* toThreadDeltas(
	input: AsyncGenerator<GenerateContentResponse, GeminiResult>,
): AsyncGenerator<ThreadDelta, GeminiResult> {
	// TODO: we should probably not be using anthropic deltas here. Currently
	// doing it for maximum deletability.
	let index = 0
	const messageID = uniqueId('message')
	yield {
		type: 'assistant:message-delta',
		event: {
			type: 'message_start',
			message: {
				id: messageID,
				content: [],
				model: GEMINI_2_5_PRO,
				role: 'assistant',
				type: 'message',
				stop_reason: null,
				stop_sequence: null,
				usage: {
					// TODO
					service_tier: 'standard',
					server_tool_use: null,
					cache_creation_input_tokens: null,
					cache_read_input_tokens: null,
					input_tokens: 0,
					output_tokens: 0,
				},
			},
		},
	}
	let currentBlock: RawContentBlockStartEvent | undefined
	while (true) {
		const event = await input.next()
		if (event.done) {
			return event.value
		}
		const response = event.value
		const candidate = response.candidates?.at(0)
		if (candidate === undefined) {
			continue
			// throw new Error('unexpected response with zero candidates ' + JSON.stringify(response))
		}
		for (const part of candidate.content?.parts ?? []) {
			if (part.text && !part.thought) {
				if (currentBlock && currentBlock.content_block.type !== 'text') {
					yield {
						type: 'assistant:message-delta',
						event: {
							type: 'content_block_stop',
							index: currentBlock.index,
						},
					}
					index += 1
					currentBlock = undefined
				}

				if (!currentBlock) {
					currentBlock = {
						type: 'content_block_start',
						index: index,
						content_block: {
							type: 'text',
							citations: [],
							text: '',
						},
					}

					yield {
						type: 'assistant:message-delta',
						event: currentBlock,
					}
				}

				// We've already started a message, so send a delta
				yield {
					type: 'assistant:message-delta',
					event: {
						type: 'content_block_delta',
						index: currentBlock.index,
						delta: {
							type: 'text_delta',
							text: response.text ?? '',
						},
					},
				}
			} else if (part.text && part.thought) {
				if (currentBlock && currentBlock.content_block.type !== 'thinking') {
					yield {
						type: 'assistant:message-delta',
						event: {
							type: 'content_block_stop',
							index: currentBlock.index,
						},
					}
					index += 1
					currentBlock = undefined
				}

				if (!currentBlock) {
					currentBlock = {
						type: 'content_block_start',
						index: index,
						content_block: {
							type: 'thinking',
							thinking: '',
							signature: '',
						},
					}

					yield {
						type: 'assistant:message-delta',
						event: currentBlock,
					}
				}

				// We've already started a message, so send a delta
				yield {
					type: 'assistant:message-delta',
					event: {
						type: 'content_block_delta',
						index: currentBlock.index,
						delta: {
							type: 'thinking_delta',
							thinking: part.text,
						},
					},
				}
			} else {
				if (currentBlock) {
					// We're starting a non-text block. Close the active text block
					yield {
						type: 'assistant:message-delta',
						event: {
							type: 'content_block_stop',
							index: currentBlock.index,
						},
					}
					currentBlock = undefined
					index += 1
				}
				// We only support function calls for now.
				if (part.functionCall) {
					yield {
						type: 'assistant:message-delta',
						event: {
							type: 'content_block_start',
							content_block: {
								type: 'tool_use',
								name: part.functionCall.name ?? '',
								input: '',
								id: `${newThreadToolUseID()}__${part.functionCall.name ?? ''}`,
							},
							index,
						},
					}
					yield {
						type: 'assistant:message-delta',
						event: {
							type: 'content_block_delta',
							delta: {
								type: 'input_json_delta',
								partial_json: JSON.stringify(part.functionCall.args),
							},
							index,
						},
					}
					yield {
						type: 'assistant:message-delta',
						event: {
							type: 'content_block_stop',
							index,
						},
					}
					index += 1
				}
			}
		}
		if (candidate.finishReason) {
			if (currentBlock) {
				// We started a block, so be sure to end it
				yield {
					type: 'assistant:message-delta',
					event: {
						type: 'content_block_stop',
						index: currentBlock.index,
					},
				}
				index += 1
			}
			// Finally, yield a message stop
			yield {
				type: 'assistant:message-delta',
				event: {
					type: 'message_delta',
					delta: {
						stop_reason: extractFinishReason(candidate),
						stop_sequence: null,
					},
					usage: {
						cache_creation_input_tokens: null,
						cache_read_input_tokens: null,
						input_tokens: null,
						server_tool_use: null,
						output_tokens:
							(response.usageMetadata?.candidatesTokenCount ?? 0) +
							(response.usageMetadata?.thoughtsTokenCount ?? 0),
					},
				},
			}
		}
	}
}

function extractFinishReason(
	candidate: Candidate,
): 'tool_use' | 'end_turn' | 'max_tokens' | 'stop_sequence' {
	switch (candidate.finishReason) {
		case FinishReason.MAX_TOKENS:
			return 'max_tokens'
		case FinishReason.STOP:
			if (candidate.content?.parts?.some((part) => part.functionCall !== undefined)) {
				return 'tool_use'
			}
			break
	}
	return 'end_turn'
}
