import logger from '@sourcegraph/amp-core/src/common/logger'
import { mkdir, readFile, writeFile } from 'node:fs/promises'
import { homedir } from 'node:os'
import path from 'node:path'
import { xdgData } from 'xdg-basedir'
import { type HistoryProvider } from './line-editor'

const DATA_DIR = path.join(xdgData || path.join(homedir(), '.local/share'), 'amp')
const DEFAULT_HISTORY_FILE = path.join(DATA_DIR, 'history.json')
const DEFAULT_HISTORY_SIZE = 1000

/**
 * File-based history provider that stores history entries as JSON.
 */
export class FileHistoryProvider implements HistoryProvider {
	private history: string[] = []
	private index: number = -1
	private loaded: boolean = false
	private historyFile: string
	private maxSize: number

	constructor(options?: { historyFile?: string; maxSize?: number }) {
		this.historyFile = options?.historyFile || DEFAULT_HISTORY_FILE
		this.maxSize = options?.maxSize || DEFAULT_HISTORY_SIZE
		this.ensureLoaded()
	}

	private async ensureLoaded(): Promise<void> {
		if (!this.loaded) {
			try {
				const content = await readFile(this.historyFile, 'utf-8')
				try {
					this.history = JSON.parse(content)
					logger.info('Loaded history from file', {
						file: this.historyFile,
						size: this.history.length,
					})
				} catch (e) {
					// If the file is not valid JSON, start with empty history
					this.history = []
				}
			} catch (error) {
				// If file doesn't exist or can't be read, start with empty history
				this.history = []
			}
			this.loaded = true
		}
	}

	async add(message: string): Promise<void> {
		await this.ensureLoaded()

		// Don't add empty messages or duplicates at the end
		if (
			!message.trim() ||
			(this.history.length > 0 && this.history[this.history.length - 1] === message)
		) {
			return
		}

		// Add to history
		this.history.push(message)

		// Trim history to max size
		if (this.history.length > this.maxSize) {
			this.history = this.history.slice(this.history.length - this.maxSize)
		}

		// Save to file
		try {
			// Ensure the directory exists
			await mkdir(path.dirname(this.historyFile), { recursive: true })

			// Write history to file
			await writeFile(this.historyFile, JSON.stringify(this.history), 'utf-8')
		} catch (error) {
			logger.error(
				`Failed to save history: ${error instanceof Error ? error.message : String(error)}`,
			)
		}

		// Reset index
		this.reset()
	}

	previous(): string | null {
		if (this.history.length === 0) {
			return null
		}

		// If at the beginning of history, stay there
		if (this.index === 0) {
			return this.history[0] || null
		}

		// Navigate back in history
		if (this.index === -1) {
			this.index = this.history.length - 1
		} else {
			this.index--
		}

		return this.history[this.index] || null
	}

	next(): string | null {
		if (this.history.length === 0 || this.index === -1) {
			return null
		}

		// Navigate forward in history
		this.index++

		// If we've moved past the end of history, return null (to restore original)
		if (this.index >= this.history.length) {
			this.index = -1
			return null
		}

		return this.history[this.index] || null
	}

	reset(): void {
		this.index = -1
	}
}
