import { firstValueFrom } from '@sourcegraph/observable'
import { AnthropicConverters, runAnthropicSync } from '../inference/backends/anthropic'
import { type Dict, type ToolRun } from '../tools/tool-service'
import { newThreadID, type Thread, type ThreadID } from './thread'
import { type ThreadService } from './thread-service'

/**
 * Service for creating summary threads.
 */
export class ThreadSummaryService {
	constructor(private threadService: ThreadService) {}

	public async summarizeThread(threadID: ThreadID): Promise<string> {
		const originalThread = await firstValueFrom(this.threadService.observe(threadID))
		if (!originalThread) {
			throw new Error(`Thread ${threadID} not found`)
		}

		return await summarizeThread(originalThread)
	}
	/**
	 * Create a new thread that contains a summary of the original thread.
	 */
	public async createSummaryThread(threadID: ThreadID): Promise<ThreadID> {
		const originalThread = await firstValueFrom(this.threadService.observe(threadID))
		if (!originalThread) {
			throw new Error(`Thread ${threadID} not found`)
		}

		const summary = await summarizeThread(originalThread)
		return this.createThreadWithSummary(threadID, summary)
	}

	private async createThreadWithSummary(
		originThreadID: ThreadID,
		summary: string,
	): Promise<ThreadID> {
		const originalThread = await firstValueFrom(this.threadService.observe(originThreadID))
		if (!originalThread) {
			throw new Error(`Thread ${originThreadID} not found`)
		}
		const newID = newThreadID()
		const newThreadReadWriter = await this.threadService.exclusiveSyncReadWriter(newID)
		const newThread = newThreadReadWriter.read()
		newThread.originThreadID = originThreadID
		newThread.v++
		newThread.title = summary.split(' ').slice(0, 7).join(' ')

		// Copy environment from original thread
		if (originalThread.env) {
			newThread.env = JSON.parse(JSON.stringify(originalThread.env))
		}
		newThread.messages.push({
			role: 'user',
			content: [
				{
					type: 'text',
					text: 'Summarize the previous conversation and continue from here.',
				},
			],
		})

		newThread.messages.push({
			role: 'assistant',
			content: [
				{
					type: 'text',
					text: summary,
				},
			],
			state: { type: 'complete', stopReason: 'end_turn' },
		})

		newThreadReadWriter.write(newThread)
		newThreadReadWriter.dispose()

		const expectedVersion = newThread.v
		await this.threadService.flushVersion(newID, expectedVersion)

		return newID
	}
}

export const MAX_TOOL_RESULT_LINES = 50

const SUMMARY_INSTRUCTION = `Provide a detailed but concise summary of our conversation above.

Provide enough information so that someone else can continue the conversation in your place.

Your summary should contain what I wanted, what we did, what we're doing, and what we still have to include.

Include important file paths, function names, and commands that are helpful for someone else to continue where we left off.

Address the user in the second person.

Do NOT use headings in your response. Use short paragraphs and lists.

Do NOT start with 'You asked me to explain' or 'You asked me to summarize' or other phrases to that effect. Start with the summary.",
`

const TRUNCATION_MESSAGE =
	'\n\n[ ... omitted remaining lines to make summarizing use less tokens ... ]'

const RESULT_PROPERTIES_TO_TRUNCATE = ['text', 'diff', 'output'] as const

function truncateString(text: string): string {
	const lines = text.split('\n')
	if (lines.length > MAX_TOOL_RESULT_LINES) {
		return lines.slice(0, MAX_TOOL_RESULT_LINES).join('\n') + TRUNCATION_MESSAGE
	}
	return text
}

function truncateToolRunResult(
	result: Extract<ToolRun, { status: 'done' }>['result'],
): Extract<ToolRun, { status: 'done' }>['result'] {
	// Handle string results (e.g., read_file, Grep)
	if (typeof result === 'string') {
		return truncateString(result)
	}

	// Handle array results (e.g., Grep, get_diagnostics)
	if (Array.isArray(result)) {
		// Truncate array length if needed
		if (result.length > MAX_TOOL_RESULT_LINES) {
			result.splice(
				MAX_TOOL_RESULT_LINES,
				result.length - MAX_TOOL_RESULT_LINES,
				TRUNCATION_MESSAGE,
			)
		}

		// Truncate each string in the array
		for (let i = 0; i < result.length; i++) {
			if (typeof result[i] === 'string') {
				result[i] = truncateString(result[i])
			}
		}
		return result
	}

	// Handle object results
	if (typeof result === 'object' && result !== null) {
		// Process common string properties
		for (const prop of RESULT_PROPERTIES_TO_TRUNCATE) {
			if (prop in result && typeof result[prop as keyof typeof result] === 'string') {
				;(result as any)[prop] = truncateString((result as any)[prop])
			}
		}
		return result
	}

	return result
}

type CancelledProgress = Extract<ToolRun, { status: 'cancelled' }>['progress']

/**
 * Truncates progress information from cancelled tool runs to reduce token usage for summarization
 */
function truncateCancelledProgress(progress: CancelledProgress): CancelledProgress {
	if (progress === undefined) {
		return undefined
	}

	// Handle string progress
	if (typeof progress === 'string') {
		return truncateString(progress)
	}

	// Handle string[] progress
	if (Array.isArray(progress)) {
		// If it's an array of strings
		if (progress.length > 0 && typeof progress[0] === 'string') {
			const result: string[] = []
			for (const item of progress) {
				if (typeof item === 'string') {
					result.push(truncateString(item))
				}
			}
			return result
		}

		// If it's an array of Dict objects
		if (progress.length > 0 && typeof progress[0] === 'object') {
			const result: Dict[] = []
			for (const item of progress) {
				if (typeof item === 'object' && item !== null) {
					result.push(truncateObjectProperties(item as Dict))
				}
			}
			return result
		}
	}

	// Handle Dict object progress
	if (typeof progress === 'object' && progress !== null) {
		return truncateObjectProperties(progress as Dict)
	}

	return progress
}

// Helper function to truncate properties in an object
function truncateObjectProperties(obj: Dict): Dict {
	// Create a deep copy
	const copy = JSON.parse(JSON.stringify(obj))

	// Truncate common string properties
	for (const prop of RESULT_PROPERTIES_TO_TRUNCATE) {
		if (prop in copy && typeof copy[prop] === 'string') {
			copy[prop] = truncateString(copy[prop])
		}
	}

	return copy
}

export function truncateThread(thread: Thread) {
	for (const message of thread.messages) {
		if (message.role === 'user') {
			// If the thread is too large, we drop the file mentions, since they can eat up a lot of space
			message.fileMentions = undefined

			// Find and truncate tool result blocks that are too long
			for (const block of message.content) {
				if (block.type === 'tool_result') {
					if (block.run.status === 'done') {
						block.run.result = truncateToolRunResult(block.run.result)
					} else if (block.run.status === 'cancelled' && block.run.progress) {
						// Use the same logic for progress as we use for results
						block.run.progress = truncateCancelledProgress(block.run.progress)
					}
				}
			}
		}
	}
}

async function summarizeThread(thread: Thread): Promise<string> {
	// Create a deep clone of the thread to avoid modifying the original
	const threadClone = JSON.parse(JSON.stringify(thread)) as Thread
	// Truncate it to reduce tokens
	truncateThread(threadClone)

	const messages = AnthropicConverters.fromNativeThread(threadClone)

	messages.push({
		role: 'user',
		content: SUMMARY_INSTRUCTION,
	})

	const response = await runAnthropicSync(messages, [], [])

	if (!response.message) {
		throw new Error('Summary did not produce a message')
	}
	return response.message?.content
		.filter((block) => block.type === 'text')
		.map((block) => (block.type === 'text' ? block.text : ''))
		.join('\n\n')
		.trimEnd()
}
