import logger from '../../common/logger'
import type { ThreadID, ToolUseBlockComplete } from '../thread'
import type { ThreadWorker } from '../thread-worker'
import { compatibleHooks, type Hook } from './schema'

export type HookResult =
	| {
			hookID: string
			action: Hook['action']
	  }
	| { action: null }

/**
 * Process hooks for tool execution events
 *
 * @param hooks Array of hooks from settings
 * @param context Context information about the current tool execution
 * @returns HookResult with the action to take and possible message
 */
export function processToolExecutionHooks(
	hooks: Hook[] | undefined,
	context: {
		threadID: ThreadID
		toolUse: Pick<ToolUseBlockComplete, 'id' | 'name' | 'input'>
	},
): HookResult {
	if (!hooks) return { action: null }

	hooks = compatibleHooks(hooks)
	for (const hook of hooks) {
		if (hook.if === false) {
			// Hook is explicitly disabled.
			continue
		}

		switch (hook.on.event) {
			case 'tool:pre-execute': {
				const toolNameEquals = Array.isArray(hook.on.tool) ? hook.on.tool : [hook.on.tool]
				if (!toolNameEquals.includes(context.toolUse.name)) {
					continue
				}

				// Convert input to string for string matching. TODO(sqs): match against something
				// nicer than just JSON.stringify.
				const inputStr = JSON.stringify(context.toolUse.input)

				const inputContains = Array.isArray(hook.on['input.contains'])
					? hook.on['input.contains']
					: [hook.on['input.contains']]
				for (const matchString of inputContains) {
					if (inputStr.includes(matchString)) {
						logger.debug(`Hook triggered: ${hook.id}`, {
							hookID: hook.id,
							threadID: context.threadID,
							toolName: context.toolUse.name,
							toolUseID: context.toolUse.id,
							matchString,
							action: hook.action,
						})

						if (hook.action.type === 'send-user-message') {
							return {
								hookID: hook.id,
								action: hook.action,
							}
						}
					}
				}
			}
		}
	}

	// No hooks matched.
	return { action: null }
}

/**
 * Apply a {@link HookResult} to the agent's {@link ThreadWorker} for a thread.
 */
export async function applyHookResult(
	worker: ThreadWorker,
	result: HookResult,
): Promise<{ abortOp: boolean }> {
	if (!result.action) return { abortOp: false }

	switch (result.action.type) {
		case 'send-user-message':
			await worker.handle({
				type: 'user:message',
				message: {
					content: [
						{
							type: 'text',
							text: result.action.message,
						},
					],
					source: { type: 'hook', hook: result.hookID },
				},
			})
			return { abortOp: true }
	}
}
