/**
 * Creates a throttled function that limits how often the given function can be executed
 * and caches results based on the provided cacheKey function.
 */
export function throttledPromise<Args extends any[], R>(options: {
	interval: number
	fn: (...args: Args) => Promise<R>
	cacheKey?: (...args: Args) => string | number
}): (...args: Args) => Promise<R> {
	const { interval, fn, cacheKey } = options
	const lastPromiseMap = new Map<string | number, Promise<R>>()
	const lastTimeMap = new Map<string | number, number>()

	return (...args: Args): Promise<R> => {
		const key = cacheKey ? cacheKey(...args) : 'default'
		const now = Date.now()
		const lastTime = lastTimeMap.get(key) || 0

		if (lastPromiseMap.has(key) && now - lastTime < interval) {
			return lastPromiseMap.get(key)!
		}

		lastTimeMap.set(key, now)
		const promise = fn(...args)
		lastPromiseMap.set(key, promise)
		return promise
	}
}
