export async function resolveArgumentDirectory(
	dirs: string[],
	dirPath: string | undefined,
): Promise<string> {
	if (import.meta.env.VITE_PLATFORM === 'vscode-web') {
		const fnc = await import('../tools/builtin/filesystem/list_directory.vscode-web')
		return fnc.resolveArgumentDirectoryWeb(dirs, dirPath)
	}
	const fnc = await import('../tools/builtin/filesystem/list_directory.node')
	return fnc.resolveArgumentDirectoryNode(dirs, dirPath)
}
