import { fstatSync } from 'node:fs'
import logger from '@sourcegraph/amp-core/src/common/logger'
import { readlinkSync } from 'node:fs'

/**
 * Configuration for stdin reading with safety limits
 */
export interface StdinReaderOptions {
	/** Maximum bytes to read (default: 1MB) */
	maxSizeBytes?: number
	/** Timeout in milliseconds (default: 30 seconds) */
	timeoutMs?: number
}

/**
 * Result of stdin reading operation
 */
export interface StdinReadResult {
	/** The content read from stdin */
	content: string
	/** Whether the operation was truncated due to size limit */
	truncated: boolean
	/** Whether the operation timed out */
	timedOut: boolean
	/** The actual number of bytes read */
	bytesRead: number
}

/**
 * Error thrown when stdin reading fails
 */
export class StdinReadError extends Error {
	constructor(
		message: string,
		public readonly cause?: unknown,
	) {
		super(message)
		this.name = 'StdinReadError'
	}
}

/**
 * Safely reads content from stdin with size limits, timeouts, and special device detection
 */
export async function readStdinContent(options: StdinReaderOptions = {}): Promise<StdinReadResult> {
	const { maxSizeBytes = 1024 * 1024, timeoutMs = 30_000 } = options

	// If stdin is a TTY, return empty content immediately
	if (process.stdin.isTTY) {
		return {
			content: '',
			truncated: false,
			timedOut: false,
			bytesRead: 0,
		}
	}

	// Check if stdin is a special device file that could cause infinite reads
	try {
		const stats = fstatSync(process.stdin.fd)
		if (stats.isCharacterDevice()) {
			// Check if it's a known problematic device
			const stdinPath = `/proc/self/fd/${process.stdin.fd}`
			try {
				const realPath = readlinkSync(stdinPath)
				if (realPath === '/dev/zero') {
					// Special case for /dev/zero: return empty content immediately
					logger.warn(
						'Detected /dev/zero input, returning empty content to avoid infinite read',
					)
					return {
						content: '',
						truncated: false,
						timedOut: false,
						bytesRead: 0,
					}
				}
				if (realPath === '/dev/random' || realPath === '/dev/urandom') {
					throw new StdinReadError(
						`Refusing to read from special device: ${realPath}. This would cause infinite data consumption.`,
					)
				}
			} catch {
				// Ignore readlink errors, continue with size-limited reading
			}
		}
	} catch {
		// Ignore fstat errors, continue with size-limited reading
	}

	return new Promise((resolve, reject) => {
		let content = ''
		let bytesRead = 0
		let truncated = false
		let timedOut = false
		let timeoutHandle: NodeJS.Timeout | null = null

		const cleanup = () => {
			if (timeoutHandle) {
				clearTimeout(timeoutHandle)
				timeoutHandle = null
			}
			process.stdin.removeAllListeners('data')
			process.stdin.removeAllListeners('end')
			process.stdin.removeAllListeners('error')
		}

		const finish = () => {
			cleanup()
			resolve({
				content,
				truncated,
				timedOut,
				bytesRead,
			})
		}

		// Set up timeout
		timeoutHandle = setTimeout(() => {
			timedOut = true
			finish()
		}, timeoutMs)

		// Handle errors
		process.stdin.once('error', (error) => {
			cleanup()
			reject(new StdinReadError('Failed to read from stdin', error))
		})

		// Handle end of stream
		process.stdin.once('end', () => {
			finish()
		})

		// Handle data chunks
		process.stdin.on('data', (chunk: Buffer) => {
			const chunkSize = chunk.length

			// Check if this chunk would exceed our size limit
			if (bytesRead + chunkSize > maxSizeBytes) {
				// Only read up to the limit
				const remainingBytes = maxSizeBytes - bytesRead
				if (remainingBytes > 0) {
					content += chunk.subarray(0, remainingBytes).toString('utf8')
					bytesRead = maxSizeBytes
				}
				truncated = true
				finish()
				return
			}

			// Process the chunk normally
			content += chunk.toString('utf8')
			bytesRead += chunkSize
		})

		// Start reading
		process.stdin.resume()
	})
}

/**
 * Legacy wrapper that matches the original readStdinContent behavior
 * but with safety protections
 */
export async function readStdinContentLegacy(): Promise<string> {
	const result = await readStdinContent()

	if (result.timedOut) {
		throw new StdinReadError('Timeout while reading from stdin')
	}

	if (result.truncated) {
		// Log a warning but continue with truncated content
		logger.warn(`Warning: stdin input was truncated at ${result.bytesRead} bytes`)
	}

	return result.content
}
