import { getClientApplication } from '../misc/known-clients'
import { platform, type PlatformInfo } from '../platform/platform'

export interface ThreadEnvironment {
	/**
	 * The root folders of the workspace where the thread is running.
	 *
	 * For example, if the thread is running in VS Code open to the root of a Git repository, this
	 * would contain the single Git repository.
	 */
	trees?: Tree[]

	/**
	 * The platform on which the thread is running.
	 */
	platform?: Pick<PlatformInfo, 'os' | 'webBrowser' | 'client'> & {
		clientVersion?: string | null
		clientType?: string
	}

	/**
	 * Whether Amp is running interactively (i.e., where the user can intervene at any step).
	 */
	interactive?: boolean

	/**
	 * Tags for any internal details about the environment that should be recorded.
	 */
	tags?: string[] | undefined
}

export function getThreadEnvPlatform(): NonNullable<ThreadEnvironment['platform']> {
	const c = getClientApplication()
	const p = platform()
	return {
		os: p.os,
		webBrowser: p.webBrowser,
		client: c.name,
		clientVersion: c.version,
		clientType: c.type,
	}
}

/**
 * A folder that the thread can run in.
 */
export interface Tree {
	/**
	 * A display name (not meaningful or unique) for the tree, such as the basename of the tree's
	 * directory.
	 */
	displayName?: string

	/**
	 * The file system path of this directory.
	 */
	fsPath?: string

	/**
	 * The repository that contains this tree, if any.
	 */
	repository?: TreeGitRepository | undefined
}

export interface TreeGitRepository {
	type: 'git'

	/**
	 * The normalized URL to the repository, such as `https://github.com/owner/repo`.
	 */
	url: string

	/**
	 * All remote URLs for the repository, with the current branch's upstream or the default
	 * upstream (if any) first.
	 */
	allURLs?: string[] | undefined

	/**
	 * The subdirectory of the tree (where the thread is running) underneath the root of the
	 * repository.
	 */
	dir?: string | undefined

	/**
	 * The current Git ref, such as `refs/heads/mybranch`.
	 */
	ref?: string | undefined

	/**
	 * The Git SHA of HEAD.
	 */
	sha?: string | undefined
}
