import { abortableOperation, startWith, Subject, type Observable } from '@sourcegraph/observable'

/**
 * Storage that can be implemented by IndexedDB, file system, an in-memory map, etc., depending on
 * the current platform.
 */
export interface Storage<T> {
	get<K extends keyof T>(key: K): Promise<T[K] | undefined>
	set<K extends keyof T>(key: K, value: T[K] | undefined): Promise<void>
	delete<K extends keyof T>(key: K): Promise<void>
	keys(): Promise<(keyof T)[]>

	path<K extends keyof T>(key: K): Promise<string | undefined>

	/** `undefined` means it is not known which keys changed. */
	changes: Observable<keyof T | undefined>
}

export function observeAll<T>(
	storage: Pick<Storage<T>, 'get' | 'keys' | 'changes'>,
): Observable<T> {
	let cache: T = {} as T
	let needInitialLoad = true
	return storage.changes.pipe(
		startWith(undefined),
		abortableOperation(async (changedKey, signal) => {
			if (changedKey === undefined || needInitialLoad) {
				const keys = await storage.keys()
				signal?.throwIfAborted()
				cache = Object.fromEntries(
					await Promise.all(keys.map(async (key) => [key, await storage.get(key)])),
				)
				signal?.throwIfAborted()
				needInitialLoad = false
			} else {
				// Otherwise, just update the specific key that changed.
				const result = await storage.get(changedKey)
				signal?.throwIfAborted()
				if (result === undefined) {
					cache = { ...cache, [changedKey]: undefined }
					delete cache[changedKey] // so Object.keys on this value doesn't include the deleted key
				} else {
					cache = { ...cache, [changedKey]: result }
				}
			}

			return cache as T
		}),
	)
}

export function inMemoryStorage<T>(initial?: T): Storage<T> {
	const data: { [K in keyof T]?: T[K] } = initial ?? {}
	const changes = new Subject<keyof T>()
	return {
		async get<K extends keyof T>(key: K): Promise<T[K] | undefined> {
			return Promise.resolve(data[key])
		},
		async set<K extends keyof T>(key: K, value: T[K]): Promise<void> {
			data[key] = value
			changes.next(key)
		},
		async delete<K extends keyof T>(key: K): Promise<void> {
			delete data[key]
			changes.next(key)
		},
		async keys(): Promise<(keyof T)[]> {
			return Promise.resolve(Object.keys(data) as (keyof T)[])
		},
		async path<K extends keyof T>(_key: K): Promise<string | undefined> {
			return Promise.resolve(undefined)
		},
		changes: changes.pipe(startWith(undefined as unknown as keyof T)),
	}
}
