const LAST_THREAD_ID_FILE = path.join(
	xdgState || path.join(process.env.HOME || process.cwd(), '.local', 'state'),
	'amp',
	'last-thread-id',
)

async function setLastThreadId(threadId: ThreadID): Promise<void> {
	try {
		await mkdir(path.dirname(LAST_THREAD_ID_FILE), { recursive: true })
		await writeFile(LAST_THREAD_ID_FILE, threadId, 'utf-8')
	} catch (error) {
		logger.debug('Failed to save last thread ID', error)
	}
}

async function getLastThreadId(): Promise<string | null> {
	try {
		const content = await readFile(LAST_THREAD_ID_FILE, 'utf-8')
		return content.trim()
	} catch (error) {
		return null
	}
}

type Context = {
	printer: Printer
	isInteractiveMode: boolean
	isTTY: boolean
	ampURL: string
}

type CLIOption =
	| {
			name: string
			type: 'flag'
			default?: boolean
			long: string
			description: (enable: boolean) => string
	  }
	| { name: string; type: 'option'; default?: boolean; long: string; description: string }

const sharedOptions: CLIOption[] = [
	{
		name: 'notifications',
		long: 'notifications',
		type: 'flag',
		default: false,
		description: (enable) =>
			`${enable ? 'Enable' : 'Disable'} sound notifications (enabled by default when interactive)`,
	},
	{
		name: 'color',
		long: 'color',
		type: 'flag',
		description: (enable) =>
			`${enable ? 'Enable' : 'Disable'} color output (enabled by default if stdout and stderr are sent to a TTY)`,
	},
	{
		name: 'settingsFile',
		long: 'settings-file',
		type: 'option',
		description: `Custom settings file path (overrides the default location ${DEFAULT_SETTINGS_FILE})`,
	},
	{
		name: 'logLevel',
		long: 'log-level',
		type: 'option',
		description: `Set log level (${Object.keys(logger).join(', ')})`,
	},
	{
		name: 'logFile',
		long: 'log-file',
		type: 'option',
		description: 'Set log file location',
	},
] as const

type OptionsFromDefinitions<T extends readonly CLIOption[]> = {
	[K in T[number] as K['name']]?: Extract<T[number], { name: K['name'] }>['type'] extends 'flag'
		? boolean
		: string
}

type SharedOptions = OptionsFromDefinitions<typeof sharedOptions>

// This is defined at build time via vite.config.ts
declare const VERSION: string

/**
 * CLI implementation of SystemPromptService that provides environment data and memory access
 */
class CLISystemPromptService implements SystemPromptService {
	async environment(): Promise<EnvironmentData> {
		const rootPath = process.cwd()
		const rootUri = URI.file(rootPath)

		const rootDirectoryListing = await getThrottledFormattedDirectoryListing(rootUri, {
			workingDirectory: rootUri,
		})
		return {
			workspacePaths: [rootPath],
			workingDirectory: rootPath,
			rootDirectoryListing,
		}
	}

	async agentFile(agentFileName: string): Promise<string | null> {
		try {
			const content = await readFile(path.join(process.cwd(), agentFileName), 'utf-8')
			return content
		} catch (error) {
			return null
		}
	}
}

type TokenUsage = {
	used: number
	maxAvailable: number
}

type CommonState = {
	tokenUsage: TokenUsage | null
	compactionPromptState: 'none' | 'first' | 'final'
	updateState: 'none' | 'checking' | 'updating' | 'success' | 'error'
}

type State = CommonState &
	(
		| { type: 'initial' }
		| { type: 'checking-agent-file' }
		| { type: 'confirming-agent-file-creation' }
		| { type: 'awaiting-user-message' }
		| {
				type: 'awaiting-agent'
				statusMessage: string
				displayedError: string | null
		  }
		| { type: 'compacting'; statusMessage: string }
		| { type: 'awaiting-user-confirmation' }
		| { type: 'out-of-credits' }
		| { type: 'confirming-compaction' }
		| { type: 'closing' }
	)

type ConfirmationType =
	| 'error-retry'
	| 'compaction'
	| 'out-of-credits'
	| 'tool-use'
	| 'agent-file-creation'

type Event =
	| { type: 'thread-state'; thread: Thread }
	| { type: 'worker-state'; status: ThreadWorkerStatus }
	| { type: 'yes-no-confirmation'; value: boolean; confirmationType: ConfirmationType }
	| { type: 'compaction-result'; result: boolean }
	| { type: 'agent-file-check-complete'; shouldGenerate: boolean; migrated: boolean }
	| { type: 'interrupt' }
	| { type: 'exit' }
	| { type: 'tick'; number: number }
	| { type: 'update-start-updating' }
	| { type: 'update-result'; success: boolean; version?: string }
	| { type: 'clear-update-status' }
	| LineEditorEvent

interface Disposable {
	dispose: () => void
}

type ThreadDependencies = {
	configService: ReturnType<typeof createConfigService>
	systemPromptService: SystemPromptService
	toolService: ReturnType<typeof createToolService>
	mcpService: ReturnType<typeof createMCPService>
	threadService: ThreadService
	threadHistoryService: ThreadHistoryService
	summaryService: ThreadSummaryService
	threadSyncService: ThreadSyncService | undefined
	threadStorage: ReturnType<typeof inMemoryStorage<Record<ThreadID, Thread>>>
	secretStorage: ReturnType<typeof createFileSecretStorage>
	settingsStorage: ReturnType<typeof createFileSettingsStorage>
	fileWatcher: Disposable
	fuzzyServer: FuzzyServerClient
}

async function createThreadDependencies(
	context: Context,
	options: SharedOptions,
): Promise<ThreadDependencies> {
	// Configure logging based on CLI arguments
	setupLogger({
		logLevel: options.logLevel || process.env.AMP_LOG_LEVEL,
		logFile: options.logFile || process.env.AMP_LOG_FILE,
	})

	setClientVersion(VERSION)

	const settingsFile = options.settingsFile ?? process.env.AMP_SETTINGS_FILE
	logger.info('Using settings file', { settingsFile })
	const secretStorage = createFileSecretStorage()
	const settingsStorage = createFileSettingsStorage({ settingsFile })
	const ampAPIKey = process.env.AMP_API_KEY
	if (ampAPIKey) {
		await secretStorage.set('apiKey', ampAPIKey, context.ampURL)
	}

	const configService = createConfigService({
		storage: settingsStorage,
		secretStorage,
		workspaceRoots: Observable.of([URI.file(process.cwd())]),
		defaultAmpURL: context.ampURL,
	})

	const systemPromptService = new CLISystemPromptService()
	const toolService = createToolService()
	const mcpService = createMCPService({ configService, toolService })
	registerBuiltinTools(toolService)

	// Initialize fuzzy server and file watcher only in interactive mode
	let fuzzyServer: FuzzyServerClient
	let fileWatcher: Disposable

	if (context.isInteractiveMode) {
		// Initialize fuzzy server for file completion
		fuzzyServer = new FuzzyServerClient()
		await fuzzyServer.start()

		// Index files in the current directory
		try {
			indexFiles(fuzzyServer, process.cwd())
		} catch (error) {
			logger.error('Failed to index files for fuzzy completion', error)
		}

		// Start watching for file changes
		fileWatcher = watchFiles(fuzzyServer, process.cwd())
	} else {
		// Create no-op implementations for non-interactive mode
		fuzzyServer = new (class extends FuzzyServerClient {
			async start(): Promise<void> {}
			async query(): Promise<string[]> {
				return []
			}
			dispose(): void {}
		})()
		fileWatcher = { dispose: () => {} }
	}

	// Check if we need to start the login flow
	{
		const apiKey = await secretStorage.get('apiKey', context.ampURL)
		if (!apiKey) {
			stdout.write('No API key found. Starting login flow...\n')
			const success = await startLoginFlow(context, secretStorage)
			if (!success) {
				process.exit(1)
			}
		}
	}

	const threadStorage = inMemoryStorage<Record<ThreadID, Thread>>()
	const threadService = new ThreadService(threadStorage)
	const threadHistoryService = new ThreadHistoryService(threadStorage)
	const summaryService = new ThreadSummaryService(threadService)

	let threadSyncService: ThreadSyncService | undefined = undefined
	logger.info('Starting Amp background services')
	{
		threadSyncService = createThreadSyncService({
			threadService,
			threadHistoryService,
			isExtensionDevelopment: false,
		})
	}

	return {
		configService,
		systemPromptService,
		toolService,
		mcpService,
		threadService,
		threadHistoryService,
		summaryService,
		threadSyncService,
		threadStorage,
		secretStorage,
		settingsStorage,
		fileWatcher,
		fuzzyServer,
	}
}

async function startLoginFlow(
	context: Context,
	secretStorage: ReturnType<typeof createFileSecretStorage>,
): Promise<boolean> {
	if (context.isInteractiveMode) {
		const confirmed = (await question('Would you like to log in to Amp?', ['y', 'n'])) == 'y'
		if (!confirmed) {
			stdout.write('Login cancelled. Run the command again to retry.\n')
			return false
		}
	}

	return await startLoginFlowNonInteractive(context, secretStorage)
}

async function startLoginFlowNonInteractive(
	context: Context,
	secretStorage: ReturnType<typeof createFileSecretStorage>,
): Promise<boolean> {
	const authToken = randomBytes(32).toString('hex')
	const loginUrl = await generateLoginLink(context.ampURL, authToken)

	stdout.write(
		`If your browser doesn't open automatically, visit:\n\n${context.printer.print(loginUrl, {
			foreground: 'blue',
			style: 'bold',
		})}\n\n`,
	)

	try {
		await openInBrowser(loginUrl)
	} catch (error) {
		stderr.write(
			`${context.printer.print('Error opening browser!', { foreground: 'red' })}\n\n`,
		)
	}

	stdout.write(`Waiting for login to complete...\n`)
	stdout.write(`Press Ctrl+C to cancel\n\n`)

	try {
		const apiKey = await waitForAPIKey(authToken)
		await completeLogin(apiKey, context.ampURL, secretStorage)
		stdout.write(`\nLogin successful! You can now use the Amp CLI.\n`)
		return true
	} catch (error) {
		stderr.write(`\nLogin failed: ${error instanceof Error ? error.message : String(error)}\n`)
		return false
	}
}

function createProgram(context: Context) {
	const program = new Command()
		.name('amp')
		.description('AI-powered coding assistant')
		.version(VERSION)
		.option('--thread-id [THREAD_ID]', 'ID of the thread to continue running', undefined)

	program.addHelpText(
		'after',
		(() => {
			let output = ''

			// Extra information only for main command
			output += context.printer.print('Environment variables:', { style: 'bold' }) + '\n\n'
			output +=
				'  ' +
				context.printer.print('AMP_API_KEY', { foreground: 'green' }) +
				'              API key for Amp (see https://ampcode.com/settings)\n'
			output +=
				'  ' +
				context.printer.print('AMP_URL', { foreground: 'green' }) +
				'                  URL for the Amp service (default is ' +
				DEFAULT_AMP_URL_STRING +
				')\n'
			output +=
				'  ' +
				context.printer.print('AMP_LOG_LEVEL', { foreground: 'green' }) +
				'            Set log level (can also use --log-level)\n'
			output +=
				'  ' +
				context.printer.print('AMP_LOG_FILE', { foreground: 'green' }) +
				'             Set log file location (can also use --log-file)\n'
			output +=
				'  ' +
				context.printer.print('AMP_SETTINGS_FILE', { foreground: 'green' }) +
				'        Set settings file path (can also use --settings-file, default: ' +
				DEFAULT_SETTINGS_FILE +
				')\n'
			output += '\n'

			output += context.printer.print('Examples:', { style: 'bold' }) + '\n\n'
			for (const example of helpContent.examples) {
				output += example.description + '\n\n'
				output +=
					'  ' + context.printer.print(example.command, { foreground: 'green' }) + '\n\n'
			}

			output += context.printer.print('Configuration:', { style: 'bold' }) + '\n\n'
			output += helpContent.configuration.description + '\n\n'
			output += context.printer.print('Sample configuration:', { style: 'bold' }) + '\n\n'
			output += helpContent.configuration.sampleConfig + '\n\n'
			output += context.printer.print('Settings reference:', { style: 'bold' }) + '\n\n'
			for (const setting of helpContent.configuration.keyDescriptions) {
				output += '  ' + context.printer.print(setting.key, { foreground: 'green' }) + '\n'
				output += '    ' + setting.description + '\n\n'
			}

			return output
		})(),
	)

	program.configureHelp({
		formatHelp: (cmd, _helper) => {
			// Helper function to format options
			const formatOptions = (options: readonly Option[], title: string) => {
				if (options.length === 0) return ''
				let output = context.printer.print(title, { style: 'bold' }) + '\n\n'
				for (const option of options) {
					const flags = option.flags.padEnd(40)
					output +=
						'  ' +
						context.printer.print(flags, { foreground: 'green' }) +
						' ' +
						option.description +
						'\n'
				}
				return output + '\n'
			}

			let output = ''

			if (cmd.parent === null) {
				output += context.printer.print('Amp CLI', { style: 'bold' }) + '\n\n'
			}

			// Build command path
			const commandPath = []
			{
				let currentCmd = cmd
				while (currentCmd.parent) {
					commandPath.unshift(currentCmd.name())
					currentCmd = currentCmd.parent
				}
				commandPath.unshift(currentCmd.name())
			}

			output +=
				context.printer.print('Usage:', { style: 'bold' }) +
				' ' +
				context.printer.print(
					`${commandPath.join(' ')} [options]${cmd.commands.length > 0 ? ' [subcommand]' : ''}`,
					{
						foreground: 'green',
					},
				) +
				'\n\n'

			if (cmd.parent !== null && cmd.description()) {
				output += cmd.description() + '\n\n'
			}

			// Show subcommand-specific options first
			output += formatOptions(cmd.options, 'Options:')

			// Show global options from parent command
			if (cmd.parent) {
				output += formatOptions(cmd.parent.options, 'Global options:')
			}

			// Commands
			const commands = cmd.commands
			if (commands.length > 0) {
				output += context.printer.print('Commands:', { style: 'bold' }) + '\n\n'

				function formatCommandTree(
					commands: readonly Command[],
					indent: string = '  ',
				): string {
					let result = ''
					for (const command of commands) {
						const name = command.name().padEnd(Math.max(30 - indent.length, 10))
						result +=
							indent +
							context.printer.print(name, { foreground: 'green' }) +
							command.description() +
							'\n'

						// Recursively format subcommands
						if (command.commands && command.commands.length > 0) {
							result += formatCommandTree(command.commands, indent + '  ')
						}
					}
					return result
				}

				output += formatCommandTree(commands)
				output += '\n'
			}

			return output
		},
	})

	program
		.command('logout')
		.description('Log out by removing stored API key')
		.action(async (_, cmd) => {
			const opts = cmd.optsWithGlobals() as SharedOptions
			const context = createContext(opts)
			const secretStorage = createFileSecretStorage()

			// Check if already logged out
			const existingKey = await secretStorage.get('apiKey', context.ampURL)
			if (!existingKey) {
				stdout.write('Already logged out.\n')
				process.exit(0)
			}

			// Remove the API key by setting it to an empty value
			await secretStorage.set('apiKey', '', context.ampURL)
			stdout.write('Successfully logged out.\n')
			process.exit(0)
		})

	program
		.command('login')
		.description('Log in to Amp')
		.action(async (_, cmd) => {
			const opts = cmd.optsWithGlobals() as SharedOptions
			const context = createContext(opts)
			const secretStorage = createFileSecretStorage()

			const existingKey = await secretStorage.get('apiKey', context.ampURL)
			if (existingKey) {
				stdout.write('Already logged in.\n')
				const reply = await question('Do you want to log in again?', ['y', 'n'])
				if (reply === 'n') {
					process.exit(0)
				}
				stdout.write('\n')
			}

			const success = await startLoginFlowNonInteractive(context, secretStorage)
			process.exit(success ? 0 : 1)
		})

	// Threads command with subcommands
	const threadsCommand = program.command('threads').description('Thread management commands')

	threadsCommand
		.command('new')
		.description('Create a new thread and print its ID')
		.action(async (_, cmd) => {
			const opts = cmd.optsWithGlobals() as SharedOptions
			const context = createContext(opts)
			const deps = await createThreadDependencies(context, opts)

			try {
				const threadID = newThreadID()

				// Create an empty thread
				const emptyThread: Thread = {
					id: threadID,
					messages: [],
					created: Date.now(),
					v: 0,
				}

				// Store the thread locally
				deps.threadStorage.set(threadID, emptyThread)

				// Directly upload the empty thread to server (bypassing sync service's empty thread check)
				const resp = await fetchFromAmpServer(`/api/threads/${threadID}`, {
					method: 'POST',
					body: JSON.stringify(emptyThread),
				})
				if (!resp.ok) {
					const responseText = await resp
						.text()
						.catch(() => 'Unable to read response body')
					throw new Error(
						`Failed to create thread on server: ${resp.status}${responseText ? `: ${responseText}` : ''}`,
					)
				}

				// Save this as the last used thread
				await setLastThreadId(threadID)

				// Print thread ID to stdout and exit
				stdout.write(`${threadID}\n`)

				// Clean up services
				deps.threadSyncService?.dispose()
				deps.threadService.dispose()
				await deps.mcpService.dispose()

				process.exit(0)
			} catch (error) {
				stderr.write(
					`Error creating thread: ${error instanceof Error ? error.message : String(error)}\n`,
				)
				process.exit(1)
			}
		})

	threadsCommand
		.command('continue [threadId]')
		.description('Continue an existing thread (uses last used thread if no ID provided)')
		.action(async (threadId, _, cmd) => {
			const opts = cmd.optsWithGlobals() as SharedOptions
			const context = createContext(opts)

			// Get thread ID from argument or last used thread
			let resolvedThreadId = threadId
			if (!resolvedThreadId) {
				resolvedThreadId = await getLastThreadId()
				if (!resolvedThreadId) {
					stderr.write(
						context.printer.print('Error: ', { foreground: 'red', style: 'bold' }) +
							'No thread ID provided and no previously used thread found.\n' +
							'Provide a thread ID as an argument or run a thread first.\n',
					)
					process.exit(1)
				}
			}

			await runMainThread(context, { ...opts, threadId: resolvedThreadId })
		})

	threadsCommand
		.command('fork [threadId]')
		.description(
			'Create a new thread by forking an existing one and print its ID (uses last used thread if no ID provided)',
		)
		.action(async (threadId, _, cmd) => {
			const opts = cmd.optsWithGlobals() as SharedOptions
			const context = createContext(opts)
			const deps = await createThreadDependencies(context, opts)

			try {
				let sourceThreadId = threadId
				if (!sourceThreadId) {
					sourceThreadId = await getLastThreadId()
					if (!sourceThreadId) {
						stderr.write(
							context.printer.print('Error: ', { foreground: 'red', style: 'bold' }) +
								'No thread ID provided and no previously used thread found.\n' +
								'Provide a thread ID as an argument or run a thread first.\n',
						)
						process.exit(1)
					}
				}

				if (!isThreadID(sourceThreadId)) {
					stderr.write('Invalid thread ID given.\n')
					process.exit(1)
				}

				const response = await fetchFromAmpServer(`/api/threads/${sourceThreadId}`)
				if (!response.ok) {
					stderr.write(
						`Failed to load thread ${sourceThreadId}: ${await response.text()}\n`,
					)
					process.exit(1)
				}

				const sourceThread = (await response.json()) as Thread
				const forkedThreadID = newThreadID()

				const forkedThread: Thread = {
					id: forkedThreadID,
					messages: sourceThread.messages,
					created: Date.now(),
					v: 0,
				}

				deps.threadStorage.set(forkedThreadID, forkedThread)

				const resp = await fetchFromAmpServer(`/api/threads/${forkedThreadID}`, {
					method: 'POST',
					body: JSON.stringify(forkedThread),
				})
				if (!resp.ok) {
					const responseText = await resp
						.text()
						.catch(() => 'Unable to read response body')
					throw new Error(
						`Failed to create forked thread on server: ${resp.status}${responseText ? `: ${responseText}` : ''}`,
					)
				}

				await setLastThreadId(forkedThreadID)
				stdout.write(`${forkedThreadID}\n`)

				deps.threadSyncService?.dispose()
				deps.threadService.dispose()
				await deps.mcpService.dispose()

				process.exit(0)
			} catch (error) {
				stderr.write(
					`Error forking thread: ${error instanceof Error ? error.message : String(error)}\n`,
				)
				process.exit(1)
			}
		})

	threadsCommand
		.command('list')
		.description('List all your threads with their titles and share status')
		.action(async (_, cmd) => {
			const opts = cmd.optsWithGlobals() as SharedOptions
			const context = createContext(opts)
			const deps = await createThreadDependencies(context, opts)

			try {
				const response = await fetchFromAmpServer('/api/threads')
				if (!response.ok) {
					stderr.write(
						`Failed to load threads: ${response.status} ${await response.text()}\n`,
					)
					process.exit(1)
				}

				type DBThreadSummary = {
					id: ThreadID
					title?: string
					meta: { private: boolean; public: boolean }
					messageCount: number
					updatedAt: Date
				}

				const threads = (await response.json()) as DBThreadSummary[]

				if (threads.length === 0) {
					stdout.write('No threads found.\n')
					process.exit(0)
				}

				const headers = ['Title', 'Last Updated', 'Visibility', 'Messages', 'Thread ID']
				const rows = threads.map((thread) => {
					const title = thread.title || 'Untitled'
					let shareStatus = 'Team'
					if (thread.meta.private) {
						shareStatus = 'Private'
					} else if (thread.meta.public) {
						shareStatus = 'Public'
					}

					const updatedDate = formatTimeAgoShort(new Date(thread.updatedAt))

					return [
						title,
						updatedDate,
						shareStatus,
						thread.messageCount.toString(),
						thread.id,
					]
				})

				printTable(headers, rows, {
					columnFormatters: [
						(text, width) => {
							const truncated =
								text.length > width ? text.substring(0, width - 3) + '...' : text
							return truncated.padEnd(width)
						},
						undefined,
						undefined,
						(text, width) => text.padStart(width),
						(text, width) =>
							context.printer.print(text.padEnd(width), { foreground: 'green' }),
					],
					truncateColumnIndex: 0,
				})

				deps.threadSyncService?.dispose()
				deps.threadService.dispose()
				await deps.mcpService.dispose()

				process.exit(0)
			} catch (error) {
				stderr.write(
					`Error listing threads: ${error instanceof Error ? error.message : String(error)}\n`,
				)
				process.exit(1)
			}
		})

	// Tools command with subcommands
	const toolsCommand = program.command('tools').description('Tool management commands')

	toolsCommand
		.command('show')
		.description('Show available tools')
		.action(async (_, cmd) => {
			try {
				const opts = cmd.optsWithGlobals() as SharedOptions
				const context = createContext(opts)

				// Configure logging with minimal setup
				setupLogger({})

				const toolService = createToolService()
				registerBuiltinTools(toolService)

				const tools = await firstValueFrom(toolService.tools)
				stderr.write(`# ${tools.length} tools\n`)
				stderr.write('\n')
				for (const tool of tools) {
					stderr.write(`## ${tool.spec.name}${!tool.enabled ? ' (disabled)' : ''}\n`)
					if (tool.spec.description) {
						stderr.write(
							context.printer.print(`${tool.spec.description}`, {
								foreground: 'gray',
							}) + '\n',
						)
						stderr.write('\n')
					}
					stderr.write(
						context.printer.print(
							`Input schema: ${JSON.stringify(tool.spec.inputSchema, null, 2)}`,
							{
								foreground: 'gray',
							},
						) + '\n',
					)
					stderr.write('\n')
				}
			} catch (error) {
				stderr.write(`Error: ${error}\n`)
				process.exit(1)
			}
		})

	// Add global options (available to all commands)
	for (const opt of sharedOptions) {
		if (opt.type === 'flag') {
			program.option(`--${opt.long}`, opt.description(true))
			program.option(`--no-${opt.long}`, opt.description(false))
		} else {
			program.option(`--${opt.long} <value>`, opt.description, opt.default)
		}
	}

	// Default action
	program.action(async (options) => {
		const opts = options as SharedOptions & { threadId?: string }
		const context = createContext(opts)

		// Check for deprecated --thread-id option
		if ((options as any).threadId !== undefined) {
			stderr.write(
				context.printer.print('Error: ', { foreground: 'red', style: 'bold' }) +
					'The --thread-id option has been deprecated.\n' +
					'Use ' +
					context.printer.print('amp threads continue [THREAD_ID]', {
						foreground: 'green',
					}) +
					' instead.\n',
			)
			process.exit(1)
		}

		await runMainThread(context, opts)
	})

	return program
}

async function runMainThread(context: Context, args: SharedOptions & { threadId?: string }) {
	const pipedInput = await readStdinContentLegacy()

	const useNotifications =
		args.notifications !== undefined ? args.notifications : context.isInteractiveMode

	// Get all thread dependencies
	const {
		configService,
		systemPromptService,
		toolService,
		mcpService,
		threadService,
		summaryService,
		threadSyncService,
		threadStorage,
		settingsStorage,
		fileWatcher,
		fuzzyServer,
	} = await createThreadDependencies(context, args)

	let threadID = newThreadID()
	const printedMessageIndexes = new Set<number>()
	if (args.threadId) {
		if (!isThreadID(args.threadId)) {
			stderr.write('Invalid thread ID given.\n')
			process.exit(1)
		}
		threadID = args.threadId
		try {
			const response = await fetchFromAmpServer(`/api/threads/${threadID}`)
			if (!response.ok) {
				stderr.write(`Failed to load thread ${threadID}: ${await response.text()}\n`)
				process.exit(0)
			}

			const thread = (await response.json()) as Thread
			threadStorage.set(threadID, thread)
			thread.messages.map((_, idx) => printedMessageIndexes.add(idx))
		} catch (error) {
			logger.error('Failed to load thread', error)
			process.exit(0)
		}
	}

	// Save this as the last used thread
	await setLastThreadId(threadID)

	await new Promise((resolve, reject) => {
		threadSyncService?.threadSyncInfo([threadID]).subscribe(
			(synced) => {
				if (!synced[threadID]) {
					reject(new Error(`Thread ${threadID} not found`))
					return
				}
				resolve(synced[threadID])
			},
			(error) => reject(error),
		)
	})

	const thread = storeLastValue(
		threadService.observe(threadID).pipe(filter((thread) => thread !== undefined)),
	)
	const printedToolRuns = new Set<ThreadToolUseID>()

	await firstValueFrom(thread.observable)
	const worker = await ThreadWorker.getOrCreateForThread(
		{
			configService,
			threadService,
			toolService,
			getThreadEnvironment,
			systemPromptService,
			threadSummaryService: summaryService,
			createFileSystem: await PLATFORM_FILE_SYSTEM_CREATOR,
		},
		threadID,
	)

	await worker.resume().catch((err) => {
		logger.error('Initial resume', err)
	})

	firstValueFrom(worker.status.pipe(filter((status) => status.state === 'active'))).then(
		async () => {
			if (!context.isInteractiveMode && pipedInput) {
				worker
					.handle({
						type: 'user:message',
						message: { content: [{ type: 'text', text: pipedInput }] },
					})
					.catch((err) => {
						logger.error('Handle piped message failed', err)
					})

				if (threadSyncService) {
					threadSyncService.sync().catch((error) => {
						logger.error(`Thread sync error`, error, { threadID })
					})
				}
			}
		},
	)

	const notify: () => void = useNotifications ? () => playNotificationSound('idle') : () => {}

	async function complete(pattern: string): Promise<string[]> {
		try {
			return await fuzzyServer.query(pattern)
		} catch (error) {
			logger.error('Completion failed', error)
			return []
		}
	}

	const historyProvider = context.isInteractiveMode ? new FileHistoryProvider() : undefined

	const lineEditor = context.isInteractiveMode
		? createLineEditor({
				prompt: '> ',
				complete,
				printer: context.printer,
				history: historyProvider,
			})
		: null
	const lineEditorEvents: Observable<Event> = lineEditor ? lineEditor.observable : Observable.of()

	const workerStates: Observable<Event> = worker.status.pipe(
		map((status) => {
			return { type: 'worker-state', status }
		}),
	)

	const threadStates: Observable<Event> = thread.observable.pipe(
		map((thread) => {
			return { type: 'thread-state', thread }
		}),
	)

	const tickEvents: Observable<Event> = interval(200).pipe(
		map((number) => ({
			type: 'tick',
			number,
		})),
	)

	const interruptEvents: Observable<Event> = fromEvent<Buffer>(process.stdin, 'data').pipe(
		filter((buffer) => {
			return buffer.equals(ESC) || buffer.equals(CTRL_C) || buffer.equals(kitty.CTRL_C)
		}),
		map(() => ({ type: 'interrupt' })),
	)

	const triggeredEvents = new Subject<Event>()

	const events: Observable<Event> = merge(
		tickEvents,
		workerStates,
		threadStates,
		lineEditorEvents,
		// merge only takes 5 args so have to nest them
		merge(interruptEvents, triggeredEvents),
	)

	async function exit() {
		kitty.disable()
		stderr.write('Shutting down...\n')
		await threadSyncService?.sync().catch((error) => {
			logger.error(`Thread sync error`, error, { threadID })
		})
		worker.dispose()
		if (threadSyncService) {
			threadSyncService.dispose()
		}
		threadService.dispose()
		await mcpService.dispose()
		fileWatcher.dispose()
		fuzzyServer.dispose()
		process.exit(0)
	}

	if (context.isInteractiveMode) {
		kitty.enable()

		stderr.write(
			`${context.printer.print('Type ', { foreground: 'gray' })}${context.printer.print('\\', { foreground: 'gray', style: 'bold' })}${context.printer.print(' followed by ', { foreground: 'gray' })}${context.printer.print('Enter', { foreground: 'gray', style: 'bold' })}${context.printer.print(' to insert newlines, or use ', { foreground: 'gray' })}${context.printer.print('Shift+Enter', { foreground: 'gray', style: 'bold' })}${context.printer.print(' in supported terminals.\nInterrupt the agent with ', { foreground: 'gray' })}${context.printer.print('Ctrl+C', { foreground: 'gray', style: 'bold' })}${context.printer.print(', exit with ', { foreground: 'gray' })}${context.printer.print('Ctrl+D', { foreground: 'gray', style: 'bold' })}${context.printer.print('.', { foreground: 'gray' })}\n\n`,
		)
	}

	const stateMachine = events
		.pipe(
			finalize(() => {
				exit()
			}),
		)
		.pipe(
			reduce(
				logStateMachine(function (state: State, event: Event): State {
					async function compactThread() {
						const summary = await summaryService.summarizeThread(threadID)
						await worker.handle({
							type: 'summary:created',
							summary: { type: 'internal', summary },
						})
					}
					function acceptNextMessage(): State {
						if (state.type === 'awaiting-user-message') {
							logger.debug('already accepting, ignoring event', { state, event })
							return state
						}

						const tokenUsage = 'tokenUsage' in state ? state.tokenUsage : undefined

						threadSyncService?.sync()?.catch((error) => {
							logger.error(`Thread sync error`, error, { threadID })
						})
						if (context.isInteractiveMode) {
							if (tokenUsage && state.type !== 'confirming-compaction') {
								const used = tokenUsage.used / tokenUsage.maxAvailable
								let compactQuestion: string | undefined = undefined
								let compactionPromptState = state.compactionPromptState

								if (
									used > TOKEN_WARNING_THRESHOLD &&
									state.compactionPromptState === 'none'
								) {
									compactQuestion = context.printer.print(
										`You're running out of tokens. Compact thread?`,
										{ foreground: 'yellow' },
									)
									compactionPromptState = 'first'
								} else if (
									used > TOKEN_DANGER_THRESHOLD &&
									state.compactionPromptState === 'first'
								) {
									compactQuestion = context.printer.print(
										`You're running out of tokens. Compact thread?`,
										{ foreground: 'red' },
									)
									compactionPromptState = 'final'
								}

								if (compactQuestion) {
									clearProgressIndicator()
									if (useNotifications) {
										playNotificationSound('requires-user-input')
									}
									question(compactQuestion, ['y', 'n']).then((answer) => {
										stdout.write('\n')
										triggeredEvents.next({
											type: 'yes-no-confirmation',
											value: answer == 'y',
											confirmationType: 'compaction',
										})
									})
									return {
										type: 'confirming-compaction',
										tokenUsage: null, // from here we consider the token usage to be unknown
										compactionPromptState,
										updateState: state.updateState,
									}
								}
							}

							clearProgressIndicator()
							lineEditor?.acceptMessage()

							// Display token usage in the line editor's right status if available
							if (tokenUsage) {
								lineEditor?.setRightStatus(
									formatTokenUsage(tokenUsage, context.printer),
								)
							} else {
								lineEditor?.setRightStatus('')
							}

							return { ...state, type: 'awaiting-user-message' }
						} else {
							stateMachine.unsubscribe()
							return { ...state, type: 'closing' }
						}
					}

					if (state.type === 'closing') {
						return state
					}

					if (event.type === 'line-editor-state') {
						if (event.state == 'off' && state.type === 'initial') {
							// Check if this is a new thread and we should check for agent file
							const isNewThread = thread.value.last?.messages.length === 0
							if (context.isInteractiveMode && isNewThread && !args.threadId) {
								// Start agent file check process
								checkAgentFileStatus()
									.then((result) => {
										triggeredEvents.next({
											type: 'agent-file-check-complete',
											shouldGenerate: result.shouldGenerate,
											migrated: result.migrated,
										})
									})
									.catch((error) => {
										logger.error('Agent file check failed', error)
										triggeredEvents.next({
											type: 'agent-file-check-complete',
											shouldGenerate: false,
											migrated: false,
										})
									})
								return { ...state, type: 'checking-agent-file' }
							} else {
								// Normal flow - go directly to accepting messages
								lineEditor?.acceptMessage()
								return { ...state, type: 'awaiting-user-message' }
							}
						}
					} else if (event.type == 'submit') {
						const { message: userMessage } = event
						logger.info(
							`Processing user message: ${userMessage.slice(0, 50)}${userMessage.length > 50 ? '...' : ''}`,
						)
						worker
							.handle({
								type: 'user:message',
								message: { content: [{ type: 'text', text: userMessage }] },
							})
							.then(() => {
								if (threadSyncService) {
									threadSyncService.sync().catch((error) => {
										logger.error(`Thread sync after submit error`, error, {
											threadID,
										})
									})
								}
							})
							.catch((err) => {
								logger.error('Handle user message failed', err)
							})

						return {
							...state,
							type: 'awaiting-agent',
							statusMessage: 'Thinking...',
							displayedError: null,
						}
					} else if (event.type === 'thread-state') {
						if (state.type === 'awaiting-user-message') {
							if (!context.isInteractiveMode) {
								stateMachine.unsubscribe()
								return { ...state, type: 'closing' }
							}
						}
						if (state.type === 'awaiting-agent') {
							if (
								!context.isInteractiveMode &&
								pipedInput === '' &&
								event.thread.messages.length === 0
							) {
								stateMachine.unsubscribe()
								return { ...state, type: 'closing' }
							}
						}

						// Extract token usage from thread debug info
						let tokenUsage = state.tokenUsage
						const usage = event.thread?.['~debug']?.lastInferenceUsage
						if (usage) {
							tokenUsage = {
								used: usage.totalInputTokens,
								maxAvailable: usage.maxInputTokens,
							}
						}

						// tool was confirmed
						if (state.type === 'awaiting-user-confirmation') {
							if (
								event.thread.messages
									.at(-1)
									?.content.some(
										(b): b is ToolResultBlock =>
											b.type === 'tool_result' &&
											(b.run.status === 'in-progress' ||
												b.run.status === 'done'),
									)
							) {
								return {
									...state,
									type: 'awaiting-agent',
									statusMessage: event.thread.messages.some(
										(msg) =>
											msg.role === 'assistant' &&
											msg.state.type === 'streaming',
									)
										? 'Thinking...'
										: 'Working...',
									displayedError: null,
									tokenUsage,
								}
							} else {
								return {
									...state,
									type: 'awaiting-user-confirmation',
									tokenUsage,
								}
							}
						}

						if (state.type === 'awaiting-agent' || state.type === 'compacting') {
							const lastMessage = event.thread.messages.at(-1)

							const filteredThread = filterPrintedThread(
								event.thread,
								printedMessageIndexes,
								printedToolRuns,
							)
							if (
								filteredThread.messages.length > 0 ||
								filteredThread.toolResults.length > 0
							) {
								clearProgressIndicator()
								const terminalHeight = process.stdout.rows || 24
								const maxToolResultHeight = Math.max(
									Math.floor(terminalHeight / 2),
									5,
								)
								printThreadToConsole(
									filteredThread,
									(text) => stdout.write(text + '\n'),
									stdout.columns || 80,
									context.printer,
									maxToolResultHeight,
								)
							}

							// summary message means end of compaction
							if (
								event.thread.messages
									.at(-1)
									?.content.some((b) => b.type === 'summary')
							) {
								const nextState = acceptNextMessage()
								return {
									...nextState,
									tokenUsage: null,
									compactionPromptState: 'none',
								}
							}

							// end_turn, wait for user input
							if (
								lastMessage?.role === 'assistant' &&
								lastMessage?.state.type === 'complete' &&
								lastMessage?.state.stopReason === 'end_turn'
							) {
								const nextState = acceptNextMessage()
								return {
									...nextState,
									tokenUsage: tokenUsage ?? nextState.tokenUsage,
								}
							}

							if (!thread) {
								throw new Error(
									'unexpected: thread is required when waiting for tool input',
								)
							}

							// In non-interactive mode, auto-reject tool use that requires confirmation
							if (!context.isInteractiveMode) {
								const resultBlock = lastMessageOfRole(
									event.thread,
									'user',
								)?.content.find(
									(b): b is ToolResultBlock =>
										b.type === 'tool_result' &&
										b.run.status === 'blocked-on-user',
								)
								if (!resultBlock) {
									return { ...state, tokenUsage }
								}
								const useBlock = toolUseBlock(event.thread, resultBlock.toolUseID)
								if (useBlock) {
									clearProgressIndicator()

									stderr.write(
										`\nThe ${context.printer.print(useBlock.name, { foreground: 'green' })} tool call is not allowed by default.\n`,
									)

									// Get the actual command for a Bash command invocation
									if (
										useBlock.name === BASH_TOOL_NAME &&
										useBlock.input &&
										typeof useBlock.input === 'object' &&
										'cmd' in useBlock.input &&
										resultBlock.run.status === 'blocked-on-user'
									) {
										stderr.write(
											`To allow it automatically, add an entry to ${context.printer.print(DEFAULT_SETTINGS_FILE, { foreground: 'blue' })} like the following:\n`,
										)

										stderr.write(
											`\n{\n  ${context.printer.print(
												`"amp.commands.allowlist": ${JSON.stringify(resultBlock.run.toAllow ?? [useBlock.input?.cmd])}`,
												{ foreground: 'green', style: 'bold' },
											)}\n}\n\n`,
										)
									}

									// Automatically reject the tool use
									worker
										.handle({
											type: 'user:tool-input',
											toolUse: useBlock.id,
											value: { accepted: false },
										})
										.catch((err) => {
											logger.error('Automatic reject failed', err)
										})
									return { ...state, type: 'awaiting-user-message', tokenUsage }
								}
							}

							const resultBlock = lastMessageOfRole(
								event.thread,
								'user',
							)?.content.find(
								(b): b is ToolResultBlock =>
									b.type === 'tool_result' && b.run.status === 'blocked-on-user',
							)
							if (!resultBlock) {
								return state
							}
							const run = resultBlock.run
							const useBlock = toolUseBlock(event.thread, resultBlock.toolUseID)
							if (
								useBlock &&
								useBlock.name === BASH_TOOL_NAME &&
								run.status === 'blocked-on-user'
							) {
								clearProgressIndicator()
								;(async () => {
									if (useNotifications) {
										playNotificationSound('requires-user-input')
									}
									const answer = await question(
										printConfirmation(context.printer, useBlock),
										['n', 'y', '!'],
									)
									stdout.write('\n')

									if (answer === '!') {
										const toAppendToAllowlist = run.toAllow ?? [] // ignore legacy thread tool calls
										if (toAppendToAllowlist.length > 0) {
											const allowlist = await settingsStorage.get(
												'commands.allowlist',
												'global',
											)
											const newAllowlist = [
												...(allowlist ?? []),
												...toAppendToAllowlist,
											]
											await settingsStorage.set(
												'commands.allowlist',
												newAllowlist,
												'global', // TODO: set in workspace when supported
											)
										}
									}

									await worker
										.handle({
											type: 'user:tool-input',
											toolUse: useBlock.id,
											value: {
												accepted: answer === 'y' || answer === '!',
											},
										})
										.catch((err) => {
											logger.error('User confirmation send failed', err)
										})
								})()
								return {
									...state,
									type: 'awaiting-user-confirmation',
									tokenUsage,
								}
							}
						}
					} else if (event.type === 'worker-state') {
						// Skip error handling if we're already in a confirmation state
						if (
							state.type === 'awaiting-user-confirmation' &&
							event.status.state === 'active' &&
							event.status.ephemeralError
						) {
							return state
						}

						const lastMessage = thread.value.last?.messages.at(-1)
						let displayedError: string | null = null
						let tokenUsage = state.tokenUsage

						if (event.status.state === 'active') {
							const usage = thread.value.last?.['~debug']?.lastInferenceUsage
							if (usage) {
								tokenUsage = {
									used: usage.totalInputTokens,
									maxAvailable: usage.maxInputTokens,
								}
							}
						}

						switch (event.status.state) {
							case 'initial':
								return {
									...state,
									type: 'awaiting-user-message',
									tokenUsage,
								}
							case 'active':
								if (event.status.ephemeralError) {
									if (
										event.status.ephemeralError.message.includes(
											outOfCreditsErrorMessage,
										)
									) {
										if (state.type === 'out-of-credits') {
											return state
										}
										clearProgressIndicator()
										stdout.write(
											context.printer.print('Out of credits. ', {
												foreground: 'red',
											}) +
												'\n\nVisit ' +
												context.printer.print(
													new URL('/settings', context.ampURL).toString(),
													{
														foreground: 'blue',
														style: 'bold',
													},
												) +
												' to purchase more credits and continue using Amp.\n\n',
										)

										if (useNotifications) {
											playNotificationSound('requires-user-input')
										}
										question('Retry?', ['y', 'n']).then((answer) => {
											stdout.write('\n')
											if (answer === 'y') {
												triggeredEvents.next({
													type: 'yes-no-confirmation',
													value: true,
													confirmationType: 'out-of-credits',
												})
											} else {
												stateMachine.unsubscribe()
											}
										})

										return {
											...state,
											type: 'out-of-credits',
											tokenUsage,
										}
									} else if (isContextLimitError(event.status.ephemeralError)) {
										clearProgressIndicator()
										// TODO(owickstrom): implement compact (and maybe fork, but could
										// it be weird in to do interactively in the CLI?)
										displayedError = context.printer.print(
											'The context is full. Please start a new thread.',
											{
												foreground: 'red',
											},
										)
									} else if (
										event.status.ephemeralError.message.includes(
											'401 unauthorized',
										)
									) {
										clearProgressIndicator()
										const apiKeySource = process.env.AMP_API_KEY
											? 'environment variable AMP_API_KEY'
											: `secrets file (${SECRETS_PATH})`
										stderr.write(
											context.printer.print(
												`Unauthorized: API key from ${apiKeySource} may be invalid or expired. See https://ampcode.com/settings`,
												{
													foreground: 'red',
												},
											) + '\n\n',
										)
										stateMachine.unsubscribe()
										return { ...state, type: 'closing' }
									} else {
										logger.error('Ephemeral error', {
											error: event.status.ephemeralError,
										})
										clearProgressIndicator()
										displayedError = context.printer.print(
											event.status.ephemeralError.message,
											{
												foreground: 'red',
											},
										)

										// Only show error prompt and confirmation if we're awaiting agent response (not already confirming)
										if (
											state.type === 'awaiting-agent' &&
											state.displayedError !== displayedError
										) {
											stderr.write(displayedError + '\n\n')
											question('Retry?', ['y', 'n']).then((answer) => {
												stdout.write('\n')
												triggeredEvents.next({
													type: 'yes-no-confirmation',
													value: answer === 'y',
													confirmationType: 'error-retry',
												})
											})
											return {
												...state,
												type: 'awaiting-user-confirmation',
												tokenUsage,
											}
										} else {
											// If error message hasn't changed, just return current state
											return state
										}
									}
								}
								switch (event.status.inferenceState) {
									case 'running':
										return {
											...state,
											type: 'awaiting-agent',
											statusMessage: thread.value.last?.messages.some(
												(msg) =>
													msg.role === 'assistant' &&
													msg.state.type === 'streaming',
											)
												? 'Thinking...'
												: 'Working...',
											displayedError,
											tokenUsage,
										}
									case 'cancelled':
										return { ...acceptNextMessage(), tokenUsage }
									case 'idle':
										if (
											lastMessage?.content.some(
												(b) =>
													b.type === 'tool_result' &&
													b.run.status === 'blocked-on-user',
											)
										) {
											return {
												...state,
												type: 'awaiting-user-confirmation',
												tokenUsage,
											}
										} else if (
											thread.value.last?.messages.some(
												(msg) =>
													msg.role === 'user' &&
													msg.content.some(
														(block) =>
															block.type === 'tool_result' &&
															block.run.status === 'in-progress',
													),
											) ??
											false
										) {
											// Check for active tools to show appropriate status message
											const activeToolRun = thread.value.last?.messages
												.flatMap(
													(
														msg,
													): Array<
														TextBlock | ImageBlock | ToolResultBlock
													> => msg.content as any,
												)
												.filter(
													(b): b is ToolResultBlock =>
														b.type === 'tool_result' &&
														'run' in b &&
														'status' in b.run &&
														b.run.status === 'in-progress',
												)
												.pop()

											// Show tool progress if available
											return {
												...state,
												type: 'awaiting-agent',
												statusMessage: activeToolRun
													? 'Running tool...'
													: thread.value.last?.messages.some(
																(msg) =>
																	msg.role === 'assistant' &&
																	msg.state.type === 'streaming',
														  )
														? 'Thinking...'
														: 'Working...',
												displayedError,
												tokenUsage,
											}
										} else if (
											lastMessage?.role === 'assistant' &&
											lastMessage?.state.type === 'complete' &&
											lastMessage?.state.stopReason === 'end_turn'
										) {
											notify()
											return { ...acceptNextMessage(), tokenUsage }
										}
								}
						}
					} else if (event.type === 'yes-no-confirmation') {
						if (
							state.type === 'out-of-credits' &&
							event.confirmationType === 'out-of-credits'
						) {
							ThreadWorker.retry(threadID)
						} else if (
							state.type === 'awaiting-user-confirmation' &&
							event.value &&
							event.confirmationType === 'error-retry'
						) {
							// User confirmed retry for ephemeral error
							ThreadWorker.retry(threadID)
							return {
								...state,
								type: 'awaiting-agent',
								statusMessage: 'Retrying...',
								displayedError: null,
							}
						} else if (
							state.type === 'awaiting-user-confirmation' &&
							!event.value &&
							event.confirmationType === 'error-retry'
						) {
							// User declined retry, go back to accepting messages
							stdout.write('\n')
							return acceptNextMessage()
						} else if (
							state.type === 'confirming-compaction' &&
							event.confirmationType === 'compaction'
						) {
							if (event.value) {
								compactThread()
									.then(() => {
										triggeredEvents.next({
											type: 'compaction-result',
											result: true,
										})
									})
									.catch((error) => {
										logger.error('Compaction failed', error)
										triggeredEvents.next({
											type: 'compaction-result',
											result: false,
										})
									})
								return {
									...state,
									type: 'compacting',
									statusMessage: 'Compacting...',
								}
							} else {
								logger.info('Compaction cancelled')
								return acceptNextMessage()
							}
						} else if (
							event.confirmationType === 'agent-file-creation' &&
							state.type === 'confirming-agent-file-creation'
						) {
							if (event.value) {
								// User confirmed, generate the file
								stdout.write('\nGenerating AGENT.md file...\n\n')
								generateAgentFile(worker).catch((err) => {
									logger.error('Agent file generation failed', err)
								})
								return {
									...state,
									type: 'awaiting-agent',
									statusMessage: 'Generating AGENT.md...',
									displayedError: null,
								}
							} else {
								// User declined, continue to normal flow
								lineEditor?.acceptMessage()
								return { ...state, type: 'awaiting-user-message' }
							}
						}
					} else if (event.type === 'compaction-result') {
						if (event.result) {
							return state
						} else {
							question(
								context.printer.print(`Compaction failed! Retry?`, {
									foreground: 'red',
								}),
								['y', 'n'],
							).then((value) => {
								stdout.write('\n')
								triggeredEvents.next({
									type: 'yes-no-confirmation',
									value: value == 'y',
									confirmationType: 'compaction',
								})
							})
							return { ...state, type: 'confirming-compaction' }
						}
					} else if (event.type === 'interrupt') {
						if (state.type === 'awaiting-agent') {
							clearProgressIndicator()
							stdout.write(context.printer.print('Cancelled', { foreground: 'gray' }))
							stdout.write('\n')
							stdout.write('\n')
							ThreadWorker.cancel(threadID)
							return state
						} else if (state.type === 'awaiting-user-message') {
							// Ignore interrupts when waiting for user input. Maybe we wanna print a message here
							// about how to exit.
							return state
						} else if (state.type === 'awaiting-user-confirmation') {
							return state
						} else {
							// todo: some confirmation before forced exit
							logger.info('Force exit in state', { state })
							stateMachine.unsubscribe()
							clearProgressIndicator()
							return { ...state, type: 'closing' }
						}
					} else if (event.type === 'exit') {
						if (state.type === 'awaiting-user-message') {
							stateMachine.unsubscribe()
							return { ...state, type: 'closing' }
						}
					} else if (event.type === 'tick') {
						if (state.type === 'awaiting-agent' || state.type === 'compacting') {
							renderProgressIndicator(
								context.printer,
								event.number,
								state.statusMessage,
								state.tokenUsage ?? undefined,
							)
							return state
						} else if (state.type === 'awaiting-user-message') {
							// Update token usage and update status display in line editor during user input
							const rightStatus = formatRightStatus(state, context.printer)
							lineEditor?.setRightStatus(rightStatus)
							return state
						}
					} else if (event.type === 'update-start-updating') {
						return { ...state, updateState: 'updating' }
					} else if (event.type === 'update-result') {
						if (event.success) {
							// Set timer to clear success message after 3 seconds
							setTimeout(() => {
								triggeredEvents.next({ type: 'clear-update-status' })
							}, 10000)
							return { ...state, updateState: 'success' }
						} else {
							// Set timer to clear error message after 3 seconds
							setTimeout(() => {
								triggeredEvents.next({ type: 'clear-update-status' })
							}, 10000)
							return { ...state, updateState: 'error' }
						}
					} else if (event.type === 'clear-update-status') {
						return { ...state, updateState: 'none' }
					} else if (event.type === 'agent-file-check-complete') {
						if (state.type === 'checking-agent-file') {
							if (event.migrated) {
								stdout.write('Migrated .sourcegraph/memory.md to AGENT.md\n')
							}

							if (event.shouldGenerate) {
								// Need to ask user if they want to generate the file
								clearProgressIndicator()
								question(
									'No AGENT.md file found. Would you like me to create one for this codebase?',
									['y', 'n'],
								).then((answer) => {
									triggeredEvents.next({
										type: 'yes-no-confirmation',
										value: answer === 'y',
										confirmationType: 'agent-file-creation',
									})
								})
								return {
									...state,
									type: 'confirming-agent-file-creation',
								}
							} else {
								// No generation needed, continue to normal flow
								lineEditor?.acceptMessage()
								return { ...state, type: 'awaiting-user-message' }
							}
						}
					}

					return state
				}),
				context.isInteractiveMode
					? {
							type: 'initial',
							tokenUsage: null,
							compactionPromptState: 'none',
							updateState: 'none',
						}
					: {
							type: 'awaiting-agent',
							statusMessage: 'Syncing...',
							displayedError: null,
							tokenUsage: null,
							compactionPromptState: 'none',
							updateState: 'none',
						},
			),
		)
		.subscribe({
			error: (error) => {
				clearProgressIndicator()
				logger.error(`Error in state machine`, error)
				stderr.write(context.printer.print(error.toString(), { foreground: 'red' }) + '\n')
				exit()
			},
			complete: () => {},
		})

	// Start async update check
	startAsyncUpdateCheck(triggeredEvents, context.isInteractiveMode).catch((error) => {
		logger.debug('Update check startup failed', error)
	})
}

function createContext(options?: SharedOptions): Context {
	const isInteractiveMode = process.stdin.isTTY
	const isTTY = typeof process !== 'undefined' && process.stdout?.isTTY && process.stderr?.isTTY
	const ampURL = process.env.AMP_URL || DEFAULT_AMP_URL_STRING

	const useColor = options?.color !== undefined ? options.color : isTTY
	const printer = useColor ? colorPrinter : monochromePrinter

	return {
		isInteractiveMode,
		isTTY,
		printer,
		ampURL,
	}
}

async function main() {
	// Set up initial logging with environment variables
	setupLogger({
		logLevel: process.env.AMP_LOG_LEVEL,
		logFile: process.env.AMP_LOG_FILE,
	})

	logger.info(`Starting Amp CLI.`)
	// Check Node.js version - require v22 or higher
	if (parseInt(process.version.slice(1).split('.')[0] ?? '') < 22) {
		stderr.write(
			`Amp CLI requires Node.js v22 or higher. Current version: ${process.version}\n`,
		)
		process.exit(1)
	}

	// Create initial context for program creation (uses default TTY-based color detection)
	const initialContext = createContext()

	// Parse command line arguments
	await createProgram(initialContext).parseAsync(process.argv)
}

main().catch((error) => {
	logger.error('Fatal error', error)
	stderr.write(`Fatal error: ${error.message}: ${error.stack}\n`)
	process.exit(1)
})

function logStateMachine<S extends { type: string }, E extends { type: string }>(
	reducer: (state: S, event: E) => S,
): (state: S, event: E) => S {
	return (state: S, event: E): S => {
		const nextState = reducer(state, event)
		if (event.type !== 'tick') {
			logger.debug(`${state.type} + ${event.type} -> ${nextState.type}\n`, {
				before: state,
				event,
				after: nextState,
			})
		}
		return nextState
	}
}

function renderProgressIndicator(
	printer: Printer,
	number: number,
	message?: string,
	tokenUsage?: TokenUsage,
): void {
	const states = '◉◎'
	stderr.write('\r\x1b[K')
	// Format the message to be a reasonable length and not contain newlines
	const formattedMessage = message ? message.substring(0, 80).replace(/\n/g, ' ') : ''

	const baseOutput = (states[number % states.length] ?? '') + ' ' + formattedMessage + ' '
	stderr.write(baseOutput)

	if (tokenUsage) {
		const terminalWidth = stderr.columns || 80

		const usageIndicator = formatTokenUsage(tokenUsage, printer)
		const padding = terminalWidth - baseOutput.length - usageIndicator.length

		if (padding > 0) {
			stderr.write(' '.repeat(padding) + usageIndicator)
			// Move cursor back to position after the base output
			stderr.write('\r')
			stderr.write(`\x1b[${baseOutput.length}C`)
		}
	}
}

function formatTokenUsage(tokenUsage: TokenUsage, printer: Printer): string {
	const percentUsed = tokenUsage.used / tokenUsage.maxAvailable
	const roundedPercent = Math.round(percentUsed * 100)
	const bars = '▁▂▃▄▅▆▇█'
	const barChar = bars[Math.floor(percentUsed * bars.length)]

	const percentText = `${roundedPercent}% ┃${barChar}┃`

	if (percentUsed >= TOKEN_DANGER_THRESHOLD) {
		return printer.print(percentText, { foreground: 'red' })
	} else if (percentUsed >= TOKEN_WARNING_THRESHOLD) {
		return printer.print(percentText, { foreground: 'yellow' })
	} else {
		return percentText
	}
}

function formatRightStatus(state: State, printer: Printer): string {
	const parts: string[] = []

	// Add update status if present
	if (state.updateState === 'updating') {
		parts.push('⬇ Updating Amp...')
	} else if (state.updateState === 'success') {
		parts.push(printer.print('✓ Amp updated. Restart to apply.', { foreground: 'green' }))
	} else if (state.updateState === 'error') {
		parts.push(printer.print('✗ Amp update failed.', { foreground: 'red' }))
	}

	// Add token usage if present
	if (state.tokenUsage) {
		parts.push(formatTokenUsage(state.tokenUsage, printer))
	}

	return parts.join(' ')
}

function clearProgressIndicator(): void {
	stderr.write('\r\x1b[K')
}

async function startAsyncUpdateCheck(
	triggeredEvents: Subject<Event>,
	isInteractive: boolean,
): Promise<void> {
	try {
		const updateCheck = await checkForUpdate(VERSION)
		if (updateCheck.hasUpdate && updateCheck.latestVersion) {
			if (isInteractive) {
				// Signal that we're starting the update
				triggeredEvents.next({ type: 'update-start-updating' })

				// Start the update process asynchronously
				setTimeout(async () => {
					try {
						await update()
						triggeredEvents.next({
							type: 'update-result',
							success: true,
							version: updateCheck.latestVersion,
						})
					} catch (error) {
						logger.info('Update failed', error)
						triggeredEvents.next({
							type: 'update-result',
							success: false,
							version: updateCheck.latestVersion,
						})
					}
				}, 100)
			} else {
				// In non-interactive mode, just log the available update
				logger.info(`New version available: ${VERSION} → ${updateCheck.latestVersion}`)
			}
		}
		// If no update available, don't emit any events - keep state as 'none'
	} catch (error) {
		// Update check failed, don't emit any events - keep state as 'none'
		logger.debug('Update check failed', error)
	}
}

const TOKEN_WARNING_THRESHOLD = 0.8
const TOKEN_DANGER_THRESHOLD = 0.9

function formatTimeAgoShort(date: Date): string {
	const now = new Date()
	const diffMs = now.getTime() - date.getTime()
	const diffSeconds = Math.floor(diffMs / 1000)
	const diffMinutes = Math.floor(diffSeconds / 60)
	const diffHours = Math.floor(diffMinutes / 60)
	const diffDays = Math.floor(diffHours / 24)
	const diffWeeks = Math.floor(diffDays / 7)
	const diffMonths = Math.floor(diffDays / 30)
	const diffYears = Math.floor(diffDays / 365)

	if (diffSeconds < 60) {
		return diffSeconds <= 1 ? 'now' : `${diffSeconds}s ago`
	} else if (diffMinutes < 60) {
		return `${diffMinutes}m ago`
	} else if (diffHours < 24) {
		return `${diffHours}h ago`
	} else if (diffDays < 7) {
		return `${diffDays}d ago`
	} else if (diffWeeks < 4) {
		return `${diffWeeks}w ago`
	} else if (diffMonths < 12) {
		return `${diffMonths}mo ago`
	} else {
		return `${diffYears}y ago`
	}
}

type PrintTableOptions = {
	columnFormatters?: Array<((text: string, width: number) => string) | undefined>
	minimumTruncatedColumnWidth?: number
	truncateColumnIndex?: number
}

function printTable(headers: string[], rows: string[][], options: PrintTableOptions = {}): void {
	const { columnFormatters = [], minimumTruncatedColumnWidth = 20, truncateColumnIndex } = options

	for (let i = 0; i < rows.length; i++) {
		if (rows[i]!.length !== headers.length) {
			throw new Error(
				`Row ${i} has ${rows[i]!.length} columns, but headers have ${headers.length} columns`,
			)
		}
	}

	const terminalWidth = stdout.columns || 120
	const columnSeparator = '  '
	const numSeparators = headers.length - 1
	const truncateIndex = truncateColumnIndex ?? headers.length - 1

	// Calculate natural widths for all columns except the one being truncated
	const naturalWidths: number[] = []
	for (let i = 0; i < headers.length; i++) {
		if (i === truncateIndex) {
			naturalWidths.push(0) // Will be calculated later
		} else {
			naturalWidths.push(Math.max(headers[i]!.length, ...rows.map((row) => row[i]!.length)))
		}
	}

	// Calculate available width for the truncated column
	const usedWidth =
		naturalWidths.reduce((sum, width) => sum + width, 0) +
		numSeparators * columnSeparator.length
	const availableWidth = Math.max(minimumTruncatedColumnWidth, terminalWidth - usedWidth)
	naturalWidths[truncateIndex] = availableWidth

	const widths = naturalWidths

	const headerRow = headers.map((header, i) => header.padEnd(widths[i]!)).join(columnSeparator)
	stdout.write(headerRow + '\n')

	const separatorRow = widths.map((width) => '─'.repeat(width)).join(columnSeparator)
	stdout.write(separatorRow + '\n')

	for (const row of rows) {
		const formattedRow = row.map((cell, colIndex) => {
			const maxWidth = widths[colIndex]!
			const formatter = columnFormatters[colIndex]

			if (formatter) {
				return formatter(cell, maxWidth)
			}

			// Default formatting: truncate and pad
			const truncated =
				cell.length > maxWidth ? cell.substring(0, maxWidth - 3) + '...' : cell
			return truncated.padEnd(maxWidth)
		})

		stdout.write(formattedRow.join(columnSeparator) + '\n')
	}
}
