import {
	combineLatest,
	debounceTime,
	distinctUntilChanged,
	firstValueFrom,
	map,
	Observable,
	pendingOperation,
	promiseFactoryToObservable,
	switchMap,
	type ObservableValue,
} from '@sourcegraph/observable'
import type { Disposable } from 'vscode'
import logger from '../common/logger'
import { resolvedConfig, type ResolvedConfiguration } from '../configuration/resolver'
import type { ConfigService } from '../configuration/service'
import type { WithTarget } from '../configuration/settings'
import {
	toolEnablement,
	type ToolRun,
	type ToolService,
	type ToolStatus,
} from '../tools/tool-service'
import { createMCPClient, DISABLED_CLIENT, type MCPClient } from './mcp-client'

/**
 * A specification of an MCP server (by the information needed to connect to it).
 */
export type MCPServerSpec =
	| { command: string; args?: string[]; env?: Record<string, string> }
	| { url: string }

/**
 * The environment to run the MCP server in.
 */
export interface MCPEnvironment {
	workingDirectory?: string
	/**
	 * Whether to load the user profile (e.g., .bashrc, or .envrc) from the
	 * user's working directory before running the MCP server.
	 */
	loadProfile: ResolvedConfiguration['settings']['terminal.commands.node-spawn.load-profile']
}

export interface MCPServerInfo {
	name: string
	spec: WithTarget<MCPServerSpec>
	status: ObservableValue<MCPClient['status']>
	tools: ToolStatus[] | Error
}

export interface MCPService {
	servers: Observable<MCPServerInfo[]>
	addServer(name: string, spec: MCPServerSpec): Promise<void>
	removeServer(name: string): Promise<void>
	updateServer(name: string, spec: MCPServerSpec): Promise<void>

	/**
	 * Dispose all MCP server connections and all other resources.
	 */
	dispose(): Promise<void>
}

export function createMCPService({
	configService,
	toolService,
}: {
	configService: ConfigService
	toolService: ToolService
}): MCPService {
	const configuredServers = resolvedConfig.pipe(
		map(
			({
				settings: {
					mcpServers,
					'terminal.commands.node-spawn.load-profile': loadProfile,
					'mcp.disable': disabledMCPServers,
				},
			}) => ({ mcpServers, loadProfile, disabledMCPServers }),
		),
		distinctUntilChanged(),
	)
	const workingDirectory = configService.workspaceRoots.pipe(map((uris) => uris.at(0)?.fsPath))

	const connections = new Map<
		string, // MCP server name
		// The disabled status is part of the extended spec, as it influences the client creation
		{ spec: WithTarget<MCPServerSpec> & { disabled: boolean }; client: MCPClient }
	>()
	const connectionManager = combineLatest(configuredServers, workingDirectory).pipe(
		debounceTime(300),
		map(async ([{ mcpServers, loadProfile, disabledMCPServers }, workingDirectory]) => {
			for (const [name, spec] of Object.entries(mcpServers ?? {})) {
				const disabled = disabledMCPServers?.includes(name) ?? false
				const existing = connections.get(name)
				if (existing && areSpecsEqual(existing.spec, { ...spec, disabled })) {
					continue
				}
				connections.set(name, {
					spec: { ...spec, disabled },
					client: createMCPClient(spec, { workingDirectory, loadProfile }, disabled),
				})
				if (existing) {
					await existing.client.dispose()
				}
			}

			// Remove any connections that are no longer configured.
			for (const [name, { client }] of connections.entries()) {
				if (!mcpServers || !(name in mcpServers)) {
					await client.dispose()
					connections.delete(name)
				}
			}

			return Array.from(connections.entries())
		}),
	)
	const keepalive = connectionManager.subscribe({})

	// TODO!(sqs): use pairwise to only register changed tools
	const allTools = connectionManager.pipe(
		switchMap((connections) =>
			connections.length === 0
				? Observable.of([])
				: combineLatest(
						...connections.map(([name, { spec, client }]) =>
							client.tools.pipe(
								map((tools) => ({
									name,
									spec,
									tools:
										tools === pendingOperation ||
										tools instanceof Error ||
										tools === DISABLED_CLIENT
											? []
											: tools,
									client,
								})),
							),
						),
					),
		),
	)
	let lastToolRegistrations: Disposable[] | undefined
	const toolRegisterer = allTools.subscribe(
		(allTools) => {
			if (lastToolRegistrations) {
				for (const d of lastToolRegistrations) {
					d.dispose()
				}
			}
			lastToolRegistrations = []

			for (const { name, tools, client } of allTools) {
				for (const tool of tools) {
					lastToolRegistrations.push(
						toolService.registerTool({
							spec: {
								name: tool.name,
								description: tool.description ?? '',
								inputSchema: tool.inputSchema,
								source: { mcp: name },
							},
							fn: ({ args }, env) =>
								promiseFactoryToObservable((signal) =>
									client
										.callTool(
											{ name: tool.name, arguments: args ?? undefined },
											signal,
										)
										.then(
											(contentParts) =>
												({
													status: 'done',
													result: contentParts.map((p) => {
														if (
															p.type === 'text' ||
															p.type === 'image'
														) {
															return p
														}
														throw new Error(
															`unsupported content type: ${p.type}`,
														)
													}),
												}) satisfies ToolRun,
										),
								),
						}),
					)
				}
			}
		},
		(error) => {
			logger.error('MCP toolRegisterer error', error)
		},
	)

	return {
		servers: combineLatest(resolvedConfig, connectionManager).pipe(
			switchMap(([resolvedConfig, connections]) =>
				connections.length === 0
					? Observable.of([])
					: combineLatest(
							...connections.map(([name, { spec, client }]) =>
								combineLatest(client.status, client.tools).pipe(
									map(
										([status, tools]): MCPServerInfo => ({
											name,
											spec,
											status,
											tools:
												tools instanceof Error
													? tools
													: tools === pendingOperation ||
														  tools === DISABLED_CLIENT
														? []
														: tools.map(
																(t) =>
																	({
																		spec: {
																			name: t.name,
																			description:
																				t.description,
																			inputSchema:
																				t.inputSchema,
																			source: { mcp: name },
																		},
																		...toolEnablement(
																			{
																				name: t.name,
																			},
																			resolvedConfig,
																		),
																	}) satisfies ToolStatus,
															),
										}),
									),
								),
							),
						),
			),
		),

		async addServer(name: string, spec: MCPServerSpec): Promise<void> {
			const cur = (await firstValueFrom(resolvedConfig)).settings.mcpServers
			if (cur && name in cur) {
				throw new Error(`MCP server already exists with name ${JSON.stringify(name)}`)
			}

			// Validate the server spec before adding
			if ('command' in spec && typeof spec.command !== 'string') {
				throw new Error('Command must be a string')
			}
			if ('url' in spec) {
				try {
					new URL(spec.url)
				} catch (e) {
					throw new Error(`Invalid URL: ${spec.url}`)
				}
			}

			await configService.updateSettings('mcpServers', { ...cur, [name]: spec }, 'global')
		},

		async removeServer(name: string): Promise<void> {
			const cur = (await firstValueFrom(resolvedConfig)).settings.mcpServers
			if (!cur || !(name in cur)) {
				throw new Error(`MCP server does not exist with name ${JSON.stringify(name)}`)
			}
			const tmp = { ...cur }
			delete tmp[name]
			await configService.updateSettings('mcpServers', tmp, 'global')
		},

		async updateServer(name: string, spec: MCPServerSpec): Promise<void> {
			const cur = (await firstValueFrom(resolvedConfig)).settings.mcpServers
			if (!cur || !(name in cur)) {
				throw new Error(`MCP server does not exist with name ${JSON.stringify(name)}`)
			}
			await configService.updateSettings('mcpServers', { ...cur, [name]: spec }, 'global')
		},

		async dispose(): Promise<void> {
			toolRegisterer.unsubscribe()
			keepalive.unsubscribe()
			// Close all clients and ensure resources are released
			const closePromises = Array.from(connections.values()).map(async ({ client }) => {
				try {
					await client.dispose()
				} catch (error) {
					logger.error('Error disposing MCP client:', error)
				}
			})

			// Wait for all clients to be properly closed
			await Promise.all(closePromises)
			connections.clear()
		},
	}
}

function areSpecsEqual(
	a: WithTarget<MCPServerSpec> & { disabled: boolean },
	b: WithTarget<MCPServerSpec> & { disabled: boolean },
): boolean {
	return JSON.stringify(a) === JSON.stringify(b)
}
