export function isBufferPrefix(prefix: Buffer, data: Buffer): boolean {
	if (prefix.length > data.length) return false
	return Buffer.from(Uint8Array.prototype.slice.call(data, 0, prefix.length)).equals(prefix)
}

export function bufferToHexKey(buffer: Buffer): string {
	return Array.from(buffer)
		.map((b) => b.toString(16).padStart(2, '0'))
		.join('')
}
