import {
	abortableOperation,
	combineLatest,
	concat,
	distinctUntilChanged,
	map,
	Observable,
	promiseFactoryToObservable,
	shareReplay,
	startWith,
	switchMap,
} from '@sourcegraph/observable'
import type { URI } from 'vscode-uri'
import { DEFAULT_AMP_URL_STRING } from '../constants'
import type { ClientSecrets, ResolvedConfiguration } from './resolver'
import { setResolvedConfigurationObservable } from './resolver'
import { SECRET_NAMES, type SecretName, type Settings } from './settings'
import type { SecretStorage, SettingsTarget } from './storage'
import { type SettingsStorage } from './storage'

export interface ConfigService {
	updateSettings<K extends keyof Settings>(
		key: K,
		value: Settings[K],
		target?: SettingsTarget,
	): Promise<void>
	appendSettings<K extends keyof Settings>(
		key: K,
		value: Settings[K] & any[],
		target?: SettingsTarget,
	): Promise<void>
	updateSecret(name: SecretName, value: string, url: string): Promise<void>
	workspaceRoots: Observable<URI[]>
}

export function createConfigService({
	storage,
	secretStorage,
	workspaceRoots,
	defaultAmpURL = DEFAULT_AMP_URL_STRING,
}: {
	storage: SettingsStorage
	secretStorage: SecretStorage
	workspaceRoots: Observable<URI[]>
	defaultAmpURL?: string
}): ConfigService {
	const settings = promiseFactoryToObservable(async () => {
		const keys = await storage.keys()
		const settings: Settings = Object.fromEntries(
			await Promise.all(keys.map(async (key) => [key, await storage.get(key)])),
		)
		return settings
	}).pipe(
		switchMap((lastSettings) =>
			concat(
				Observable.of(lastSettings),
				storage.changes.pipe(
					abortableOperation(async (keys) => {
						await Promise.all(
							keys.map(async (key) => {
								const value = await storage.get(key)
								// @ts-expect-error ignore
								lastSettings[key] = value
							}),
						)
						return lastSettings
					}),
				),
			),
		),
		map((settings) => {
			// Apply defaults.
			const final: ResolvedConfiguration['settings'] = {
				...settings,
				url: settings.url || defaultAmpURL,
			}
			return final
		}),
		shareReplay(),
		distinctUntilChanged(),
	)

	const secrets = combineLatest(
		secretStorage.changes.pipe(startWith(undefined)),
		settings.pipe(
			map(({ url }) => url),
			distinctUntilChanged(),
		),
	).pipe(
		abortableOperation(
			async ([, url]) =>
				({
					getToken: async (
						name: SecretName,
						url: string,
					): Promise<string | undefined> => {
						return secretStorage.get(name, url)
					},
					isSet: {
						[url]: Object.fromEntries(
							await Promise.all(
								SECRET_NAMES.map(async (name) => [
									name,
									!!(await secretStorage.get(name, url)),
								]),
							),
						),
					},
				}) satisfies ClientSecrets,
		),
	)

	setResolvedConfigurationObservable(
		combineLatest(settings, secrets).pipe(
			map(
				([settings, secrets]): ResolvedConfiguration => ({
					settings,
					secrets,
				}),
			),
		),
	)
	return {
		updateSettings<K extends keyof Settings>(
			key: K,
			value: Settings[K],
			target?: SettingsTarget,
		): Promise<void> {
			return storage.set(key, value, target)
		},
		async appendSettings<K extends keyof Settings, V extends Settings[K] & any[]>(
			key: K,
			value: V,
			target?: SettingsTarget,
		): Promise<void> {
			const old: Settings[K] | undefined = await storage.get<K>(key, target)
			if (old === undefined) {
				return storage.set(key, value, target)
			} else if (!Array.isArray(old)) {
				throw new Error(`Cannot append to non-array setting: ${key}`)
			} else if (old.length === 0) {
				return storage.set(key, value, target)
			} else {
				const merged: Settings[K] = [...old, ...value] as Settings[K]
				return storage.set(key, merged, target)
			}
		},
		updateSecret(name: SecretName, url: string, value: string): Promise<void> {
			return secretStorage.set(name, url, value)
		},
		workspaceRoots,
	}
}
