import { spawn } from 'node:child_process'

/**
 * Check if there's a newer version available on npm
 */
export async function checkForUpdate(
	currentVersion: string,
): Promise<{ hasUpdate: boolean; latestVersion?: string }> {
	try {
		const controller = new AbortController()
		const timeoutId = setTimeout(() => controller.abort(), 500)

		const response = await fetch('https://registry.npmjs.org/@sourcegraph/amp/latest', {
			signal: controller.signal,
		})
		clearTimeout(timeoutId)

		if (!response.ok) {
			return { hasUpdate: false }
		}

		const data = (await response.json()) as { version?: string }
		const latestVersion = data.version

		if (latestVersion === undefined) {
			return { hasUpdate: false }
		}

		// Simple version comparison - compare as strings since semver is ordered lexicographically for most cases
		// For a more robust solution, we could use a semver library, but this should work for basic cases
		const hasUpdate = compareVersions(currentVersion, latestVersion) < 0

		return { hasUpdate, latestVersion }
	} catch (error) {
		// Network error or timeout - don't block startup
		return { hasUpdate: false }
	}
}

/**
 * Simple version comparison (returns -1 if a < b, 0 if equal, 1 if a > b)
 */
export function compareVersions(a: string, b: string): number {
	// Parse version and label
	const parseVersion = (version: string) => {
		const [versionPart, label] = version.split('-')
		const parts = versionPart?.split('.').map(Number) || []
		return { parts, label }
	}

	const aVersion = parseVersion(a)
	const bVersion = parseVersion(b)

	// Compare version parts first
	const maxLength = Math.max(aVersion.parts.length, bVersion.parts.length)
	for (let i = 0; i < maxLength; i++) {
		const aPart = aVersion.parts[i] || 0
		const bPart = bVersion.parts[i] || 0

		if (aPart < bPart) return -1
		if (aPart > bPart) return 1
	}

	// If version parts are equal, compare labels
	if (aVersion.label === bVersion.label) {
		return 0
	}

	// No label (release) > any label (pre-release)
	if (!aVersion.label && bVersion.label) return 1
	if (aVersion.label && !bVersion.label) return -1

	// Both have labels - compare alphabetically (alpha < beta)
	if (aVersion.label && bVersion.label) {
		return aVersion.label < bVersion.label ? -1 : 1
	}

	return 0
}

type PackageManager = 'npm' | 'pnpm' | 'yarn' | 'bun'

/**
 * Detect which package manager was likely used to install this CLI
 */
export async function detectPackageManager(): Promise<PackageManager | null> {
	// Check npm_config_user_agent first (most reliable when available)
	const userAgent = process.env.npm_config_user_agent
	if (userAgent) {
		if (userAgent.includes('pnpm')) return 'pnpm'
		if (userAgent.includes('yarn')) return 'yarn'
		if (userAgent.includes('bun')) return 'bun'
		if (userAgent.includes('npm')) return 'npm'
	}

	// Check for package manager specific environment variables
	if (process.env.PNPM_HOME || process.env.PNPM_SCRIPT_SRC_DIR) return 'pnpm'
	if (process.env.YARN_WRAP_OUTPUT || process.env.YARNPKG_LOCKFILE_VERSION) return 'yarn'
	if (process.env.BUN_INSTALL) return 'bun'

	// Check which package managers are available on the system
	const [pnpm, yarn, bun, npm] = await Promise.allSettled([
		checkCommandExists('pnpm'),
		checkCommandExists('yarn'),
		checkCommandExists('bun'),
		checkCommandExists('npm'),
	])

	if (pnpm?.status === 'fulfilled' && pnpm?.value) return 'pnpm'
	if (yarn?.status === 'fulfilled' && yarn?.value) return 'yarn'
	if (bun?.status === 'fulfilled' && bun?.value) return 'bun'
	if (npm?.status === 'fulfilled' && npm?.value) return 'npm'

	return null
}

/**
 * Check if a command exists in PATH
 */
async function checkCommandExists(command: string): Promise<boolean> {
	return new Promise((resolve) => {
		const child = spawn(command, ['--version'], { stdio: 'ignore' })
		child.on('close', (code) => resolve(code === 0))
		child.on('error', () => resolve(false))
	})
}

/**
 * Get the appropriate update command for each package manager
 */
function getUpdateCommand(packageManager: PackageManager): [string, string[]] {
	switch (packageManager) {
		case 'pnpm':
			return ['pnpm', ['add', '-g', '@sourcegraph/amp']]
		case 'yarn':
			return ['yarn', ['global', 'add', '@sourcegraph/amp']]
		case 'bun':
			return ['bun', ['add', '-g', '@sourcegraph/amp']]
		case 'npm':
		default:
			return ['npm', ['install', '-g', '@sourcegraph/amp']]
	}
}

export async function update(): Promise<void> {
	const packageManager = await detectPackageManager()
	if (!packageManager) {
		throw new Error('No supported package manager found.')
	}
	const [command, args] = getUpdateCommand(packageManager)

	return new Promise((resolve, reject) => {
		const updateProcess = spawn(command, args, {
			stdio: 'pipe',
		})

		let stdout = ''
		let stderr = ''

		updateProcess.stdout?.on('data', (data) => {
			stdout += data.toString()
		})

		updateProcess.stderr?.on('data', (data) => {
			stderr += data.toString()
		})

		updateProcess.on('close', (code) => {
			if (code === 0) {
				resolve()
			} else {
				const output = stderr || stdout || 'No output'
				reject(
					new Error(`${command} ${args.join(' ')} failed with code ${code}:\n${output}`),
				)
			}
		})

		updateProcess.on('error', (error) => {
			reject(new Error(`Failed to spawn ${command}: ${error.message}`))
		})
	})
}
