import logger, { override } from '@sourcegraph/amp-core/src/common/logger'
import fs from 'node:fs'
import { homedir } from 'node:os'
import path from 'node:path'
import winston from 'winston'
import { xdgCache } from 'xdg-basedir'

export interface LoggerOptions {
	/**
	 * Override the default log level
	 */
	logLevel?: string

	/**
	 * Override the default log file location
	 */
	logFile?: string
}

export const LOG_LEVELS = Object.keys(logger)
export type LogLevel = (typeof LOG_LEVELS)[number]

export function setupLogger(options?: LoggerOptions) {
	const LOG_DIR = path.join(xdgCache || path.join(homedir(), '.cache'), 'amp', 'logs')
	const LOG_FILE = options?.logFile || path.join(LOG_DIR, 'cli.log')

	const logLevel = options?.logLevel || 'info'

	// Validate log level
	if (!LOG_LEVELS.includes(logLevel as LogLevel)) {
		// eslint-disable-next-line amp/no-console
		console.warn(`Invalid log level: ${logLevel}. Using 'info' instead.`)
	}

	try {
		fs.mkdirSync(path.dirname(LOG_FILE), { recursive: true })
	} catch (error) {
		// eslint-disable-next-line amp/no-console
		console.error(`Failed to create log directory: ${error}`)
	}

	const winstonLogger = winston.createLogger({
		level: LOG_LEVELS.includes(logLevel as LogLevel) ? logLevel : 'info',
		format: winston.format.combine(
			winston.format.timestamp(),
			winston.format.json(),
			winston.format.errors({ stack: true }),
		),
		transports: [new winston.transports.File({ filename: LOG_FILE })],
	})

	override({
		error: winstonLogger.error.bind(winstonLogger),
		warn: winstonLogger.warn.bind(winstonLogger),
		info: winstonLogger.info.bind(winstonLogger),
		debug: winstonLogger.debug.bind(winstonLogger),
		audit: (message, ...meta) => winstonLogger.info(message, { audit: true, ...meta[0] }),
	})
}
