/**
 * The schema that defines a hook in settings.
 *
 * NOTE: Update vscode/package.json#contributes/configuration/properties/hooks to keep it in sync
 * with this.
 */
export interface Hook {
	/**
	 * The version of the hook schema that this value was written to conform to. Amp will try to
	 * maintain compatibility with older-schema hooks, automatically migrating them in the settings
	 * file or on-the-fly based on schema changes since the hook's compatibilityDate and the current
	 * schema.
	 */
	compatibilityDate: '2025-05-13'

	/**
	 * A unique identifier for the hook (e.g., "run-vitest-with-env-vars").
	 */
	id: string

	/** A description of the hook. */
	description?: string

	/**
	 * The event that triggers the hook.
	 */
	on: {
		/**
		 * Trigger when a tool is about to be executed.
		 */
		event: 'tool:pre-execute'

		/**
		 * Trigger for the given tool name(s) (e.g., "edit_file").
		 */
		tool: string | string[]

		/**
		 * Trigger only when the tool input contains the given literal string or strings.
		 */
		'input.contains': string | string[]
	}

	/** The action to take when the hook is triggered. */
	action: {
		/**
		 * Send a user message to interrupt the agent and cancel the tool call in the
		 * `tool:pre-execute` trigger before it starts to run
		 */
		type: 'send-user-message'

		/**
		 * The text of the message to send.
		 */
		message: string
	}

	/**
	 * A condition that determines whether the hook should be executed.
	 *
	 * - `true` (default): The hook will be executed.
	 * - `false`: The hook will never be executed.
	 * - Support for more conditions may be added in the future.
	 */
	if?: boolean
}

/**
 * Filter out incompatible hooks.
 */
export function compatibleHooks(hooks: Hook[]): Hook[] {
	// `hooks` comes straight from the user settings, so it might be in an invalid format. Lightly protect against that.
	if (!hooks || !Array.isArray(hooks)) {
		return []
	}

	return hooks.filter((hook) => hook.compatibilityDate === '2025-05-13')
}
