import type { Thread } from './thread'
import type { InferenceState, ThreadWorkerStatus } from './thread-worker'

/**
 * Information needed to display a thread interactively in UIs.
 */
export type ThreadViewState = ThreadWorkerStatus & {
	waitingForUserInput: 'tool-use' | 'user-message-initial' | 'user-message-reply' | false
}

export function threadViewState(thread: Thread, workerStatus: ThreadWorkerStatus): ThreadViewState {
	return {
		...workerStatus,
		waitingForUserInput:
			thread && workerStatus?.state === 'active'
				? waitingForUserInput(thread, workerStatus.inferenceState)
				: false,
	}
}

export function waitingForUserInput(
	thread: Pick<Thread, 'messages'>,
	inferenceState: InferenceState,
): 'tool-use' | 'user-message-initial' | 'user-message-reply' | false {
	if (inferenceState === 'running') {
		return false
	}

	const lastMsg = thread.messages.at(-1)
	if (!lastMsg) {
		return 'user-message-initial' // empty thread
	}

	if (lastMsg.role === 'assistant') {
		return lastMsg.state.type === 'complete' && lastMsg.state.stopReason === 'end_turn'
			? 'user-message-reply'
			: false
	}

	if (
		lastMsg.content.some((b) => b.type === 'tool_result' && b.run.status === 'blocked-on-user')
	) {
		return 'tool-use'
	}

	return false
}
