export function isContextLimitError(err: {
	message: string
	error?: { type: string; message?: string }
}): boolean {
	const contextLimitPromptTooLongMessages = ['prompt is too long', 'exceed context limit']
	const isErrorInDirectAPIMode =
		err.error?.type === 'invalid_request_error' &&
		contextLimitPromptTooLongMessages.some((keyword) => err.error?.message?.includes(keyword))
	const isErrorInProxyMode = contextLimitPromptTooLongMessages.some((keyword) =>
		err.message?.includes(keyword),
	)
	return isErrorInDirectAPIMode || isErrorInProxyMode
}

export function isUnauthorizedError(err: {
	message: string
	error?: { type?: string; message?: string }
}): boolean {
	const unauthorizedKeywords = ['unauthorized', '401']

	// Check for unauthorized in the main error message
	const isErrorInMainMessage = unauthorizedKeywords.some((keyword) =>
		err.message?.toLowerCase().includes(keyword.toLowerCase()),
	)

	// Check for unauthorized in nested error object
	const isErrorInNestedMessage = Boolean(
		err.error?.message &&
			unauthorizedKeywords.some((keyword) =>
				err.error?.message?.toLowerCase().includes(keyword.toLowerCase()),
			),
	)

	return isErrorInMainMessage || isErrorInNestedMessage
}
