import { Client } from '@modelcontextprotocol/sdk/client/index.js'
import { SSEClientTransport } from '@modelcontextprotocol/sdk/client/sse.js'
import type { Transport } from '@modelcontextprotocol/sdk/shared/transport.js'
import type { CallToolRequest, CallToolResult } from '@modelcontextprotocol/sdk/types.js'
import {
	ToolListChangedNotificationSchema,
	type ImplementationSchema,
	type ServerCapabilitiesSchema,
	type ToolSchema,
} from '@modelcontextprotocol/sdk/types.js'
import {
	concat,
	finalize,
	firstResultFromOperation,
	firstValueFrom,
	lifecycle,
	map,
	NEVER,
	Observable,
	pendingOperation,
	promiseFactoryToObservable,
	startWith,
	Subject,
	switchMap,
	switchMapReplayOperation,
} from '@sourcegraph/observable'
import type { z } from 'zod'
import logger from '../common/logger'
import type { MCPEnvironment, MCPServerSpec } from './service'

export type ConnectionStatus =
	| { type: 'connecting' }
	| { type: 'connection-failed'; error: { message: string } }
	| {
			type: 'connected'
			capabilities: z.infer<typeof ServerCapabilitiesSchema>
			serverInfo: z.infer<typeof ImplementationSchema>
	  }
	| { type: 'disabled' }

/** A higher-level wrapper around `@modelcontextprotocol/sdk/client`. */
export interface MCPClient {
	status: Observable<ConnectionStatus>
	tools: Observable<
		z.infer<typeof ToolSchema>[] | typeof pendingOperation | Error | typeof DISABLED_CLIENT
	>

	callTool(
		params: CallToolRequest['params'],
		signal?: AbortSignal,
	): Promise<CallToolResult['content']>

	/**
	 * Disconnect from the server (and kill its process for stdio servers).
	 */
	dispose(): Promise<void>
}

export const DISABLED_CLIENT = Symbol.for('disabled-client')

export function createMCPClient(
	spec: MCPServerSpec,
	env: MCPEnvironment,
	disabled: boolean,
): MCPClient {
	// TODO!(sqs): can we reuse client across connections? should be safe, but just keep that in
	// mind if weird bugs.
	const client = new Client({ name: 'amp-mcp-client', version: '0.0.0-dev' })

	const clients: Observable<Client | typeof pendingOperation | Error | typeof DISABLED_CLIENT> =
		Observable.of(undefined).pipe(
			switchMapReplayOperation(
				() =>
					concat(
						promiseFactoryToObservable(async () => {
							// Disconnect existing transport, if any.
							await client.close()

							// Add a small delay to ensure the client is ready for reuse
							// This addresses an issue with the SDK's transport close not properly awaiting
							await new Promise((resolve) => setTimeout(resolve, 100))

							if (disabled) {
								return DISABLED_CLIENT
							}

							// Don't use an AbortSignal here because the client and transport are long-running.
							return connectTransport(spec, client, env)
						}),
						NEVER,
					).pipe(
						finalize((client) => {
							if (client instanceof Client) {
								client?.close()
							}
						}),
					),
				{
					shouldCountRefs: false,
				},
			),
		)

	// Keep a running subscription to the client to ensure it stays alive until we're disposed.
	const keepalive = clients.subscribe({})

	const status = clients.pipe(
		map((client) => {
			if (client === DISABLED_CLIENT) {
				return { type: 'disabled' as const }
			}
			if (client === pendingOperation) {
				return { type: 'connecting' as const }
			}
			if (client instanceof Error) {
				return {
					type: 'connection-failed' as const,
					error: {
						message: client.message,
					},
				}
			}
			return {
				type: 'connected' as const,
				capabilities: client.getServerCapabilities()!,
				serverInfo: client.getServerVersion()!,
			}
		}),
	)

	const toolListChanges = clients.pipe(
		switchMap(
			(
				client,
			): Observable<Client | typeof pendingOperation | Error | typeof DISABLED_CLIENT> => {
				if (
					client === pendingOperation ||
					client instanceof Error ||
					client === DISABLED_CLIENT
				) {
					return Observable.of(client)
				}

				const notifs = new Subject<void>()
				client.setNotificationHandler(ToolListChangedNotificationSchema, () => {
					notifs.next()
				})
				return notifs.pipe(
					lifecycle({
						onUnsubscribe: () =>
							client.removeNotificationHandler('notifications/tools/list_changed'),
					}),
					startWith(undefined),
					map(() => client),
				)
			},
		),
	)

	const tools: Observable<
		z.infer<typeof ToolSchema>[] | typeof pendingOperation | Error | typeof DISABLED_CLIENT
	> = toolListChanges.pipe(
		switchMapReplayOperation((client): Observable<z.infer<typeof ToolSchema>[] | Error> => {
			if (client instanceof Error) {
				return Observable.of(client)
			}
			if (client === DISABLED_CLIENT) {
				return Observable.of([])
			}
			return promiseFactoryToObservable(async (signal) => {
				// TODO!(sqs): handle pagination
				const response = await client.listTools({ signal })
				return response.tools
			})
		}),
	)

	return {
		status,
		tools,
		async callTool(
			params: CallToolRequest['params'],
			signal?: AbortSignal,
		): Promise<CallToolResult['content']> {
			const client = await firstResultFromOperation(clients)
			if (client === DISABLED_CLIENT) {
				throw new Error('cannot call tools for disabled client')
			}
			const resp = await client.callTool(params, undefined, {
				signal,
				// TODO(sqs): hacky, but avoid having the MCP lib's default timeout (60 seconds)
				// apply here because we have our own timeout imposed by `signal`.
				timeout: 999999 * 1000,
			})
			if (!('content' in resp)) {
				throw new Error('unexpected response')
			}
			return resp.content as CallToolResult['content']
		},
		async dispose() {
			keepalive.unsubscribe()
			try {
				await client.close()
			} catch (error) {
				logger.error('Error closing MCP client:', error)
			}
		},
	}
}

async function connectTransport(
	spec: MCPServerSpec,
	client: Client,
	env: MCPEnvironment,
): Promise<Client> {
	let transport: Transport
	if ('url' in spec) {
		// Use the URL directly as provided - don't modify the path
		transport = new SSEClientTransport(new URL(spec.url))
	} else {
		// Conditionally import so that we don't import Node-only modules on the web.
		if (
			import.meta.env.VITE_PLATFORM === 'vscode' ||
			import.meta.env.VITE_PLATFORM === 'node'
		) {
			const { loadProfileEnvironmentVariables } = await import(
				'../tools/builtin/bash/load-profile'
			)
			const environmentVariables =
				env.loadProfile === 'never' || !env.workingDirectory
					? process.env
					: await firstValueFrom(
							loadProfileEnvironmentVariables(env.workingDirectory, env.loadProfile),
						)
			transport = await import('@modelcontextprotocol/sdk/client/stdio.js').then(
				({ StdioClientTransport }) =>
					new StdioClientTransport({
						...spec,
						stderr: 'pipe',
						cwd: env.workingDirectory,
						env: { ...environmentVariables, ...spec.env } satisfies Record<
							string,
							string | undefined
						> as Record<string, string>,
					}),
			)
		} else {
			throw new Error('stdio MCP servers are not supported on this platform')
		}
	}

	// If we're using stdio transport and have piped stderr, set up handlers
	if (
		'stderr' in transport &&
		transport.stderr &&
		typeof transport.stderr === 'object' &&
		transport.stderr !== null &&
		'on' in transport.stderr &&
		typeof transport.stderr.on === 'function'
	) {
		transport.stderr.on('data', (chunk: Buffer) => {
			// Log stderr output through our logger system instead of direct console output
			const message = chunk.toString().trim()
			if (message) {
				logger.info('mcp-server-stderr', { stderr: message, spec })
			}
		})
	}

	try {
		// Set a timeout of 10 seconds for the connection attempt
		const connectPromise = client.connect(transport)
		const timeoutPromise = new Promise<never>((_, reject) => {
			const id = setTimeout(() => {
				clearTimeout(id)
				reject(
					new Error('Connection timeout: MCP server did not respond within 10 seconds'),
				)
			}, 10000)
		})

		await Promise.race([connectPromise, timeoutPromise])
		return client
	} catch (error) {
		// Provide a more useful error message
		if (error instanceof Error) {
			if (error.message.includes('Connection closed')) {
				throw new Error(
					'MCP server connection was closed unexpectedly. ' +
						'Check if the server is running and try refreshing the connection.',
				)
			} else if (error.message.includes('Invalid content type')) {
				throw new Error(
					'SSE error: Invalid content type, expected "text/event-stream". ' +
						'Check that the URL is correct and points to a valid MCP SSE endpoint. ' +
						'The server must respond with Content-Type: text/event-stream header.',
				)
			} else if ('url' in spec && error.message.includes('fetch failed')) {
				throw new Error(
					`Could not connect to MCP server at ${spec.url}. ` +
						'Check that the server is running and accessible.',
				)
			}
		}
		throw error
	}
}
