import * as child_process from 'node:child_process'
import * as fs from 'node:fs/promises'
import * as path from 'node:path'
import { promisify } from 'node:util'
import type { URI } from 'vscode-uri'
import { Utils } from 'vscode-uri'
import logger from '../common/logger'
import type { Tree } from './thread-env'

const exec = promisify(child_process.exec)

/**
 * Gets Git repository information for a specified directory
 */
export async function getGitRepositoryInfo(dir: URI): Promise<Tree> {
	const treeWithDisplayNameOnly: Tree = {
		displayName: Utils.basename(dir),
	}

	if (dir.scheme !== 'file') {
		return treeWithDisplayNameOnly
	}

	treeWithDisplayNameOnly.fsPath = dir.fsPath

	try {
		// Check if directory is a git repository.
		const isGitRepo = await fs
			.access(path.join(dir.fsPath, '.git'))
			.then(() => true)
			.catch(() => false)
		if (!isGitRepo) {
			return treeWithDisplayNameOnly
		}

		const { stdout: remoteUrl } = await exec('git remote get-url origin', {
			cwd: dir.fsPath,
		}).catch(() => ({
			stdout: '',
		}))
		if (!remoteUrl.trim()) {
			return treeWithDisplayNameOnly
		}

		const { stdout: refName } = await exec('git symbolic-ref HEAD', {
			cwd: dir.fsPath,
		}).catch(() => ({
			stdout: '',
		}))

		const { stdout: commitSHA } = await exec('git rev-parse HEAD', { cwd: dir.fsPath }).catch(
			() => ({
				stdout: '',
			}),
		)

		return {
			...treeWithDisplayNameOnly,
			repository: {
				type: 'git',
				url: normalizeGitRemoteURL(remoteUrl.trim()),
				ref: refName.trim() ?? undefined,
				sha: commitSHA.trim() ?? undefined,
			},
		}
	} catch (error) {
		logger.error('Error getting repository info:', error, { dir: dir.fsPath })
		return treeWithDisplayNameOnly
	}
}

/**
 * Normalize Git remote URLs to HTTPS format.
 *
 * Handles various URL formats including:
 * - HTTPS: https://github.com/org/name
 * - SSH: git@github.com:org/name.git
 * - SCP-like: github.com:org/name
 *
 * Supports only specific hosting services (GitHub, GitLab, Bitbucket, Azure
 * DevOps). URLs from other hosting services are returned unchanged since we
 * may incorrectly normalize the URL.
 */
export function normalizeGitRemoteURL(url: string): string {
	// Known Git hosting services that we'll normalize
	const knownHosts = ['github.com', 'gitlab.com', 'bitbucket.org']

	// Remove .git suffix if present
	const cleanUrl = url.replace(/\.git$/, '')

	// Extract host and path based on URL format
	let host: string | null = null
	let path: string | null = null

	// First, try SSH URL format: git@github.com:owner/repo or git@github.com/owner/repo
	const sshMatch = cleanUrl.match(/^git@([^:]+)[:|/](.+)$/)
	if (sshMatch && sshMatch[1] && sshMatch[2]) {
		host = sshMatch[1]
		path = sshMatch[2]
	}

	// If not SSH, try HTTPS/HTTP URL format
	if (!host || !path) {
		const httpsMatch = cleanUrl.match(/^https?:\/\/([^/]+)\/(.+)$/)
		if (httpsMatch && httpsMatch[1] && httpsMatch[2]) {
			host = httpsMatch[1]
			path = httpsMatch[2]
		}
	}

	// If not HTTPS either, try SCP-like syntax: github.com:org/name
	if (!host || !path) {
		const scpMatch = cleanUrl.match(/^([^:]+):(.+)$/)
		if (scpMatch && scpMatch[1] && scpMatch[2]) {
			host = scpMatch[1]
			path = scpMatch[2]
		}
	}

	// If we have both host and path, and the host is known, normalize to HTTPS
	if (host && path && knownHosts.includes(host)) {
		return `https://${host}/${path}`
	}

	// Otherwise return the original URL unchanged
	return url
}
