import logger from '../common/logger'
import type { ReadonlyDeep } from '../common/types'
import {
	abortableOperation,
	catchError,
	combineLatest,
	debounceTime,
	distinctUntilChanged,
	NEVER,
	promiseFactoryToObservable,
	startWith,
	switchMap,
	throttleTime,
} from '@sourcegraph/observable'
import { tick } from '../misc/sleep'
import type { Observable } from '@sourcegraph/observable'
import { map } from '@sourcegraph/observable'
import { BehaviorSubject, Subject } from '@sourcegraph/observable'
import type { Storage } from '../storage/common'
import { type Thread, type ThreadID } from './thread'

export type DebugUsage = {
	maxInputTokens: number
	inputTokens: number
	outputTokens: number
	cacheCreationInputTokens: number | null
	cacheReadInputTokens: number | null
	/**
	 * Total tokens used in the context window, accounting for all input token types
	 * (direct input + cache creation + cache reads)
	 */
	totalInputTokens: number
}

/**
 * A handle for exclusive synchronous read/write access to a thread.
 */
export interface ThreadExclusiveSyncReadWriter {
	/** Get the current value of the thread. */
	read(): Thread

	/** Update the thread value. */
	write(updated: Thread): void
}

export class ThreadService {
	/** Keeps track of threads that have exclusive read-writers, along with their BehaviorSubjects. */
	private exclusiveThreads = new Map<ThreadID, BehaviorSubject<Thread>>()

	/** Emits whenever the set of exclusive threads changes. */
	private exclusiveThreadsChanged = new Subject<void>()

	/**
	 * Observable that emits whenever the set of exclusive threads (the threads that are writable by
	 * this process) changes. Observers should listen to the individual BehaviorSubjects for each
	 * thread to listen for changes to the content of a thread.
	 *
	 * Observers MUST NOT mutate the returned Map, as it is shared with other observers.
	 */
	public readonly exclusiveThreadsChanges: Observable<
		ReadonlyMap<ThreadID, BehaviorSubject<ReadonlyDeep<Thread>>>
	> = this.exclusiveThreadsChanged.pipe(
		startWith(undefined),
		map(() => this.exclusiveThreads),
	)

	private flushRequests = new Subject<void>()

	/** Periodically flush threads to storage. */
	private flusher = this.exclusiveThreadsChanged
		.pipe(
			map(() => Array.from(this.exclusiveThreads.keys())),
			switchMap((threadIDs) =>
				threadIDs.length > 0
					? combineLatest(
							...threadIDs.map((threadID) => {
								const latest = this.exclusiveThreads.get(threadID)
								if (!latest) {
									return NEVER
								}
								return combineLatest(
									latest.pipe(debounceTime(0), startWith(undefined)),
									this.flushRequests.pipe(startWith(undefined)),
								).pipe(
									abortableOperation(async () => {
										const thread = latest.getValue()
										// console.debug(`Flushing ${threadID}@${thread.v}`)
										await this.storage.set(threadID, thread)
									}),
									catchError((error) => {
										logger.error(`Error flushing thread to storage:`, error, {
											name: 'ThreadService',
											threadID,
										})
										return NEVER
									}),
								)
							}),
						)
					: NEVER,
			),
		)
		.subscribe({})

	constructor(
		private storage: Pick<Storage<Record<string, Thread>>, 'get' | 'set' | 'changes'>,
	) {}

	/**
	 * Observe the chat thread with the given {@link id}. The returned {@link Observable} emits
	 * whenever there are any changes to the chat thread by an
	 * {@link ThreadService.exclusiveSyncReadWriter} in this execution context.
	 */
	public observe(id: ThreadID): Observable<Thread | undefined> {
		return this.exclusiveThreadsChanged.pipe(
			startWith(undefined),
			map(() => this.exclusiveThreads.has(id)),
			distinctUntilChanged(),
			switchMap((hasExclusive) => {
				// If we have an exclusive read-writer for this thread, return its corresponding
				// BehaviorSubject.
				if (hasExclusive) {
					return this.exclusiveThreads.get(id)!
				}

				// Otherwise read it from storage.
				return promiseFactoryToObservable(async (signal) => {
					let thread = await this.storage.get(id)
					signal?.throwIfAborted()
					if (!thread) {
						thread = newEmptyThread(id)
						await this.storage.set(id, thread)
						signal?.throwIfAborted()
					}
					return thread
				})
			}),

			// Throttle to avoid passing very large objects (~1MB+ often) across the RPC boundary,
			// which causes GC pressure.
			throttleTime(100, { leading: true, trailing: true }),
		)
	}

	/**
	 * Get exclusive synchronous read/write access to a thread. Only one caller can hold this access
	 * at a time, and this is the only way to update a thread. It returns an object that supports
	 * synchronous reading and writing of the {@link Thread} value.
	 *
	 * @throws Error if someone already has exclusive access to this thread
	 */
	public async exclusiveSyncReadWriter(
		id: ThreadID,
	): Promise<ThreadExclusiveSyncReadWriter & { dispose: () => void }> {
		// Check if this thread already has an exclusive read-writer.
		if (this.exclusiveThreads.has(id)) {
			throw new Error(`Thread ${id} already has an exclusive read-writer`)
		}

		// First ensure the thread is loaded by fetching it from storage if needed.
		let latest = this.exclusiveThreads.get(id)
		if (!latest) {
			let thread = await this.storage.get(id)
			if (!thread) {
				thread = newEmptyThread(id)
				await this.storage.set(id, thread)
			}

			latest = new BehaviorSubject<Thread>(thread)
			this.exclusiveThreads.set(id, latest)
			this.exclusiveThreadsChanged.next()
			await tick()
		}

		let disposed = false
		const readWriter: ThreadExclusiveSyncReadWriter & { dispose: () => void } = {
			read: () => {
				if (disposed) {
					throw new Error('thread exclusive read-writer was disposed')
				}

				return latest.getValue()
			},
			write: (updated: Thread) => {
				if (disposed) {
					throw new Error('thread exclusive read-writer was disposed')
				}

				const tmp = JSON.parse(JSON.stringify(updated)) as Thread
				latest.next(tmp)
			},
			dispose: () => {
				if (!disposed) {
					disposed = true
					this.exclusiveThreads.delete(id)
					this.exclusiveThreadsChanged.next()
				}
			},
		}

		return readWriter
	}

	public flush(): void {
		this.flushRequests.next()
	}

	/**
	 * Flush a thread to storage and wait until the storage has been updated with at least the specified version.
	 * This is useful for ensuring that changes to a thread are persisted before continuing.
	 *
	 * The caller MUST ensure that they've written a thread with that version before.
	 *
	 * @param id The ID of the thread to flush
	 * @param version The minimum version number to wait for
	 * @param timeout Optional timeout in milliseconds (defaults to 5000ms)
	 * @returns A promise that resolves when the thread has been flushed with the specified version
	 */
	public async flushVersion(id: ThreadID, version: number, timeout = 5000): Promise<void> {
		this.flush()

		const startTime = Date.now()

		while (true) {
			if (Date.now() - startTime > timeout) {
				throw new Error(`Timed out waiting for thread ${id} to reach version ${version}`)
			}

			const thread = await this.storage.get(id)
			if (thread && thread.v >= version) {
				// console.debug(`Thread ${id} flushed to version ${thread.v}`)
				return
			}

			await new Promise((resolve) => setTimeout(resolve, 10))
		}
	}

	/**
	 * Delete a thread from exclusive access tracking.
	 * Note: This does not delete the thread from storage. Use ThreadHistoryService.delete for that.
	 */
	public deleteFromExclusiveAccess(id: ThreadID): void {
		this.exclusiveThreads.delete(id)
		this.exclusiveThreadsChanged.next()
	}

	public dispose(): void {
		this.flusher.unsubscribe()
		this.exclusiveThreads.clear()
	}
}

function newEmptyThread(id: ThreadID): Thread {
	return {
		v: 0,
		id,
		created: Date.now(),
		messages: [],
	}
}
