import yaml from 'yaml'
import type { ToolRun } from './tool-service'

export interface PrettyPrintToolRunParams {
	detail: 'full' | 'truncated'
}

/**
 * Renders this tool run in a human-readable format.
 */
export function prettyPrintToolRun(toolRun: ToolRun, params: PrettyPrintToolRunParams): string {
	if (toolRun.status === 'done') {
		if (
			toolRun.result !== null &&
			typeof toolRun.result === 'object' &&
			'output' in toolRun.result &&
			typeof toolRun.result.output === 'string'
		) {
			if (
				params.detail === 'full' &&
				'hidden_truncatedOutput' in toolRun.result &&
				typeof toolRun.result.hidden_truncatedOutput === 'string'
			) {
				return (
					toolRun.result.hidden_truncatedOutput +
					// Strip out the '--- Truncated ...' line explaining what got truncated
					toolRun.result.output.split('\n').slice(1).join('\n')
				)
			}
			return toolRun.result.output
		}
	}

	return yaml.stringify(toolRun)
}
