import type { ThreadID } from '../../threads/thread'
import type { FileSystem } from './fs'
import { NO_FILE_SYSTEM } from './none'

export type CreateFileSystem = (threadID: ThreadID) => FileSystem

export const PLATFORM_FILE_SYSTEM_CREATOR: CreateFileSystem | Promise<CreateFileSystem> =
	import.meta.env.VITE_PLATFORM === 'node'
		? import('./node').then(
				({ nodeFileSystem: createFileTrackingFilesystem }) => createFileTrackingFilesystem,
			)
		: import.meta.env.VITE_PLATFORM === 'vscode' ||
			  import.meta.env.VITE_PLATFORM === 'vscode-web'
			? import('./vscode').then(
					({ vscodeFileSystem: createFileTrackingFilesystem }) =>
						createFileTrackingFilesystem,
				)
			: () => NO_FILE_SYSTEM
