import { readdir, stat } from 'node:fs/promises'
import { join } from 'node:path'
import type { URI } from 'vscode-uri'
import { throttledPromise } from '../misc/throttle'
import { isLikelySecretsFile } from '../tools/builtin/filesystem/read_file.util'

export const getThrottledFormattedDirectoryListing: typeof getFormattedDirectoryListing =
	throttledPromise({
		interval: 20 * 1000,
		fn: getFormattedDirectoryListing,
		cacheKey: (uri) => uri.toString(),
	})

export async function getFormattedDirectoryListing(
	rootPath: URI,
	params?: { workingDirectory?: URI; testing?: boolean },
): Promise<string> {
	// NOTE: can't use process.cwd() because it's '/' in VS Code extensions.
	const workingDirectoryExplanation =
		params?.workingDirectory?.fsPath === rootPath.fsPath ? ' (your working directory)' : ''
	const lines = [params?.testing ? './' : rootPath.fsPath + workingDirectoryExplanation]
	let dirs = 0
	let files = 0

	// Read directory contents
	const entries = await readdir(rootPath.fsPath)

	// Filter out secrets files and get stats for each entry to determine if it's a directory
	const entryStats = await Promise.all(
		entries
			.filter((entry) => !isLikelySecretsFile(entry))
			.map(async (entry) => {
				try {
					const stats = await stat(join(rootPath.fsPath, entry))
					return { name: entry, isDirectory: stats.isDirectory() }
				} catch {
					return { name: entry, isDirectory: false }
				}
			}),
	)

	// Sort entries: directories first, then alphabetically
	entryStats.sort((a, b) => {
		if (a.isDirectory === b.isDirectory) return a.name.localeCompare(b.name)
		return a.isDirectory ? -1 : 1
	})

	// Generate formatted output
	for (let i = 0; i < entryStats.length; i++) {
		const { name, isDirectory } = entryStats[i]!
		const isLast = i === entryStats.length - 1
		const prefix = isLast ? '└ ' : '├ '

		if (isDirectory) dirs++
		else files++

		const line = `${prefix}${name}${isDirectory ? '/' : ''}`
		lines.push(line)
	}

	return lines.join('\n')
}
