import type { ThreadToolUseID } from '../../../threads/thread'
import type { ToolRegistration } from '../../tool-service'

export type RunRoutineProgressMessage =
	| {
			type: 'message'
			speaker: 'user' | 'assistant'
			message: string
	  }
	| {
			type: 'toolCall'
			toolName: string
			id: ThreadToolUseID
			args: string
	  }
	| {
			type: 'toolResult'
			toolName: string
			id: ThreadToolUseID
			result: string
	  }
	| {
			type: 'meta'
			message: string
			variant?: 'header' | 'normal'
	  }

export type RunRoutineToolDef = {
	name: 'run_routine'
	args: { id: string; file?: string; userInput?: string }
	progress: RunRoutineProgressMessage[] | RunRoutineProgressMessage
	result: string
}

export const RUN_ROUTINE_TOOL_NAME = 'run_routine'

const description = `Execute a routine from your routines.amp.yaml file.

This tool allows you to run predefined routines that automate common tasks. You can optionally provide a userInput argument that describes what input should be passed to the routine.

IMPORTANT: The result of this tool (if any) should be displayed directly to the user and no further action or text should be sent to the user.
`

export const runRoutineToolReg: ToolRegistration<RunRoutineToolDef> = {
	spec: {
		name: 'run_routine',
		description,
		inputSchema: {
			type: 'object',
			properties: {
				id: {
					type: 'string',
					description: 'The ID of the routine to run.',
				},
				file: {
					type: 'string',
					description:
						'Optional path to the routine file. Defaults to routines.amp.yaml if not provided.',
				},
				userInput: {
					type: 'string',
					description:
						'Optional user input to pass to the routine. Can be referenced in routine steps.',
				},
			},
			required: ['id'],
		},
		source: 'builtin',
	},
	fn:
		import.meta.env.VITE_PLATFORM === 'node' || import.meta.env.VITE_PLATFORM === 'vscode'
			? import('./node').then(({ runRoutine }) => runRoutine)
			: null,
}
