import type { Observable } from './observable'
import { BehaviorSubject } from './subject'

export function createSetSubject<T>(): {
	add(value: T): void
	delete(value: T): void
	set: Set<T>
	observable: Observable<Set<T>>
} {
	const set = new Set<T>()
	const subject = new BehaviorSubject<Set<T>>(set)
	return {
		add(value): void {
			set.add(value)
			subject.next(set)
		},
		delete(value): void {
			set.delete(value)
			subject.next(set)
		},
		set,
		observable: subject,
	}
}

export function createMapSubject<K, V>(): {
	get(key: K): V | undefined
	set(key: K, value: V): void
	delete(key: K): void
	map: Map<K, V>
	observable: Observable<Map<K, V>>
} {
	const map = new Map<K, V>()
	const subject = new BehaviorSubject<Map<K, V>>(map)
	return {
		get(key): V | undefined {
			return map.get(key)
		},
		set(key, value): void {
			map.set(key, value)
			subject.next(map)
		},
		delete(key): void {
			map.delete(key)
			subject.next(map)
		},
		map,
		observable: subject,
	}
}
