import logger from '@sourcegraph/amp-core/src/common/logger'
import type { SecretName, Settings } from '@sourcegraph/amp-core/src/configuration/settings'
import type {
	SecretData,
	SecretStorage,
	SettingsStorage,
	SettingsTarget,
} from '@sourcegraph/amp-core/src/configuration/storage'
import { Subject } from '@sourcegraph/observable'
import fs from 'node:fs/promises'
import { homedir } from 'node:os'
import path from 'node:path'
import { xdgConfig, xdgData } from 'xdg-basedir'

const DATA_DIR = path.join(xdgData || path.join(homedir(), '.local/share'), 'amp')
export const DEFAULT_SETTINGS_FILE = path.join(
	xdgConfig || path.join(homedir(), '.config'),
	'amp',
	'settings.json',
)
const SECRETS_FILE = 'secrets.json'
export const SECRETS_PATH = path.join(DATA_DIR, SECRETS_FILE)

interface CLISettingsOptions {
	settingsFile?: string
	dataDir?: string
}

/**
 * Create a file-based settings storage for CLI
 * @param options Optional directory overrides for testing
 * @returns A settings storage implementation that uses the file system
 */
export function createFileSettingsStorage(options?: CLISettingsOptions): SettingsStorage {
	const settingsPath = options?.settingsFile ?? DEFAULT_SETTINGS_FILE
	const changes = new Subject<(keyof Settings)[]>()
	let cachedSettings: Partial<Settings> | undefined

	async function ensureConfigDir(): Promise<void> {
		try {
			const dir = path.dirname(settingsPath)
			await fs.mkdir(dir, { recursive: true })
		} catch (error) {
			logger.error(`Failed to create config directory: ${error}`)
			throw error
		}
	}

	async function readSettings(): Promise<Partial<Settings>> {
		if (cachedSettings) {
			return cachedSettings
		}

		try {
			await ensureConfigDir()
			const data = await fs.readFile(settingsPath, 'utf-8')
			const rawConfig = JSON.parse(data)

			// Convert from amp.* prefixed keys to the internal Settings type
			const settings: Partial<Settings> = {}
			for (const [key, value] of Object.entries(rawConfig)) {
				if (key.startsWith('amp.')) {
					const unprefixedKey = key.substring(4)
					// @ts-expect-error: Dynamic key access
					settings[unprefixedKey] = value
				}
			}

			cachedSettings = settings
			return settings
		} catch (error) {
			if ((error as NodeJS.ErrnoException).code === 'ENOENT') {
				// File doesn't exist yet, return empty settings
				cachedSettings = {}
				return cachedSettings
			}
			logger.error(`Failed to read settings: ${error}`)
			throw error
		}
	}

	async function writeSettings(settings: Partial<Settings>): Promise<void> {
		try {
			await ensureConfigDir()

			// Convert from internal Settings type to amp.* prefixed keys
			const prefixedSettings: Record<string, unknown> = {}
			for (const [key, value] of Object.entries(settings)) {
				prefixedSettings[`amp.${key}`] = value
			}

			await fs.writeFile(settingsPath, JSON.stringify(prefixedSettings, null, 2), 'utf-8')
			cachedSettings = settings
		} catch (error) {
			logger.error(`Failed to write settings: ${error}`)
			throw error
		}
	}

	return {
		async get<K extends keyof Settings>(key: K): Promise<Settings[K] | undefined> {
			const settings = await readSettings()
			return settings[key]
		},
		async set<K extends keyof Settings>(
			key: K,
			value: Settings[K],
			target?: SettingsTarget,
		): Promise<void> {
			if (target === 'workspace') {
				throw new Error(`Unsupported target: ${target}`)
			}
			const settings = await readSettings()
			settings[key] = value
			await writeSettings(settings)
			changes.next([key])
		},
		async delete<K extends keyof Settings>(key: K, target?: SettingsTarget): Promise<void> {
			if (target === 'workspace') {
				throw new Error(`Unsupported target: ${target}`)
			}
			const settings = await readSettings()
			delete settings[key]
			await writeSettings(settings)
			changes.next([key])
		},
		async keys(): Promise<(keyof Settings)[]> {
			const settings = await readSettings()
			return Object.keys(settings) as (keyof Settings)[]
		},
		changes,
	}
}

/**
 * Create a file-based secret storage for CLI
 * @param options Optional directory overrides for testing
 * @returns A secret storage implementation that uses the file system
 */
export function createFileSecretStorage(options?: CLISettingsOptions): SecretStorage {
	const dataDir = options?.dataDir || DATA_DIR
	const changes = new Subject<keyof SecretData>()
	let cachedSecrets: Partial<SecretData> | undefined

	async function ensureDataDir(): Promise<void> {
		try {
			await fs.mkdir(dataDir, { recursive: true })
		} catch (error) {
			logger.error(`Failed to create data directory: ${error}`)
			throw error
		}
	}

	async function readSecrets(): Promise<Partial<SecretData>> {
		if (cachedSecrets) {
			return cachedSecrets
		}

		try {
			await ensureDataDir()
			const secretsPath = options?.dataDir ? path.join(dataDir, SECRETS_FILE) : SECRETS_PATH
			const data = await fs.readFile(secretsPath, 'utf-8')
			cachedSecrets = JSON.parse(data) as Partial<SecretData>
			return cachedSecrets
		} catch (error) {
			if ((error as NodeJS.ErrnoException).code === 'ENOENT') {
				// File doesn't exist yet, return empty secrets
				cachedSecrets = {}
				return cachedSecrets
			}
			logger.error(`Failed to read secrets: ${error}`)
			throw error
		}
	}

	async function writeSecrets(secrets: Partial<SecretData>): Promise<void> {
		try {
			await ensureDataDir()
			const secretsPath = options?.dataDir ? path.join(dataDir, SECRETS_FILE) : SECRETS_PATH
			await fs.writeFile(secretsPath, JSON.stringify(secrets, null, 2), 'utf-8')
			cachedSecrets = secrets
		} catch (error) {
			logger.error(`Failed to write secrets: ${error}`)
			throw error
		}
	}

	return {
		async get(name: SecretName, url: string): Promise<string | undefined> {
			const secrets = await readSecrets()
			return secrets[`${name}@${url}`]
		},
		async set(name: SecretName, value: string, url: string): Promise<void> {
			const key: keyof SecretData = `${name}@${url}`
			const secrets = await readSecrets()
			secrets[key] = value
			await writeSecrets(secrets)
			changes.next(key)
		},
		changes,
	}
}
