import logger from '@sourcegraph/amp-core/src/common/logger'
import { spawn } from 'node:child_process'
import fs from 'node:fs'
import path from 'node:path'
import os from 'node:os'
import { LRUCache } from '../lru'

// Message types for fuzzy server communication
interface BaseMessage {
	id?: number
}

interface QueryMessage extends BaseMessage {
	type: 'query'
	text: string
}

interface IndexAddMessage extends BaseMessage {
	type: 'index:add'
	files: string[]
}

interface IndexRemoveMessage extends BaseMessage {
	type: 'index:remove'
	files: string[]
}

type FuzzyServerMessage = QueryMessage | IndexAddMessage | IndexRemoveMessage

// Response types
interface BaseResponse {
	id: number
}

interface QueryResponse extends BaseResponse {
	results: string[]
}

interface IndexResponse extends BaseResponse {
	success?: boolean
}

type FuzzyServerResponse<T extends FuzzyServerMessage> = T extends QueryMessage
	? QueryResponse
	: T extends IndexAddMessage | IndexRemoveMessage
		? IndexResponse
		: never

/**
 * Manages communication with the fuzzy server subprocess for file fuzzy matching.
 *
 * Features:
 * - Spawns and manages a Node.js subprocess running fuzzy-server.js
 * - Maintains an LRU cache (50 entries) for query results to avoid redundant searches
 * - Handles bidirectional JSON message passing over stdin/stdout with the subprocess
 * - Supports adding/removing files from the search index with automatic cache invalidation
 */
export class FuzzyServerClient {
	private process: ReturnType<typeof spawn> | null = null
	private messageId = 0
	private pendingRequests = new Map<
		number,
		{ resolve: (value: any) => void; reject: (error: Error) => void }
	>()
	private queryCache = new LRUCache<string, string[]>(50)

	constructor() {}

	async start(): Promise<void> {
		const serverPath = path.join(__dirname, 'fuzzy-server.js')
		this.process = spawn('node', [serverPath], {
			stdio: ['pipe', 'pipe', 'inherit'],
		})

		if (!this.process.stdout) {
			throw new Error('Failed to create fuzzy server process')
		}

		// Set up response handling
		let buffer = ''
		this.process.stdout.on('data', (data: Buffer) => {
			buffer += data.toString()
			const lines = buffer.split('\n')
			buffer = lines.pop() || ''

			for (const line of lines) {
				if (line.trim()) {
					try {
						const response = JSON.parse(line)
						const pending = this.pendingRequests.get(response.id)
						if (pending) {
							this.pendingRequests.delete(response.id)
							pending.resolve(response)
						}
					} catch (error) {
						logger.error('Failed to parse fuzzy server response', error)
					}
				}
			}
		})

		this.process.on('error', (error) => {
			logger.error('Fuzzy server process error', error)
		})

		this.process.on('exit', (code) => {
			logger.debug('Fuzzy server process exited', { code })
		})
	}

	private async sendMessage<T extends FuzzyServerMessage>(
		message: T,
	): Promise<FuzzyServerResponse<T>> {
		if (!this.process?.stdin) {
			throw new Error('Fuzzy server not started')
		}

		const id = ++this.messageId

		return new Promise((resolve, reject) => {
			this.pendingRequests.set(id, { resolve, reject })
			this.process!.stdin!.write(JSON.stringify({ ...message, id }) + '\n')
		})
	}

	async addFiles(files: string[]): Promise<void> {
		await this.sendMessage({
			type: 'index:add',
			files,
		})
		// Clear cache when index changes
		this.queryCache.clear()
	}

	async removeFiles(files: string[]): Promise<void> {
		await this.sendMessage({
			type: 'index:remove',
			files,
		})
		// Clear cache when index changes
		this.queryCache.clear()
	}

	async query(text: string): Promise<string[]> {
		logger.debug('Processing completion for pattern', { pattern: text })

		// Check cache first
		const cached = this.queryCache.get(text)
		if (cached) {
			return cached
		}

		const response = await this.sendMessage({
			type: 'query',
			text,
		})
		const results = response.results || []

		// Cache the results
		this.queryCache.set(text, results)
		return results
	}

	dispose(): void {
		if (this.process) {
			this.process.kill()
			this.process = null
		}
	}
}

/**
 * Index files using ripgrep and send them to the fuzzy server
 */
export async function indexFiles(fuzzyServer: FuzzyServerClient, rootPath: string): Promise<void> {
	// Skip indexing if rootPath is the home directory
	const homeDir = os.homedir()
	if (path.resolve(rootPath) === path.resolve(homeDir)) {
		logger.debug('Skipping indexing for home directory')
		return
	}

	return new Promise((resolve, reject) => {
		const rg = spawn('rg', ['--files'], {
			cwd: rootPath,
			stdio: ['ignore', 'pipe', 'inherit'],
		})

		if (!rg.stdout) {
			reject(new Error('Failed to start ripgrep process'))
			return
		}

		let buffer = ''
		const files: string[] = []

		rg.stdout.on('data', (data: Buffer) => {
			buffer += data.toString()
			const lines = buffer.split('\n')
			buffer = lines.pop() || ''

			for (const line of lines) {
				if (line.trim()) {
					files.push(line.trim())
				}
			}
		})

		rg.on('error', (error) => {
			logger.error('Ripgrep process error', error)
			reject(error)
		})

		rg.on('exit', async (code) => {
			if (code === 0) {
				try {
					await fuzzyServer.addFiles(files)
					logger.debug(`Indexed ${files.length} files`)
					resolve()
				} catch (error) {
					reject(error)
				}
			} else {
				reject(new Error(`Ripgrep exited with code ${code}`))
			}
		})
	})
}

/**
 * Watch directory for file changes and update fuzzy server index
 */
export function watchFiles(
	fuzzyServer: FuzzyServerClient,
	rootPath: string,
): { dispose: () => void } {
	// Skip watching if rootPath is the home directory
	const homeDir = os.homedir()
	if (path.resolve(rootPath) === path.resolve(homeDir)) {
		logger.debug('Skipping file watching for home directory')
		return { dispose: () => {} }
	}
	const watchers: fs.FSWatcher[] = []
	const pendingChanges = new Set<string>()
	let debounceTimer: NodeJS.Timeout | null = null

	const processChanges = async () => {
		if (pendingChanges.size === 0) return

		const files = Array.from(pendingChanges)
		pendingChanges.clear()

		const existingFiles: string[] = []
		const removedFiles: string[] = []

		// Check which files exist vs were removed
		for (const file of files) {
			const fullPath = path.join(rootPath, file)
			try {
				const stat = await fs.promises.stat(fullPath)
				if (stat.isFile()) {
					existingFiles.push(file)
				}
			} catch {
				// File doesn't exist, was removed
				removedFiles.push(file)
			}
		}

		try {
			if (existingFiles.length > 0) {
				await fuzzyServer.addFiles(existingFiles)
				logger.debug(`Added ${existingFiles.length} files to index`)
			}
			if (removedFiles.length > 0) {
				await fuzzyServer.removeFiles(removedFiles)
				logger.debug(`Removed ${removedFiles.length} files from index`)
			}
		} catch (error) {
			logger.error('Failed to update fuzzy server index', error)
		}
	}

	const scheduleProcessChanges = () => {
		if (debounceTimer) {
			clearTimeout(debounceTimer)
		}
		debounceTimer = setTimeout(processChanges, 100)
	}

	const watchDirectory = (dirPath: string) => {
		try {
			const watcher = fs.watch(dirPath, { recursive: true }, (eventType, filename) => {
				if (!filename) return

				// Debug logging
				logger.debug('File system event', { eventType, filename, dirPath })

				// We only care about rename events (created, deleted, renamed)
				// Ignore change events (content modifications)
				if (eventType !== 'rename') {
					logger.debug('Ignoring change event', { eventType, filename })
					return
				}

				// Skip hidden files and directories
				if (filename.includes('/.') || filename.startsWith('.')) return

				// Convert to relative path from root
				const relativePath = path.relative(rootPath, path.join(dirPath, filename))
				if (relativePath.startsWith('..')) return

				pendingChanges.add(relativePath)
				scheduleProcessChanges()
			})

			watchers.push(watcher)

			watcher.on('error', (error) => {
				logger.error(`File watcher error for ${dirPath}`, error)
			})
		} catch (error) {
			logger.error(`Failed to watch directory ${dirPath}`, error)
		}
	}

	// Start watching the root directory
	watchDirectory(rootPath)

	return {
		dispose: () => {
			if (debounceTimer) {
				clearTimeout(debounceTimer)
			}
			for (const watcher of watchers) {
				watcher.close()
			}
			watchers.length = 0
		},
	}
}
