import logger from '@sourcegraph/amp-core/src/common/logger'
import type { SecretStorage } from '@sourcegraph/amp-core/src/configuration/storage'
import { DEFAULT_AMP_URL_STRING } from '@sourcegraph/amp-core/src/constants'
import { exec } from 'node:child_process'
import { createServer } from 'node:http'
import { platform } from 'node:os'
import { promisify } from 'node:util'

const execAsync = promisify(exec)

let selectedPort: number | null = null

export async function generateLoginLink(
	baseURL: string = DEFAULT_AMP_URL_STRING,
	authToken: string,
): Promise<string> {
	selectedPort = await findAvailablePort(35789)

	const callbackPort = `${selectedPort}`
	logger.info(`Generated callback port: ${callbackPort}`)

	return `${baseURL}/auth/cli-login?authToken=${encodeURIComponent(authToken)}&callbackPort=${encodeURIComponent(callbackPort)}`
}

async function findAvailablePort(startPort: number, maxAttempts: number = 10): Promise<number> {
	logger.info(`Finding available port starting from ${startPort}`)

	for (let i = 0; i < maxAttempts; i++) {
		const port = startPort + i
		try {
			await new Promise<void>((resolve, reject) => {
				const server = createServer()
				server.once('error', (err: NodeJS.ErrnoException) => {
					if (err.code === 'EADDRINUSE') {
						reject(new Error(`Port ${port} is in use`))
					} else {
						reject(err)
					}
				})
				server.once('listening', () => {
					server.close(() => resolve())
				})
				server.listen(port, '127.0.0.1')
			})
			logger.info(`Found available port: ${port}`)
			return port
		} catch (err) {
			logger.info(`Port ${port} is in use, trying port ${port + 1}`)
			continue
		}
	}
	throw new Error(`Could not find an available port after ${maxAttempts} attempts`)
}

export async function waitForAPIKey(
	tokenSecret: string,
	timeoutMs: number = 5 * 60 * 1000,
): Promise<string> {
	logger.info('Starting local HTTP server to receive API key from browser')
	if (!selectedPort) {
		throw new Error('No port selected. Call generateLoginLink first.')
	}
	const port = selectedPort
	logger.info(`Using selected port: ${port}`)

	return new Promise((resolve, reject) => {
		const server = createServer((req, res) => {
			const corsOrigin = process.env.NODE_ENV === 'production' ? 'ampcode.com' : '*'
			res.setHeader('Access-Control-Allow-Origin', corsOrigin)
			res.setHeader('Access-Control-Allow-Methods', 'GET')

			if (req.url?.startsWith('/auth/callback')) {
				const url = new URL(req.url, `http://127.0.0.1:${port}`)
				const apiKey = url.searchParams.get('apiKey')
				const authToken = url.searchParams.get('authToken')

				if (authToken !== tokenSecret) {
					reject(new Error('Invalid authentication token'))
					return
				}

				if (apiKey) {
					logger.info(`Received API key from server: ${apiKey.substring(0, 8)}...`)
					res.writeHead(200, { 'Content-Type': 'text/html' })
					res.end(`
						<!DOCTYPE html>
						<html>
							<head>
								<title>Amp CLI Login Success</title>
								<style>
									body { font-family: ui-sans-serif, system-ui, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji"; max-width: 600px; margin: 0 auto; padding: 2rem; background-color: #fafaf8; color: #0b0d0b; }
								</style>
							</head>
							<body>
								<h1>Login Successful!</h1>
								<p>Your Amp CLI has been configured successfully. You can close this window and return to your terminal.</p>
							</body>
						</html>
					`)
					server.close()
					resolve(apiKey)
				} else {
					res.writeHead(400, { 'Content-Type': 'text/html' })
					res.end(`
						<!DOCTYPE html>
						<html>
							<head>
								<title>Amp CLI Login Error</title>
								<style>
									body { font-family: ui-sans-serif, system-ui, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji"; max-width: 600px; margin: 0 auto; padding: 2rem; background-color: #fafaf8; color: #0b0d0b; }
									h1 { color: #d44444; }
								</style>
							</head>
							<body>
								<h1>Login Failed</h1>
								<p>There was an error processing your login. Please try again.</p>
							</body>
						</html>
					`)
				}
			} else {
				res.writeHead(404)
				res.end()
			}
		})

		server.listen(port, '127.0.0.1', () => {
			logger.info(`Listening for auth callback on port ${port}`)
		})

		const timeout = setTimeout(() => {
			server.close()
			reject(new Error('Login timed out'))
		}, timeoutMs)

		server.on('error', (err: NodeJS.ErrnoException) => {
			clearTimeout(timeout)
			logger.error(`Server error: ${err.message}`)
			reject(err)
		})
	})
}

export async function completeLogin(
	apiKey: string,
	baseURL: string = DEFAULT_AMP_URL_STRING,
	secretStorage: SecretStorage,
): Promise<void> {
	await secretStorage.set('apiKey', apiKey, baseURL)
	logger.info('API key stored successfully')
}

export async function openInBrowser(url: string): Promise<void> {
	try {
		const os = platform()
		switch (os) {
			case 'win32':
				await execAsync(`start "" "${url}"`)
				break
			case 'darwin':
				await execAsync(`open "${url}"`)
				break
			default:
				await execAsync(`xdg-open "${url}"`)
				break
		}
		logger.info(`Opened browser with URL: ${url}`)
	} catch (error) {
		logger.error(`Failed to open browser: ${error}`)
		throw error
	}
}
